        // AI 
        async function modifyWithAI(modifyInstruction) {
            if (!originalAiPrompt.value) {
                window.toastr.warning('');
                return;
            }
            isModifyingAi.value = true;
            showProgress.value = true;
            try {
                // 1: 
                progressDialogRef.value?.setProgress(10);
                progressDialogRef.value?.setMessage(' AI ...');
                progressDialogRef.value?.addDetail(`: ${modifyInstruction}`);
                const currentFiles = files.value.map(f => `=== ${f.path} ===\n${f.content}`).join('\n\n');
                const systemPrompt = ` SillyTavern  HTML/CSS/JS 


1. ** index.htmlstyle.cssscript.js **
2. 
3. 
4.  FILE_START  FILE_END 

****`;
                const userPrompt = `# 
${originalAiPrompt.value}

# 
${modifyInstruction}

# 
${config.value.fields.map((f, i) => `${i + 1}${f.label} (: ${f.name}) - : $${i + 1}${f.icon ? ` - : ${f.icon}` : ''}`).join('\n')}

# 
${currentFiles}

 ****

FILE_START: index.html
<details>
  <!--  HTML $1, $2, $3  -->
</details>
FILE_END

FILE_START: style.css
/*  CSS  */
FILE_END

FILE_START: script.js
(function() {
  //  JS 
})();
FILE_END

****`;
                // 2: 
                progressDialogRef.value?.setProgress(20);
                progressDialogRef.value?.setMessage(' AI ...');
                progressDialogRef.value?.addDetail(`: ${files.value.length} `);
                const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);
                progressDialogRef.value?.setProgress(30);
                progressDialogRef.value?.setMessage(' AI ...');
                progressDialogRef.value?.addDetail(' 10-30 ');
                const response = await fetch(apiUrl, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${settings.value.api_key}`,
                    },
                    body: JSON.stringify({
                        model: settings.value.model,
                        messages: [
                            { role: 'system', content: systemPrompt },
                            { role: 'user', content: userPrompt },
                        ],
                        temperature: 0.7,
                        max_tokens: 65500,
                    }),
                });
                if (!response.ok) {
                    throw new Error(`API : ${response.statusText}`);
                }
                // 3: 
                progressDialogRef.value?.setProgress(60);
                progressDialogRef.value?.setMessage(' AI ...');
                const data = await response.json();
                const resultText = data.choices[0]?.message?.content || '';
                // 4: 
                progressDialogRef.value?.setProgress(80);
                progressDialogRef.value?.setMessage('...');
                progressDialogRef.value?.addDetail(`: ${resultText.length} `);
                // 
                const FILE_START_REGEX = /FILE_START:\s*([^\n]+)\n([\s\S]*?)(?=FILE_START:|FILE_END:|$)/g;
                const matches = [...resultText.matchAll(FILE_START_REGEX)];
                if (matches.length === 0) {
                    throw new Error('AI ');
                }
                progressDialogRef.value?.addDetail(` ${matches.length} `);
                // 
                const generatedFiles = matches.map(m => m[1].trim().replace(/^\.\//, ''));
                const requiredFiles = ['index.html', 'style.css', 'script.js'];
                const missingFiles = requiredFiles.filter(f => !generatedFiles.includes(f));
                if (missingFiles.length > 0) {
                    progressDialogRef.value?.addDetail(` : AI : ${missingFiles.join(', ')}`);
                    window.toastr.warning(`AI : ${missingFiles.join(', ')}`, '', {
                        timeOut: 5000,
                    });
                }
                if (matches.length > 0) {
                    for (const match of matches) {
                        const normalizedPath = match[1].trim().replace(/^\.\//, '');
                        let content = sanitizeFileContent(normalizedPath, match[2]);
                        if (content.endsWith('FILE_END')) {
                            content = content.slice(0, -8).trim();
                        }
                        const file = files.value.find(f => f.path === normalizedPath);
                        if (file) {
                            file.content = content;
                            progressDialogRef.value?.addDetail(` : ${normalizedPath}`);
                        }
                    }
                }
                // 5: 
                progressDialogRef.value?.setProgress(95);
                progressDialogRef.value?.setMessage('...');
                // 
                originalAiPrompt.value += `\n\n${modifyInstruction}`;
                updatePreview();
                // 
                progressDialogRef.value?.setProgress(100);
                progressDialogRef.value?.setMessage(' AI ');
                setTimeout(() => {
                    showProgress.value = false;
                    showAiModifyDialog.value = false;
                    window.toastr.success(` AI  ${matches.length} `);
                }, 800);
            }
            catch (error) {
                console.error('AI :', error);
                showProgress.value = false;
                window.toastr.error('AI : ' + error.message);
            }
            finally {
                isModifyingAi.value = false;
            }
        }
