`, "",{"version":3,"sources":["webpack://./src/_with_worldbook/components/StatusBarGenerator.vue"],"names":[],"mappings":"AA43FA,sCACE,YAAa,CACb,WAAY,CACZ,YAAa,CACb,qBAAsB,CACtB,QAAS,CACT,kBACF","sourcesContent":["<template>\n  <div class=\"statusbar-generator\">\n    <!--  -->\n    <div\n      class=\"section-header\"\n      style=\"\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        padding: 20px 28px;\n        background: linear-gradient(\n          135deg,\n          rgba(30, 30, 30, 0.95) 0%,\n          rgba(38, 38, 38, 0.9) 50%,\n          rgba(30, 30, 30, 0.95) 100%\n        );\n        backdrop-filter: blur(12px);\n        border-radius: 14px;\n        margin-bottom: 20px;\n        border: 1px solid rgba(255, 255, 255, 0.06);\n        box-shadow:\n          0 3px 12px rgba(0, 0, 0, 0.3),\n          inset 0 1px 0 rgba(255, 255, 255, 0.04),\n          inset 0 -1px 0 rgba(0, 0, 0, 0.2);\n      \"\n    >\n      <h3\n        style=\"\n          margin: 0;\n          color: #fff;\n          font-size: 16px !important;\n          font-weight: 600;\n          display: flex;\n          align-items: center;\n          gap: 12px;\n          letter-spacing: 0.5px;\n          text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);\n        \"\n      >\n        <i class=\"fa-solid fa-chart-bar\" style=\"color: #4a9eff; font-size: 18px\"></i>\n        \n      </h3>\n      <div style=\"display: flex; gap: 10px; flex-wrap: wrap\">\n        <button\n          style=\"\n            padding: 8px 16px;\n            background: linear-gradient(135deg, #10b981 0%, #059669 100%);\n            border: none;\n            border-radius: 8px;\n            color: white;\n            font-size: 13px;\n            font-weight: 600;\n            cursor: pointer;\n            transition: all 0.3s ease;\n          \"\n          @click=\"loadTemplate('abo')\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.transform = 'translateY(-2px)')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.transform = 'translateY(0)')\"\n        >\n          <i class=\"fa-solid fa-magic\" style=\"margin-right: 6px\"></i>\n           ABO \n        </button>\n        <button\n          style=\"\n            padding: 8px 16px;\n            background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);\n            border: none;\n            border-radius: 8px;\n            color: white;\n            font-size: 13px;\n            font-weight: 600;\n            cursor: pointer;\n            transition: all 0.3s ease;\n          \"\n          @click=\"showAIDialog\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.transform = 'translateY(-2px)')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.transform = 'translateY(0)')\"\n        >\n          <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 6px\"></i>\n          AI \n        </button>\n        <button\n          style=\"\n            padding: 8px 16px;\n            background: linear-gradient(135deg, #06b6d4 0%, #0891b2 100%);\n            border: none;\n            border-radius: 8px;\n            color: white;\n            font-size: 13px;\n            font-weight: 600;\n            cursor: pointer;\n            transition: all 0.3s ease;\n          \"\n          @click=\"exportRegex\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.transform = 'translateY(-2px)')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.transform = 'translateY(0)')\"\n        >\n          <i class=\"fa-solid fa-download\" style=\"margin-right: 6px\"></i>\n           JSON\n        </button>\n      </div>\n    </div>\n\n    <!--  -->\n    <div style=\"display: grid; grid-template-columns: 280px 1fr 500px; gap: 20px; min-height: 600px\">\n      <!--  -->\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 15px;\n          border: 1px solid #3a3a3a;\n          display: flex;\n          flex-direction: column;\n          overflow-y: auto;\n        \"\n      >\n        <h4\n          style=\"\n            margin: 0 0 15px 0;\n            color: #fff;\n            font-size: 14px;\n            font-weight: 600;\n            display: flex;\n            align-items: center;\n            gap: 8px;\n          \"\n        >\n          <i class=\"fa-solid fa-cog\" style=\"color: #4a9eff\"></i>\n          \n        </h4>\n\n        <!--  -->\n        <div style=\"margin-bottom: 15px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #c0c0c0; font-size: 12px; font-weight: 600\"\n            ></label\n          >\n          <input\n            v-model=\"config.findRegex\"\n            type=\"text\"\n            placeholder=\"<-CHARACTER_STATUS->\"\n            style=\"\n              width: 100%;\n              padding: 8px 12px;\n              background: #1e1e1e;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 12px;\n            \"\n          />\n        </div>\n\n        <!--  -->\n        <div style=\"flex: 1; display: flex; flex-direction: column; gap: 10px; margin-bottom: 15px\">\n          <div\n            v-for=\"(field, index) in config.fields\"\n            :key=\"index\"\n            style=\"padding: 10px; background: #1e1e1e; border-radius: 6px; border: 1px solid #3a3a3a\"\n          >\n            <div style=\"display: flex; justify-content: space-between; align-items: center; margin-bottom: 8px\">\n              <span style=\"color: #4a9eff; font-size: 11px; font-weight: 600\"> {{ index + 1 }}</span>\n              <button\n                :disabled=\"config.fields.length <= 1\"\n                :style=\"{\n                  padding: '4px 8px',\n                  background: '#ef4444',\n                  border: 'none',\n                  borderRadius: '4px',\n                  color: 'white',\n                  fontSize: '10px',\n                  cursor: 'pointer',\n                  opacity: config.fields.length <= 1 ? 0.4 : 1,\n                }\"\n                @click=\"removeField(index)\"\n              >\n                \n              </button>\n            </div>\n            <input\n              v-model=\"field.name\"\n              type=\"text\"\n              placeholder=\"\"\n              style=\"\n                width: 100%;\n                padding: 6px 10px;\n                background: #2a2a2a;\n                border: 1px solid #3a3a3a;\n                border-radius: 4px;\n                color: #e0e0e0;\n                font-size: 11px;\n                margin-bottom: 6px;\n              \"\n            />\n            <input\n              v-model=\"field.label\"\n              type=\"text\"\n              placeholder=\"\"\n              style=\"\n                width: 100%;\n                padding: 6px 10px;\n                background: #2a2a2a;\n                border: 1px solid #3a3a3a;\n                border-radius: 4px;\n                color: #e0e0e0;\n                font-size: 11px;\n                margin-bottom: 6px;\n              \"\n            />\n            <div style=\"display: flex; gap: 6px; align-items: center\">\n              <input\n                v-model=\"field.icon\"\n                type=\"text\"\n                placeholder=\"fa-solid fa-user\"\n                style=\"\n                  flex: 1;\n                  padding: 6px 10px;\n                  background: #2a2a2a;\n                  border: 1px solid #3a3a3a;\n                  border-radius: 4px;\n                  color: #e0e0e0;\n                  font-size: 11px;\n                  font-family: monospace;\n                \"\n              />\n              <button\n                style=\"\n                  padding: 6px 10px;\n                  background: #3a3a3a;\n                  border: 1px solid #4a4a4a;\n                  border-radius: 4px;\n                  color: #fff;\n                  font-size: 12px;\n                  cursor: pointer;\n                  min-width: 36px;\n                \"\n                title=\"\"\n                @click=\"showIconPicker(index)\"\n              >\n                <i v-if=\"field.icon\" :class=\"field.icon\"></i>\n                <i v-else class=\"fa-solid fa-icons\"></i>\n              </button>\n            </div>\n          </div>\n        </div>\n\n        <button\n          style=\"\n            width: 100%;\n            padding: 8px;\n            background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);\n            border: none;\n            border-radius: 6px;\n            color: white;\n            font-size: 12px;\n            font-weight: 600;\n            cursor: pointer;\n          \"\n          @click=\"addField\"\n        >\n          <i class=\"fa-solid fa-plus\" style=\"margin-right: 6px\"></i>\n          \n        </button>\n\n        <button\n          style=\"\n            width: 100%;\n            padding: 8px;\n            margin-top: 10px;\n            background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);\n            border: none;\n            border-radius: 6px;\n            color: white;\n            font-size: 12px;\n            font-weight: 600;\n            cursor: pointer;\n          \"\n          @click=\"showXmlParseDialog\"\n        >\n          <i class=\"fa-solid fa-code\" style=\"margin-right: 6px\"></i>\n          AI  XML \n        </button>\n\n        <button\n          style=\"\n            width: 100%;\n            padding: 8px;\n            margin-top: 10px;\n            background: linear-gradient(135deg, #10b981 0%, #059669 100%);\n            border: none;\n            border-radius: 6px;\n            color: white;\n            font-size: 12px;\n            font-weight: 600;\n            cursor: pointer;\n          \"\n          @click=\"showAiFieldGeneratorDialog\"\n        >\n          <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 6px\"></i>\n          AI \n        </button>\n\n        <button\n          style=\"\n            width: 100%;\n            padding: 8px;\n            margin-top: 10px;\n            background: linear-gradient(135deg, #10b981 0%, #059669 100%);\n            border: none;\n            border-radius: 6px;\n            color: white;\n            font-size: 12px;\n            font-weight: 600;\n            cursor: pointer;\n          \"\n          @click=\"generateFromFields\"\n        >\n          <i class=\"fa-solid fa-sparkles\" style=\"margin-right: 6px\"></i>\n          \n        </button>\n      </div>\n\n      <!--  -->\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 15px;\n          border: 1px solid #3a3a3a;\n          display: flex;\n          flex-direction: column;\n        \"\n      >\n        <!--  -->\n        <div\n          style=\"display: flex; gap: 8px; margin-bottom: 12px; border-bottom: 1px solid #3a3a3a; padding-bottom: 8px\"\n        >\n          <div\n            v-for=\"file in files\"\n            :key=\"file.path\"\n            :style=\"{\n              padding: '6px 14px',\n              background: currentFile?.path === file.path ? '#4a9eff' : '#1e1e1e',\n              color: currentFile?.path === file.path ? '#fff' : '#c0c0c0',\n              borderRadius: '6px',\n              fontSize: '12px',\n              fontWeight: '600',\n              cursor: 'pointer',\n              transition: 'all 0.2s ease',\n            }\"\n            @click=\"currentFile = file\"\n          >\n            {{ file.path }}\n          </div>\n        </div>\n\n        <!--  -->\n        <textarea\n          v-if=\"currentFile\"\n          v-model=\"currentFile.content\"\n          style=\"\n            flex: 1;\n            width: 100%;\n            padding: 12px;\n            background: #1e1e1e;\n            border: 1px solid #3a3a3a;\n            border-radius: 8px;\n            color: #e0e0e0;\n            font-size: 13px;\n            font-family: 'Consolas', 'Monaco', monospace;\n            line-height: 1.6;\n            resize: none;\n            min-height: 400px;\n          \"\n          @input=\"updatePreview\"\n        ></textarea>\n      </div>\n\n      <!--  -->\n      <div style=\"display: flex; flex-direction: column; gap: 15px; overflow-y: auto\">\n        <!--  -->\n        <div style=\"background: #2a2a2a; border-radius: 12px; padding: 15px; border: 1px solid #3a3a3a\">\n          <div style=\"display: flex; justify-content: space-between; align-items: center; margin-bottom: 12px\">\n            <h4\n              style=\"\n                margin: 0;\n                color: #fff;\n                font-size: 14px;\n                font-weight: 600;\n                display: flex;\n                align-items: center;\n                gap: 8px;\n              \"\n            >\n              <i class=\"fa-solid fa-book\" style=\"color: #4a9eff\"></i>\n              \n            </h4>\n            <button\n              style=\"\n                padding: 6px 12px;\n                background: linear-gradient(135deg, #10b981 0%, #059669 100%);\n                border: none;\n                border-radius: 6px;\n                color: white;\n                font-size: 12px;\n                font-weight: 600;\n                cursor: pointer;\n              \"\n              @click=\"copyWorldbook\"\n            >\n              <i class=\"fa-solid fa-copy\" style=\"margin-right: 4px\"></i>\n              \n            </button>\n          </div>\n          <div\n            style=\"\n              background: #1e1e1e;\n              border-radius: 8px;\n              padding: 12px;\n              border: 1px solid #3a3a3a;\n              overflow: auto;\n              max-height: 200px;\n            \"\n          >\n            <pre\n              style=\"\n                margin: 0;\n                color: #e0e0e0;\n                font-size: 11px;\n                line-height: 1.6;\n                white-space: pre-wrap;\n                word-wrap: break-word;\n                font-family: 'Consolas', 'Monaco', monospace;\n              \"\n              >{{ worldbookContent }}</pre\n            >\n          </div>\n        </div>\n\n        <!--  -->\n        <div style=\"background: #2a2a2a; border-radius: 12px; padding: 15px; border: 1px solid #3a3a3a; flex: 1\">\n          <h4\n            style=\"\n              margin: 0 0 12px 0;\n              color: #fff;\n              font-size: 14px;\n              font-weight: 600;\n              display: flex;\n              align-items: center;\n              gap: 8px;\n            \"\n          >\n            <i class=\"fa-solid fa-eye\" style=\"color: #4a9eff\"></i>\n            \n          </h4>\n          <div\n            style=\"\n              background: #1e1e1e;\n              border-radius: 8px;\n              padding: 20px;\n              min-height: 400px;\n              border: 1px solid #3a3a3a;\n              overflow: auto;\n            \"\n            v-html=\"previewHtml\"\n          ></div>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <div\n      v-if=\"showIconPickerFor !== null\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.8);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n      \"\n      @click.self=\"showIconPickerFor = null\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 20px;\n          width: 600px;\n          max-width: 90vw;\n          border: 1px solid #3a3a3a;\n        \"\n      >\n        <h3 style=\"margin: 0 0 16px 0; color: #fff; font-size: 15px; font-weight: 600\"></h3>\n        <div\n          style=\"\n            display: grid;\n            grid-template-columns: repeat(8, 1fr);\n            gap: 8px;\n            margin-bottom: 16px;\n            max-height: 400px;\n            overflow-y: auto;\n          \"\n        >\n          <div\n            v-for=\"(iconClass, idx) in iconList\"\n            :key=\"idx\"\n            :style=\"{\n              padding: '14px',\n              background: '#1e1e1e',\n              borderRadius: '8px',\n              cursor: 'pointer',\n              textAlign: 'center',\n              border: '1px solid #3a3a3a',\n              transition: 'all 0.2s ease',\n              display: 'flex',\n              alignItems: 'center',\n              justifyContent: 'center',\n            }\"\n            :title=\"iconClass\"\n            @click=\"selectIcon(iconClass)\"\n            @mouseenter=\"\n              (e: any) => {\n                e.currentTarget.style.background = '#3a3a3a';\n                e.currentTarget.style.borderColor = '#4a9eff';\n                e.currentTarget.style.transform = 'scale(1.1)';\n                e.currentTarget.style.boxShadow = '0 4px 12px rgba(74, 158, 255, 0.3)';\n              }\n            \"\n            @mouseleave=\"\n              (e: any) => {\n                e.currentTarget.style.background = '#1e1e1e';\n                e.currentTarget.style.borderColor = '#3a3a3a';\n                e.currentTarget.style.transform = 'scale(1)';\n                e.currentTarget.style.boxShadow = 'none';\n              }\n            \"\n          >\n            <i :class=\"iconClass\" style=\"font-size: 20px; color: #4a9eff\"></i>\n          </div>\n        </div>\n        <div style=\"display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 8px 16px;\n              background: #3a3a3a;\n              border: none;\n              border-radius: 6px;\n              color: #fff;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n            \"\n            @click=\"showIconPickerFor = null\"\n          >\n            \n          </button>\n          <button\n            style=\"\n              padding: 8px 16px;\n              background: #ef4444;\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n            \"\n            @click=\"\n              config.fields[showIconPickerFor!].icon = '';\n              showIconPickerFor = null;\n            \"\n          >\n            \n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!-- AI  -->\n    <div\n      v-if=\"showAI\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.8);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n      \"\n      @click.self=\"showAI = false\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 24px;\n          width: 600px;\n          max-width: 90vw;\n          max-height: 90vh;\n          overflow-y: auto;\n          border: 1px solid #3a3a3a;\n        \"\n      >\n        <h3 style=\"margin: 0 0 16px 0; color: #fff; font-size: 16px; font-weight: 600\">AI </h3>\n\n        <!--  -->\n        <div\n          style=\"\n            background: linear-gradient(135deg, rgba(59, 130, 246, 0.15) 0%, rgba(37, 99, 235, 0.1) 100%);\n            border: 1px solid rgba(59, 130, 246, 0.3);\n            border-radius: 8px;\n            padding: 12px 16px;\n            margin-bottom: 16px;\n          \"\n        >\n          <div style=\"display: flex; align-items: center; justify-content: space-between; margin-bottom: 10px\">\n            <div style=\"display: flex; align-items: center; gap: 10px\">\n              <i class=\"fa-solid fa-cog\" style=\"color: #60a5fa; font-size: 16px\"></i>\n              <div style=\"color: #93c5fd; font-size: 13px; font-weight: 600\">\n                 {{ config.fields.length }} \n              </div>\n            </div>\n            <button\n              style=\"\n                padding: 4px 10px;\n                background: rgba(16, 185, 129, 0.2);\n                border: 1px solid rgba(16, 185, 129, 0.4);\n                border-radius: 4px;\n                color: #10b981;\n                font-size: 11px;\n                cursor: pointer;\n              \"\n              @click=\"addFieldInDialog\"\n            >\n              <i class=\"fa-solid fa-plus\"></i> \n            </button>\n          </div>\n\n          <div v-if=\"config.fields.length > 0\" style=\"display: flex; flex-direction: column; gap: 8px\">\n            <div\n              v-for=\"(field, i) in config.fields\"\n              :key=\"i\"\n              style=\"\n                background: rgba(15, 23, 42, 0.6);\n                border: 1px solid rgba(148, 163, 184, 0.15);\n                border-radius: 6px;\n                padding: 8px 10px;\n                display: flex;\n                align-items: center;\n                gap: 8px;\n              \"\n            >\n              <span style=\"color: #60a5fa; font-weight: 600; font-size: 11px; min-width: 50px\">{{ i + 1 }}:</span>\n              <input\n                v-model=\"field.label\"\n                type=\"text\"\n                placeholder=\"\"\n                style=\"\n                  flex: 1;\n                  padding: 4px 8px;\n                  background: #1e1e1e;\n                  border: 1px solid #3a3a3a;\n                  border-radius: 4px;\n                  color: #e0e0e0;\n                  font-size: 11px;\n                \"\n              />\n              <input\n                v-model=\"field.name\"\n                type=\"text\"\n                placeholder=\"\"\n                style=\"\n                  flex: 1;\n                  padding: 4px 8px;\n                  background: #1e1e1e;\n                  border: 1px solid #3a3a3a;\n                  border-radius: 4px;\n                  color: #e0e0e0;\n                  font-size: 11px;\n                \"\n              />\n              <button\n                v-if=\"field.icon\"\n                style=\"\n                  padding: 4px 8px;\n                  background: rgba(139, 92, 246, 0.2);\n                  border: 1px solid rgba(139, 92, 246, 0.3);\n                  border-radius: 4px;\n                  color: #a78bfa;\n                  font-size: 11px;\n                  cursor: pointer;\n                  min-width: 30px;\n                \"\n                title=\"\"\n                @click=\"field.icon = ''\"\n              >\n                <i :class=\"field.icon\"></i>\n              </button>\n              <button\n                :disabled=\"config.fields.length <= 1\"\n                style=\"\n                  padding: 4px 8px;\n                  background: rgba(239, 68, 68, 0.2);\n                  border: 1px solid rgba(239, 68, 68, 0.3);\n                  border-radius: 4px;\n                  color: #ef4444;\n                  font-size: 11px;\n                  cursor: pointer;\n                \"\n                :style=\"{\n                  opacity: config.fields.length <= 1 ? 0.4 : 1,\n                  cursor: config.fields.length <= 1 ? 'not-allowed' : 'pointer',\n                }\"\n                title=\"\"\n                @click=\"removeFieldInDialog(i)\"\n              >\n                <i class=\"fa-solid fa-trash\"></i>\n              </button>\n            </div>\n          </div>\n          <div v-else style=\"color: #fbbf24; font-size: 12px; padding: 8px 0\"> \"\"</div>\n        </div>\n\n        <textarea\n          v-model=\"aiPrompt\"\n          placeholder=\"&#10; &#10; &#10; &#10; &#10;&#10; &#10; AI &#10; \"\n          style=\"\n            width: 100%;\n            padding: 12px;\n            background: #1e1e1e;\n            border: 1px solid #3a3a3a;\n            border-radius: 6px;\n            color: #e0e0e0;\n            font-size: 13px;\n            min-height: 120px;\n            margin-bottom: 16px;\n            resize: vertical;\n          \"\n        ></textarea>\n        <div style=\"display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 8px 16px;\n              background: #3a3a3a;\n              border: none;\n              border-radius: 6px;\n              color: #fff;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n            \"\n            @click=\"showAI = false\"\n          >\n            \n          </button>\n          <button\n            style=\"\n              padding: 8px 16px;\n              background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n            \"\n            @click=\"generateWithAI\"\n          >\n            <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 6px\"></i>\n            \n          </button>\n        </div>\n        <div v-if=\"originalAiPrompt\" style=\"margin-top: 16px; display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 10px 20px;\n              background: #ffc107;\n              border: none;\n              border-radius: 6px;\n              color: #000;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n            \"\n            :disabled=\"isModifyingAi\"\n            @click=\"showAiModifyDialog = true\"\n          >\n            <i class=\"fa-solid fa-edit\" style=\"margin-right: 6px\"></i>\n            {{ isModifyingAi ? '...' : 'AI ' }}\n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!-- AI  -->\n    <AIModifyDialog\n      :show=\"showAiModifyDialog\"\n      :is-modifying=\"isModifyingAi\"\n      title=\"AI \"\n      description=\"AI \"\n      :examples=\"[\n        '',\n        '',\n        '',\n        '',\n      ]\"\n      @close=\"showAiModifyDialog = false\"\n      @confirm=\"modifyWithAI\"\n    />\n\n    <!-- XML  -->\n    <div\n      v-if=\"showXmlDialog\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.7);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n      \"\n      @click.self=\"showXmlDialog = false\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 24px;\n          width: 90%;\n          max-width: 700px;\n          max-height: 80vh;\n          overflow-y: auto;\n          box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);\n        \"\n      >\n        <h3 style=\"margin: 0 0 16px 0; color: #f59e0b; font-size: 18px; font-weight: 600\">\n          <i class=\"fa-solid fa-robot\" style=\"margin-right: 8px\"></i>\n          AI  XML \n        </h3>\n\n        <div\n          style=\"\n            background: rgba(251, 191, 36, 0.1);\n            border: 1px solid rgba(251, 191, 36, 0.3);\n            border-radius: 6px;\n            padding: 12px;\n            margin-bottom: 16px;\n          \"\n        >\n          <p style=\"margin: 0; color: #fbbf24; font-size: 12px; line-height: 1.6\">\n            <i class=\"fa-solid fa-circle-info\" style=\"margin-right: 6px\"></i>\n            <strong></strong> AI\n            <i class=\"fa-solid fa-cog\"></i>  API \n          </p>\n        </div>\n\n        <div style=\"margin-bottom: 16px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #e0e0e0; font-size: 13px\">\n             XML \n          </label>\n          <textarea\n            v-model=\"xmlInput\"\n            placeholder=\"&#10;<state_bar>&#10;<i></i>&#10;<></>&#10;<></>&#10;</state_bar>\"\n            style=\"\n              width: 100%;\n              min-height: 200px;\n              padding: 12px;\n              background: #1a1a1a;\n              border: 1px solid #404040;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-family: 'Consolas', 'Monaco', monospace;\n              font-size: 13px;\n              resize: vertical;\n            \"\n          ></textarea>\n        </div>\n\n        <div style=\"display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 10px 20px;\n              background: #404040;\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              cursor: pointer;\n            \"\n            @click=\"showXmlDialog = false\"\n          >\n            \n          </button>\n          <button\n            style=\"\n              padding: 10px 20px;\n              background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              cursor: pointer;\n              margin-right: 10px;\n            \"\n            :disabled=\"!xmlInput.trim() || isParsingXml\"\n            @click=\"parseXmlWithAI\"\n          >\n            <i class=\"fa-solid fa-robot\" style=\"margin-right: 6px\"></i>\n            {{ isParsingXml ? 'AI ...' : '' }}\n          </button>\n          <button\n            v-if=\"originalXmlInput\"\n            style=\"\n              padding: 10px 20px;\n              background: #ffc107;\n              border: none;\n              border-radius: 6px;\n              color: #000;\n              font-size: 13px;\n              cursor: pointer;\n            \"\n            :disabled=\"isModifyingXml\"\n            @click=\"showXmlModifyDialog = true\"\n          >\n            <i class=\"fa-solid fa-edit\" style=\"margin-right: 6px\"></i>\n            {{ isModifyingXml ? '...' : 'AI ' }}\n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!-- XML  -->\n    <AIModifyDialog\n      :show=\"showXmlModifyDialog\"\n      :is-modifying=\"isModifyingXml\"\n      title=\"AI  XML \"\n      description=\"AI \"\n      :examples=\"[\n        '',\n        '',\n        '',\n        '3',\n      ]\"\n      @close=\"showXmlModifyDialog = false\"\n      @confirm=\"modifyXmlWithAI\"\n    />\n\n    <!-- AI  -->\n    <div\n      v-if=\"showAiFieldDialog\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.7);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n      \"\n      @click.self=\"showAiFieldDialog = false\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 24px;\n          width: 90%;\n          max-width: 700px;\n          max-height: 80vh;\n          overflow-y: auto;\n          box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);\n        \"\n      >\n        <h3 style=\"margin: 0 0 16px 0; color: #10b981; font-size: 18px; font-weight: 600\">\n          <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 8px\"></i>\n          AI \n        </h3>\n\n        <div\n          style=\"\n            background: rgba(16, 185, 129, 0.1);\n            border: 1px solid rgba(16, 185, 129, 0.3);\n            border-radius: 6px;\n            padding: 12px;\n            margin-bottom: 16px;\n          \"\n        >\n          <p style=\"margin: 0; color: #10b981; font-size: 12px; line-height: 1.6\">\n            <i class=\"fa-solid fa-lightbulb\" style=\"margin-right: 6px\"></i>\n            <strong></strong>AI \n          </p>\n        </div>\n\n        <div style=\"margin-bottom: 16px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #e0e0e0; font-size: 13px\">\n            \n          </label>\n          <textarea\n            v-model=\"aiFieldDescription\"\n            placeholder=\"&#10;&#10;- &#10;- &#10;- &#10;- &#10;- &#10;- &#10;- \"\n            style=\"\n              width: 100%;\n              min-height: 180px;\n              padding: 12px;\n              background: #1a1a1a;\n              border: 1px solid #404040;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              resize: vertical;\n              line-height: 1.6;\n            \"\n          ></textarea>\n        </div>\n\n        <div style=\"display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 10px 20px;\n              background: #404040;\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              cursor: pointer;\n            \"\n            @click=\"showAiFieldDialog = false\"\n          >\n            \n          </button>\n          <button\n            style=\"\n              padding: 10px 20px;\n              background: linear-gradient(135deg, #10b981 0%, #059669 100%);\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              cursor: pointer;\n              margin-right: 10px;\n            \"\n            :disabled=\"!aiFieldDescription.trim() || isGeneratingFields\"\n            @click=\"generateFieldsWithAI\"\n          >\n            <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 6px\"></i>\n            {{ isGeneratingFields ? 'AI ...' : '' }}\n          </button>\n          <button\n            v-if=\"originalFieldDescription\"\n            style=\"\n              padding: 10px 20px;\n              background: #ffc107;\n              border: none;\n              border-radius: 6px;\n              color: #000;\n              font-size: 13px;\n              cursor: pointer;\n            \"\n            :disabled=\"isModifyingField\"\n            @click=\"showFieldModifyDialog = true\"\n          >\n            <i class=\"fa-solid fa-edit\" style=\"margin-right: 6px\"></i>\n            {{ isModifyingField ? '...' : 'AI ' }}\n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <AIModifyDialog\n      :show=\"showFieldModifyDialog\"\n      :is-modifying=\"isModifyingField\"\n      title=\"AI \"\n      description=\"AI \"\n      :examples=\"['', '3', '', '2']\"\n      @close=\"showFieldModifyDialog = false\"\n      @confirm=\"modifyFieldWithAI\"\n    />\n\n    <!--  -->\n    <ProgressDialog ref=\"progressDialogRef\" :show=\"showProgress\" title=\"AI \" />\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { storeToRefs } from 'pinia';\nimport { computed, onMounted, ref, watch } from 'vue';\nimport { normalizeApiEndpoint, useSettingsStore } from '../settings';\nimport { copyToClipboard, getScriptIdSafe } from '../utils';\nimport AIModifyDialog from './AIModifyDialog.vue';\nimport ProgressDialog from './ProgressDialog.vue';\n\ninterface Field {\n  name: string;\n  label: string;\n  icon: string;\n}\n\ninterface Config {\n  name: string;\n  findRegex: string;\n  fields: Field[];\n}\n\ninterface CodeFile {\n  path: string;\n  content: string;\n}\n\n//  settings store\nconst settingsStore = useSettingsStore();\nconst { settings } = storeToRefs(settingsStore);\n\n// \nconst templates = {\n  simple: {\n    name: '',\n    findRegex: '<-CHARACTER_STATUS->',\n    fields: [\n      { name: '', label: '', icon: 'fa-solid fa-user' },\n      { name: '', label: '', icon: 'fa-solid fa-venus-mars' },\n      { name: '', label: '', icon: 'fa-solid fa-cake-candles' },\n      { name: '', label: '', icon: 'fa-solid fa-heart' },\n    ],\n  },\n  abo: {\n    name: 'ABO',\n    findRegex:\n      '<-ENVIRONMENT_DATA->[\\\\r\\\\n]*\\\\|([^|]+)\\\\|([^|]+)\\\\|([^|]+)\\\\|([^|]+)\\\\|[\\\\r\\\\n]*<-CHARACTER_STATUS->[\\\\r\\\\n]*\\\\|([^|]+)\\\\|([^|]+)\\\\|([^|]+)\\\\|([^|]+)\\\\|[\\\\r\\\\n]*',\n    fields: [\n      { name: '', label: '', icon: 'fa-solid fa-calendar' },\n      { name: '', label: '', icon: 'fa-solid fa-clock' },\n      { name: '', label: '', icon: 'fa-solid fa-location-dot' },\n      { name: '', label: '', icon: 'fa-solid fa-cloud-sun' },\n      { name: '', label: '', icon: 'fa-solid fa-shirt' },\n      { name: '', label: '', icon: 'fa-solid fa-heart' },\n      { name: '', label: '', icon: 'fa-solid fa-fire' },\n      { name: '', label: '', icon: 'fa-solid fa-bookmark' },\n    ],\n  },\n};\n\nconst config = ref<Config>({\n  name: '',\n  findRegex: '<-CHARACTER_STATUS->',\n  fields: [\n    { name: '', label: '', icon: 'fa-solid fa-user' },\n    { name: '', label: '', icon: 'fa-solid fa-venus-mars' },\n    { name: '', label: '', icon: 'fa-solid fa-cake-candles' },\n    { name: '', label: '', icon: 'fa-solid fa-heart' },\n  ],\n});\n\nconst files = ref<CodeFile[]>([\n  { path: 'index.html', content: '' },\n  { path: 'style.css', content: '' },\n  { path: 'script.js', content: '' },\n]);\n\nconst currentFile = ref<CodeFile | null>(files.value[0]);\nconst showAI = ref(false);\nconst aiPrompt = ref('');\nconst originalAiPrompt = ref(''); // AI\nconst showAiModifyDialog = ref(false); // AI\nconst isModifyingAi = ref(false); // AI\nconst showIconPickerFor = ref<number | null>(null);\n\n// \nconst showProgress = ref(false);\nconst progressDialogRef = ref<InstanceType<typeof ProgressDialog> | null>(null);\n\n// XML\nconst originalXmlInput = ref(''); // XML\nconst showXmlModifyDialog = ref(false);\nconst isModifyingXml = ref(false);\n\n// AI\nconst originalFieldDescription = ref(''); // \nconst showFieldModifyDialog = ref(false);\nconst isModifyingField = ref(false);\n\n// XML \nconst showXmlDialog = ref(false);\nconst xmlInput = ref('');\nconst isParsingXml = ref(false);\n\n// AI \nconst showAiFieldDialog = ref(false);\nconst aiFieldDescription = ref('');\nconst isGeneratingFields = ref(false);\n\n// Font Awesome \nconst iconList = [\n  // \n  'fa-solid fa-cat',\n  'fa-solid fa-dog',\n  'fa-solid fa-dove',\n  'fa-solid fa-fish',\n  'fa-solid fa-paw',\n  'fa-solid fa-spider',\n  'fa-solid fa-horse',\n  // \n  'fa-solid fa-heart',\n  'fa-solid fa-heart-pulse',\n  // \n  'fa-solid fa-face-smile',\n  'fa-solid fa-face-grin-stars',\n  'fa-solid fa-face-grin-beam',\n  'fa-solid fa-face-laugh',\n  // \n  'fa-solid fa-star',\n  // \n  'fa-solid fa-wand-magic-sparkles',\n  'fa-solid fa-hat-wizard',\n  'fa-solid fa-bolt',\n  'fa-solid fa-fire',\n  'fa-solid fa-meteor',\n  'fa-solid fa-rocket',\n  // \n  'fa-solid fa-sun',\n  'fa-solid fa-moon',\n  'fa-solid fa-cloud',\n  'fa-solid fa-seedling',\n  'fa-solid fa-leaf',\n  'fa-solid fa-tree',\n  'fa-solid fa-snowflake',\n  // \n  'fa-solid fa-cake-candles',\n  'fa-solid fa-candy-cane',\n  'fa-solid fa-apple',\n  'fa-solid fa-ice-cream',\n  // \n  'fa-solid fa-gem',\n  'fa-solid fa-crown',\n  'fa-solid fa-gift',\n  'fa-solid fa-ribbon',\n  'fa-solid fa-medal',\n  'fa-solid fa-trophy',\n  // \n  'fa-solid fa-shield',\n  'fa-solid fa-scroll',\n  // \n  'fa-solid fa-book',\n  'fa-solid fa-book-open',\n  'fa-solid fa-feather',\n  'fa-solid fa-pen',\n  'fa-solid fa-paintbrush',\n  'fa-solid fa-palette',\n  // \n  'fa-solid fa-music',\n  'fa-solid fa-headphones',\n  'fa-solid fa-gamepad',\n  'fa-solid fa-chess',\n  'fa-solid fa-puzzle-piece',\n  'fa-solid fa-dice',\n  // \n  'fa-solid fa-mobile-screen',\n  'fa-solid fa-tablet',\n  'fa-solid fa-laptop',\n  'fa-solid fa-camera',\n  'fa-solid fa-video',\n  'fa-solid fa-tv',\n  // \n  'fa-solid fa-bell',\n  'fa-solid fa-envelope',\n];\n\n// \nconst worldbookContent = computed(() => {\n  // {{}}\n  const placeholders = config.value.fields.map(f => `{{${f.label || f.name}}}`).join('|');\n\n  // \n  const fieldDefinitions = config.value.fields\n    .map(f => {\n      const iconDesc = f.icon ? `<i class=\"${f.icon}\"></i> ` : '';\n      const label = f.label || f.name;\n      return `  - ${iconDesc}${label}{{char}}${label}`;\n    })\n    .join('\\n');\n\n  // \n  const exampleValues = config.value.fields.map(f => f.label || f.name || '').join('|');\n\n  // \n  const parseRegexStructure = (regex: string, fields: Field[]): { formatExample: string; exampleOutput: string } => {\n    //  <-ENVIRONMENT_DATA->\n    const triggerMatches = regex.match(/<-[^>]+->/g) || [];\n\n    if (triggerMatches.length === 0) {\n      // \n      const placeholders = fields.map(f => `{{${f.label || f.name}}}`).join('|');\n      const exampleValues = fields.map(f => f.label || f.name).join('|');\n      return {\n        formatExample: `|${placeholders}|`,\n        exampleOutput: `|${exampleValues}|`,\n      };\n    }\n\n    // \n    let normalizedRegex = regex\n      .replace(/\\\\\\\\/g, '\\\\') // \\\\ -> \\\n      .replace(/\\\\\\|/g, '|') // \\| -> |\n      .replace(/\\\\\\(/g, '(') // \\( -> (\n      .replace(/\\\\\\)/g, ')') // \\) -> )\n      .replace(/\\\\\\[/g, '[') // \\[ -> [\n      .replace(/\\\\\\]/g, ']') // \\] -> ]\n      .replace(/\\\\\\+/g, '+'); // \\+ -> +\n\n    console.log(' :', regex);\n    console.log(' :', normalizedRegex);\n\n    // \n    const lines: string[] = [];\n    const exampleLines: string[] = [];\n    let fieldIndex = 0;\n\n    // \n    const parts = normalizedRegex.split(/(<-[^>]+->)/);\n    console.log(' :', parts);\n\n    for (let i = 0; i < parts.length; i++) {\n      const part = parts[i];\n\n      // \n      if (part.match(/<-[^>]+->/)) {\n        lines.push(part);\n        exampleLines.push(part);\n        console.log(` : ${part}`);\n      }\n      // \n      else if (part.trim().length > 0 && !part.match(/<-[^>]+->/)) {\n        // \n        const allCaptures = part.match(/\\([^\\)]+\\)/g) || [];\n        const totalCount = allCaptures.length;\n        console.log(`  (100): \"${part.substring(0, 100)}\"`);\n        console.log(` :`, allCaptures);\n        console.log(` : ${totalCount}, : ${fieldIndex}, : ${fields.length}`);\n\n        if (totalCount === 0) {\n          console.warn(` `);\n          continue;\n        }\n\n        // 4\n        const perLine = 4;\n\n        for (let offset = 0; offset < totalCount; offset += perLine) {\n          const count = Math.min(perLine, totalCount - offset);\n          //  fieldIndex < fields.length \n          if (count > 0) {\n            const segmentFields = fields.slice(fieldIndex, fieldIndex + count);\n            // \n            while (segmentFields.length < count) {\n              segmentFields.push({\n                name: `${fieldIndex + segmentFields.length + 1}`,\n                label: `${fieldIndex + segmentFields.length + 1}`,\n                icon: '',\n              });\n            }\n            const placeholders = segmentFields.map(f => `{{${f.label || f.name}}}`).join('|');\n            const examples = segmentFields.map(f => f.label || f.name).join('|');\n            lines.push(`|${placeholders}|`);\n            exampleLines.push(`|${examples}|`);\n            console.log(` : |${examples}|`);\n            fieldIndex += count;\n          }\n        }\n      }\n    }\n\n    console.log(' :', lines.join('\\n'));\n    console.log(' :', exampleLines.join('\\n'));\n\n    return {\n      formatExample: lines.join('\\n'),\n      exampleOutput: exampleLines.join('\\n'),\n    };\n  };\n\n  const { formatExample, exampleOutput } = parseRegexStructure(config.value.findRegex, config.value.fields);\n\n  return `<status_rule>\n#{{char}}\n\n##\n<status>\n${formatExample}\n</status>\n\n##\n${fieldDefinitions || '  - '}\n\n##\n\n<status>\n${exampleOutput}\n</status>\n\n##\n1. ** <status> **\n   -  <status>\n   -  </status>\n   -  \n\n2. ****\n   -  <-ENVIRONMENT_DATA->\n   -  | \n   - \n   - \n\n3. ****\n   -  |: |: |\n   -  |||\n   - \n\n4. ****\n   - \n   - \n   - \n   - \n\n5. ****\n   - \n   - {{}} \n   -  {{char}}  {{user}} \n</status_rule>`;\n});\n\n//  HTML\nconst previewHtml = computed(() => {\n  const htmlFile = files.value.find(f => f.path === 'index.html');\n  const cssFile = files.value.find(f => f.path === 'style.css');\n  const jsFile = files.value.find(f => f.path === 'script.js');\n\n  if (!htmlFile) return '';\n\n  const scriptTag = 'script';\n  const styleTag = 'style';\n\n  //  $1, $2 \n  let previewContent = htmlFile.content || '';\n  config.value.fields.forEach((field, i) => {\n    const exampleValue = field.name || `${i + 1}`;\n    previewContent = previewContent.replace(new RegExp(`\\\\$${i + 1}(?!\\\\d)`, 'g'), exampleValue);\n  });\n\n  return `<!DOCTYPE html>\n<html>\n<head>\n  <meta charset=\"UTF-8\">\n  <meta name=\"viewport\" content=\"width=device-width, initial-scale=1.0\">\n  <link rel=\"stylesheet\" href=\"https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.5.1/css/all.min.css\">\n  <${styleTag}>\n    html, body {\n      margin: 0;\n      padding: 0;\n      width: 100%;\n      height: auto;\n      min-height: 800px;\n      overflow: visible;\n    }\n    ${cssFile?.content || ''}\n  </${styleTag}>\n  <${scriptTag} src=\"https://code.jquery.com/jquery-3.7.1.min.js\"></${scriptTag}>\n  <link rel=\"stylesheet\" href=\"https://cdnjs.cloudflare.com/ajax/libs/toastr.js/latest/toastr.min.css\">\n  <${scriptTag} src=\"https://cdnjs.cloudflare.com/ajax/libs/toastr.js/latest/toastr.min.js\"></${scriptTag}>\n  <${scriptTag} src=\"https://cdnjs.cloudflare.com/ajax/libs/gsap/3.12.5/gsap.min.js\"></${scriptTag}>\n  <${scriptTag} src=\"https://cdnjs.cloudflare.com/ajax/libs/lodash.js/4.17.21/lodash.min.js\"></${scriptTag}>\n</head>\n<body>\n  ${previewContent}\n  <${scriptTag}>${jsFile?.content || ''}</${scriptTag}>\n</body>\n</html>`;\n});\n\