ef.value?.setProgress(70);\n    progressDialogRef.value?.setMessage(' AI ...');\n\n    const result = await response.json();\n    const aiResponse = result.choices[0]?.message?.content || '';\n\n    progressDialogRef.value?.setProgress(85);\n    progressDialogRef.value?.setMessage('...');\n\n    //  JSON\n    let jsonText = aiResponse\n      .trim()\n      .replace(/^```(?:json)?\\s*\\n?/i, '')\n      .replace(/\\n?```\\s*$/i, '');\n    const jsonMatch = jsonText.match(/\\[\\s*{[\\s\\S]*}\\s*\\]/);\n    if (jsonMatch) {\n      jsonText = jsonMatch[0];\n    }\n\n    const parsedFields = JSON.parse(jsonText);\n    if (!Array.isArray(parsedFields)) {\n      throw new Error('AI ');\n    }\n\n    // \n    config.value.fields = parsedFields.map((field: any) => ({\n      name: field.name || '',\n      label: field.label || field.name || '',\n      icon: field.icon || '',\n    }));\n\n    // \n    originalFieldDescription.value += `\\n\\n${modifyInstruction}`;\n\n    progressDialogRef.value?.setProgress(100);\n    progressDialogRef.value?.setMessage(' ');\n    progressDialogRef.value?.addDetail(` ${parsedFields.length} `);\n\n    setTimeout(() => {\n      showProgress.value = false;\n      showFieldModifyDialog.value = false;\n      window.toastr.success(' AI ');\n    }, 800);\n  } catch (error: any) {\n    console.error('AI :', error);\n    showProgress.value = false;\n    window.toastr.error('AI : ' + error.message);\n  } finally {\n    isModifyingField.value = false;\n  }\n}\n\n// \nfunction generateFromFields() {\n  const fields = config.value.fields;\n\n  //  findRegex\n  const totalFields = fields.length;\n\n  if (totalFields === 0) {\n    config.value.findRegex = '<-CHARACTER_STATUS->';\n    console.log(' ');\n    return;\n  }\n\n  if (totalFields <= 4) {\n    // 4\n    config.value.findRegex = `<-CHARACTER_STATUS->[\\\\r\\\\n]*\\\\|${fields.map(() => '([^|]+)').join('\\\\|')}\\\\|[\\\\r\\\\n]*`;\n    console.log('  findRegex ():', config.value.findRegex);\n    console.log(' :', totalFields);\n  } else {\n    // 4\n    // 4\n    const perLine = totalFields <= 8 ? Math.ceil(totalFields / 2) : 4;\n\n    // \n    const firstHalf = Math.min(Math.ceil(totalFields / 2), totalFields <= 12 ? 4 : Math.ceil(totalFields / 3));\n\n    const lines: string[] = [];\n\n    //  + \n    lines.push('<-ENVIRONMENT_DATA->[\\\\r\\\\n]*');\n    for (let i = 0; i < firstHalf; i += perLine) {\n      const count = Math.min(perLine, firstHalf - i);\n      lines.push(\n        '\\\\|' +\n          fields\n            .slice(i, i + count)\n            .map(() => '([^|]+)')\n            .join('\\\\|') +\n          '\\\\|[\\\\r\\\\n]*',\n      );\n    }\n\n    //  + perLine\n    lines.push('<-CHARACTER_STATUS->[\\\\r\\\\n]*');\n    for (let i = firstHalf; i < totalFields; i += perLine) {\n      const count = Math.min(perLine, totalFields - i);\n      lines.push(\n        '\\\\|' +\n          fields\n            .slice(i, i + count)\n            .map(() => '([^|]+)')\n            .join('\\\\|') +\n          '\\\\|[\\\\r\\\\n]*',\n      );\n    }\n\n    config.value.findRegex = lines.join('');\n\n    console.log('  findRegex ():', config.value.findRegex);\n    console.log(' :', totalFields);\n    console.log(\n      ' :',\n      `: ${firstHalf}, : ${totalFields - firstHalf}, : ${perLine}`,\n    );\n  }\n\n  //  HTML\n  const fieldsHtml = fields\n    .map((f, i) => {\n      const iconPart = f.icon ? `<i class=\"${f.icon}\"></i> ` : '';\n      return `        <div class=\"status-field\">\n          <div class=\"field-label\">${iconPart}${f.label || '' + (i + 1)}</div>\n          <div class=\"field-value\">$${i + 1}</div>\n        </div>`;\n    })\n    .join('\\n');\n\n  const htmlContent = `<details>\n<summary> ${config.value.name} </summary>\n<div class=\"status-card\">\n    <div class=\"status-header\">\n        <span></span>\n        <span>${config.value.name}</span>\n    </div>\n    <div class=\"status-content\">\n        <div class=\"status-grid\">\n${fieldsHtml}\n        </div>\n    </div>\n</div>\n</details>`;\n\n  //  CSS\n  const cssContent = `.status-card {\n    font-family: 'Microsoft YaHei', sans-serif;\n    background: linear-gradient(145deg, #1e293b, #334155);\n    border-radius: 14px;\n    margin: 16px 0;\n    box-shadow: 0 8px 25px rgba(15, 23, 42, 0.4);\n    overflow: hidden;\n    border: 1px solid rgba(148, 163, 184, 0.2);\n    max-width: 600px;\n}\n\n.status-header {\n    background: linear-gradient(135deg, #0f172a, #1e293b);\n    color: #e2e8f0;\n    padding: 12px 20px;\n    font-size: 15px;\n    font-weight: 600;\n    display: flex;\n    align-items: center;\n    gap: 8px;\n    border-bottom: 1px solid rgba(148, 163, 184, 0.15);\n}\n\n.status-content {\n    padding: 20px;\n    background: rgba(30, 41, 59, 0.5);\n}\n\n.status-grid {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 16px;\n}\n\n.status-field {\n    background: rgba(30, 41, 59, 0.8);\n    padding: 16px;\n    border-radius: 10px;\n    border-left: 3px solid #6366f1;\n}\n\n.field-label {\n    font-size: 11px;\n    color: #94a3b8;\n    margin-bottom: 8px;\n    font-weight: 600;\n    text-transform: uppercase;\n}\n\n.field-value {\n    font-size: 14px;\n    color: #e2e8f0;\n    line-height: 1.5;\n    font-weight: 600;\n}`;\n\n  //  JS - \n  const jsContent = `// \n(function() {\n  // \n  setTimeout(function() {\n    console.log('${config.value.name} ');\n\n    // \n    if (typeof gsap !== 'undefined') {\n      gsap.from('.status-field', {\n        opacity: 0,\n        y: 20,\n        duration: 0.5,\n        stagger: 0.1,\n        ease: 'power2.out'\n      });\n\n      // \n      $('.status-field').hover(\n        function() {\n          gsap.to($(this), { scale: 1.05, duration: 0.2 });\n        },\n        function() {\n          gsap.to($(this), { scale: 1, duration: 0.2 });\n        }\n      );\n    }\n  }, 100);\n})();`;\n\n  files.value[0].content = htmlContent;\n  files.value[1].content = cssContent;\n  files.value[2].content = jsContent;\n  currentFile.value = files.value[0];\n\n  window.toastr.success(' ');\n}\n\n//  AI \nfunction showAIDialog() {\n  showAI.value = true;\n  aiPrompt.value = '';\n}\n\n// \nfunction addFieldInDialog() {\n  config.value.fields.push({\n    name: `${config.value.fields.length + 1}`,\n    label: `${config.value.fields.length + 1}`,\n    icon: '',\n  });\n}\n\n// \nfunction removeFieldInDialog(index: number) {\n  if (config.value.fields.length <= 1) {\n    window.toastr.warning('');\n    return;\n  }\n  config.value.fields.splice(index, 1);\n}\n\n// \nfunction loadTemplate(templateKey: 'simple' | 'abo') {\n  const template = templates[templateKey];\n  if (template) {\n    config.value = JSON.parse(JSON.stringify(template)); // \n    window.toastr.success(` \"${template.name}\"`);\n  }\n}\n\n// AI \nasync function generateWithAI() {\n  if (!aiPrompt.value.trim()) {\n    window.toastr.error('');\n    return;\n  }\n\n  // \n  originalAiPrompt.value = aiPrompt.value;\n\n  // \n  showProgress.value = true;\n\n  try {\n    //  API \n    if (!settings.value.api_endpoint || !settings.value.api_key) {\n      window.toastr.error(' API  API Key');\n      showProgress.value = false;\n      return;\n    }\n\n    // 1: \n    progressDialogRef.value?.setProgress(10);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(`: ${config.value.fields.length} `);\n    progressDialogRef.value?.addDetail(`: ${settings.value.model}`);\n\n    // \n    const currentFiles = files.value.map(f => `=== ${f.path} ===\\n${f.content}`).join('\\n\\n');\n\n    // \n    const systemPrompt = ` SillyTavern  HTML/CSS/JS \n\n Regex  replaceString  AI \n**{{char}}**{{user}}\n\n\n- Font Awesome \n- jQuery ($) - \n- toastr- \n- gsap- \n- lodash (_) - \n\n\n-  $1$2$3 \n- $1 $2 \n- \n- ****\n\n\nFILE_START: index.html\n<details>\n<summary>  </summary>\n<div class=\"status-card\">\n  <!--  $1, $2, $3  -->\n  <div class=\"field-value\">$1</div>\n</div>\n</details>\nFILE_END\n\nFILE_START: style.css\n.status-card {\n  /*  */\n}\nFILE_END\n\nFILE_START: script.js\n(function() {\n  // \n  //  $  document\n})();\nFILE_END\n\n\n1. index.html  <details>  </details>  <!DOCTYPE>  <html> \n2. FILE_START  FILE_END ****\\\\\\`\\\\\\`\\\\\\`html\"\"\"html \n3. HTML $1, $2, $3...\n4. CSS \n5. JS  (function() { ... })() $(function() { ... })\n6.  DOM \n7. ****`;\n\n    const userPrompt = `# \n${config.value.fields.map((f, i) => `${i + 1}${f.label} (: ${f.name}) - : $${i + 1}${f.icon ? ` - : ${f.icon}` : ''}`).join('\\n')}\n\n \n1. HTML  ${config.value.fields.length} \n2. $1 = ${config.value.fields[0]?.label || '1'}, $2 = ${config.value.fields[1]?.label || '2'}${config.value.fields[2] ? `, $3 = ${config.value.fields[2].label}` : ''}${config.value.fields[3] ? `, $4 = ${config.value.fields[3].label}` : ''}${config.value.fields[4] ? `, $5 = ${config.value.fields[4].label}` : ''}\n3. \"\"\"\"\"\"\n4.  $${config.value.fields.length + 1} \n\n# \n**{{char}}****{{user}}**\nMVU Beta{{char}}stat_data\n\n# \n${config.value.findRegex}\n\n# \n${aiPrompt.value}\n\n# \n${currentFiles}\n\n\n1. \n2.  ${config.value.fields.length} $1, $2, $3... HTML \n3. \n4.  index.htmlstyle.cssscript.js \n5. `;\n\n    // 2: \n    progressDialogRef.value?.setProgress(20);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(`API : ${settings.value.api_endpoint}`);\n\n    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n    const response = await fetch(apiUrl, {\n      method: 'POST',\n      headers: {\n        'Content-Type': 'application/json',\n        Authorization: `Bearer ${settings.value.api_key}`,\n      },\n      body: JSON.stringify({\n        model: settings.value.model,\n        messages: [\n          { role: 'system', content: systemPrompt },\n          { role: 'user', content: userPrompt },\n        ],\n        temperature: settings.value.temperature || 0.7,\n        max_tokens: settings.value.max_tokens || 65500,\n      }),\n    });\n\n    // 3: \n    progressDialogRef.value?.setProgress(40);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(' 10-30 ');\n\n    if (!response.ok) {\n      throw new Error(`API : ${response.statusText}`);\n    }\n\n    const data = await response.json();\n    const resultText = data.choices[0]?.message?.content || '';\n\n    // 4: \n    progressDialogRef.value?.setProgress(70);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(`: ${resultText.length} `);\n\n    //  AI \n    const FILE_START_REGEX = /FILE_START:\\s*([^\\n]+)\\n([\\s\\S]*?)(?=FILE_START:|FILE_END:|$)/g;\n    const matches = [...resultText.matchAll(FILE_START_REGEX)];\n\n    if (matches.length === 0) {\n      throw new Error('AI ');\n    }\n\n    for (const match of matches) {\n      const rawPath = match[1].trim();\n      const normalizedPath = rawPath.replace(/^\\.\\//, '');\n      let content = sanitizeFileContent(normalizedPath, match[2]);\n\n      //  FILE_END\n      if (content.endsWith('FILE_END')) {\n        content = content.slice(0, -8).trim();\n      }\n\n      const file = files.value.find(f => f.path === normalizedPath);\n      if (file) {\n        file.content = content;\n      } else if (normalizedPath === 'index.html' || normalizedPath === 'style.css' || normalizedPath === 'script.js') {\n        // \n        files.value.push({ path: normalizedPath, content: content });\n      }\n    }\n\n    //  './'\n    files.value.forEach(file => {\n      file.path = file.path.replace(/^\\.\\//, '');\n    });\n\n    //  './index.html' \n    const uniqueFiles = new Map<string, CodeFile>();\n    files.value.forEach(file => {\n      uniqueFiles.set(file.path, file);\n    });\n    files.value = Array.from(uniqueFiles.values());\n\n    // 5: \n    progressDialogRef.value?.setProgress(90);\n    progressDialogRef.value?.setMessage('...');\n    progressDialogRef.value?.addDetail(` ${matches.length} `);\n\n    const htmlFile = files.value.find(f => f.path === 'index.html') || files.value[0];\n    currentFile.value = htmlFile;\n    updatePreview();\n\n    // \n    progressDialogRef.value?.setProgress(100);\n    progressDialogRef.value?.setMessage(' AI ');\n\n    setTimeout(() => {\n      showProgress.value = false;\n      showAI.value = false;\n      window.toastr.success(' AI ');\n    }, 800);\n  } catch (error: any) {\n    console.error('AI :', error);\n    showProgress.value = false;\n    window.toastr.error('AI : ' + error.message);\n  }\n}\n\n// AI \nasync function modifyWithAI(modifyInstruction: string) {\n  if (!originalAiPrompt.value) {\n    window.toastr.warning('');\n    return;\n  }\n\n  isModifyingAi.value = true;\n  showProgress.value = true;\n\n  try {\n    // 1: \n    progressDialogRef.value?.setProgress(10);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(`: ${modifyInstruction}`);\n\n    const currentFiles = files.value.map(f => `=== ${f.path} ===\\n${f.content}`).join('\\n\\n');\n\n    const systemPrompt = ` SillyTavern  HTML/CSS/JS \n\n\n1. ** index.htmlstyle.cssscript.js **\n2. \n3. \n4.  FILE_START  FILE_END \n\n****`;\n\n    const userPrompt = `# \n${originalAiPrompt.value}\n\n# \n${modifyInstruction}\n\n# \n${config.value.fields.map((f, i) => `${i + 1}${f.label} (: ${f.name}) - : $${i + 1}${f.icon ? ` - : ${f.icon}` : ''}`).join('\\n')}\n\n# \n${currentFiles}\n\n ****\n\nFILE_START: index.html\n<details>\n  <!--  HTML $1, $2, $3  -->\n</details>\nFILE_END\n\nFILE_START: style.css\n/*  CSS  */\nFILE_END\n\nFILE_START: script.js\n(function() {\n  //  JS \n})();\nFILE_END\n\n****`;\n\n    // 2: \n    progressDialogRef.value?.setProgress(20);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(`: ${files.value.length} `);\n\n    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n\n    progressDialogRef.value?.setProgress(30);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(' 10-30 ');\n\n    const response = await fetch(apiUrl, {\n      method: 'POST',\n      headers: {\n        'Content-Type': 'application/json',\n        Authorization: `Bearer ${settings.value.api_key}`,\n      },\n      body: JSON.stringify({\n        model: settings.value.model,\n        messages: [\n          { role: 'system', content: systemPrompt },\n          { role: 'user', content: userPrompt },\n        ],\n        temperature: 0.7,\n        max_tokens: 65500,\n      }),\n    });\n\n    if (!response.ok) {\n      throw new Error(`API : ${response.statusText}`);\n    }\n\n    // 3: \n    progressDialogRef.value?.setProgress(60);\n    progressDialogRef.value?.setMessage(' AI ...');\n\n    const data = await response.json();\n    const resultText = data.choices[0]?.message?.content || '';\n\n    // 4: \n    progressDialogRef.value?.setProgress(80);\n    progressDialogRef.value?.setMessage('...');\n    progressDialogRef.value?.addDetail(`: ${resultText.length} `);\n\n    // \n    const FILE_START_REGEX = /FILE_START:\\s*([^\\n]+)\\n([\\s\\S]*?)(?=FILE_START:|FILE_END:|$)/g;\n    const matches = [...resultText.matchAll(FILE_START_REGEX)];\n\n    if (matches.length === 0) {\n      throw new Error('AI ');\n    }\n\n    progressDialogRef.value?.addDetail(` ${matches.length} `);\n\n    // \n    const generatedFiles = matches.map(m => m[1].trim().replace(/^\\.\\//, ''));\n    const requiredFiles = ['index.html', 'style.css', 'script.js'];\n    const missingFiles = requiredFiles.filter(f => !generatedFiles.includes(f));\n\n    if (missingFiles.length > 0) {\n      progressDialogRef.value?.addDetail(` : AI : ${missingFiles.join(', ')}`);\n      window.toastr.warning(`AI : ${missingFiles.join(', ')}`, '', {\n        timeOut: 5000,\n      });\n    }\n\n    if (matches.length > 0) {\n      for (const match of matches) {\n        const normalizedPath = match[1].trim().replace(/^\\.\\//, '');\n        let content = sanitizeFileContent(normalizedPath, match[2]);\n        if (content.endsWith('FILE_END')) {\n          content = content.slice(0, -8).trim();\n        }\n        const file = files.value.find(f => f.path === normalizedPath);\n        if (file) {\n          file.content = content;\n          progressDialogRef.value?.addDetail(` : ${normalizedPath}`);\n        }\n      }\n    }\n\n    // 5: \n    progressDialogRef.value?.setProgress(95);\n    progressDialogRef.value?.setMessage('...');\n\n    // \n    originalAiPrompt.value += `\\n\\n${modifyInstruction}`;\n\n    updatePreview();\n\n    // \n    progressDialogRef.value?.setProgress(100);\n    progressDialogRef.value?.setMessage(' AI ');\n\n    setTimeout(() => {\n      showProgress.value = false;\n      showAiModifyDialog.value = false;\n      window.toastr.success(` AI  ${matches.length} `);\n    }, 800);\n  } catch (error: any) {\n    console.error('AI :', error);\n    showProgress.value = false;\n    window.toastr.error('AI : ' + error.message);\n  } finally {\n    isModifyingAi.value = false;\n  }\n}\n\n// \nasync function copyWorldbook() {\n  await copyToClipboard(worldbookContent.value, ' ');\n}\n\nfunction sanitizeFileContent(filePath: string, raw: string): string {\n  let content = raw.trim();\n\n  //  markdown ```html ... ```\n  const fenceMatch = content.match(/^```[a-zA-Z0-9]*\\s*([\\s\\S]*?)\\s*```$/);\n  if (fenceMatch) {\n    content = fenceMatch[1].trim();\n  }\n\n  //  Python docstring \"\"\"html ... \"\"\"  '''html ... '''\n  const docstringMatch = content.match(/^(\"\"\"|''')[a-zA-Z0-9]*\\s*([\\s\\S]*?)\\s*\\1$/);\n  if (docstringMatch) {\n    content = docstringMatch[2].trim();\n  }\n\n  //  \"\"\"html  ```html \n  content = content.replace(/^(```|\"\"\"|''')[a-zA-Z0-9]*\\s*\\n?/, '');\n  content = content.replace(/\\n?\\s*(```|\"\"\"|''')$/, '');\n\n  if (filePath.endsWith('.html')) {\n    content = content.replace(/<!DOCTYPE html>/i, match => match.toUpperCase());\n  }\n\n  return content;\n}\n\n// \nfunction exportRegex() {\n  const htmlFile = files.value.find(f => f.path === 'index.html');\n  const cssFile = files.value.find(f => f.path === 'style.css');\n  const jsFile = files.value.find(f => f.path === 'script.js');\n\n  if (!htmlFile || htmlFile.content.trim() === '') {\n    window.toastr.error('');\n    return;\n  }\n\n  const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {\n    const r = (Math.random() * 16) | 0;\n    const v = c === 'x' ? r : (r & 0x3) | 0x8;\n    return v.toString(16);\n  });\n\n  // <details> \n  const scriptTag = 'script';\n  const styleTag = 'style';\n\n  //  <details> \n  const detailsHtml = htmlFile.content.trim();\n  const replaceString = `${detailsHtml}\n<${styleTag}>\n${cssFile?.content || ''}\n</${styleTag}>\n\n<${scriptTag}>\n${jsFile?.content || ''}\n</${scriptTag}>`;\n\n  //  findRegex\n  //  findRegex \n  // \n  let regexPattern = config.value.findRegex;\n\n  //  findRegex \n  const existingCaptureGroups = (regexPattern.match(/\\([^)]+\\)/g) || []).length;\n  const fieldCount = config.value.fields.length;\n\n  if (existingCaptureGroups === 0) {\n    // \n    regexPattern = `${regexPattern.replace(/[.*+?^${}()|[\\]\\\\]/g, '\\\\$&')}[\\\\r\\\\n]*${Array(fieldCount)\n      .fill('\\\\|([^|]+)')\n      .join('')}\\\\|[\\\\r\\\\n]*`;\n  }\n  // \n\n  // \n  const cleanReplaceString = replaceString.replace(/\\r\\n/g, '\\n').replace(/\\r/g, '\\n');\n\n  const regexJson = {\n    id: uuid,\n    scriptName: config.value.name,\n    findRegex: regexPattern,\n    replaceString: cleanReplaceString,\n    trimStrings: [],\n    placement: [2],\n    disabled: false,\n    markdownOnly: true,\n    promptOnly: false,\n    runOnEdit: true,\n    substituteRegex: 0,\n    minDepth: null,\n    maxDepth: null,\n  };\n\n  //  JSON\n  const blob = new Blob([JSON.stringify(regexJson, null, 2)], { type: 'application/json' });\n  const url = URL.createObjectURL(blob);\n  const a = document.createElement('a');\n  a.href = url;\n  a.download = `regex-${config.value.name}.json`;\n  a.click();\n  URL.revokeObjectURL(url);\n\n  window.toastr.success('  JSON ');\n}\n\n// ====================  ====================\n\n// \nfunction loadSavedConfig() {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n\n    const scriptVars = getVariables({ type: 'script', script_id: scriptId });\n    const savedConfig = scriptVars.statusbar_generator_config;\n\n    if (savedConfig) {\n      config.value = { ...config.value, ...savedConfig };\n      console.log(' ');\n    }\n  } catch (error) {\n    console.error(':', error);\n  }\n}\n\n// \nfunction saveConfig() {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n\n    insertOrAssignVariables({ statusbar_generator_config: config.value }, { type: 'script', script_id: scriptId });\n  } catch (error) {\n    console.error(':', error);\n  }\n}\n\n// \nonMounted(() => {\n  loadSavedConfig();\n});\n\n// \nwatch(\n  () => config.value,\n  () => {\n    saveConfig();\n  },\n  { deep: true },\n);\n</script>\n\n<style scoped>\n.statusbar-generator {\n  padding: 20px;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n  background: #1a1a1a;\n}\n</style>\n"],"sourceRoot":""}]);
