                // STScript JSON
                const stscriptJson = {
                    id: uuid,
                    scriptName: `${characterName}-`,
                    disabled: false,
                    runOnEdit: true,
                    findRegex: '//g',
                    replaceString: '```html\n' + generatedCode.value + '\n```',
