        // 
        const extractStreamContent = (chunk) => {
            // OpenAI 
            if (chunk.choices?.[0]?.delta?.content) {
                return chunk.choices[0].delta.content;
            }
            // Claude 
            if (chunk.delta?.text) {
                return chunk.delta.text;
            }
            // 
            if (typeof chunk.content === 'string') {
                return chunk.content;
            }
            if (typeof chunk.text === 'string') {
                return chunk.text;
            }
            if (typeof chunk.response === 'string') {
                return chunk.response;
            }
            return null;
        };
        // 
        const generateWithStreaming = async (requestPayload, progressRef) => {
            const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);
            const response = await fetch(apiUrl, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${settings.value.api_key}`,
                },
                body: JSON.stringify(requestPayload),
            });
            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`${response.status} ${response.statusText}\n${errorText.slice(0, 400)}`);
            }
            if (!response.body) {
                throw new Error(' API ');
            }
            const reader = response.body.getReader();
            const decoder = new TextDecoder('utf-8');
            let buffer = '';
            let done = false;
            let result = '';
            progressRef.value = 0;
            const processLine = (line) => {
                const trimmed = line.trim();
                if (!trimmed) {
                    return false;
                }
                if (trimmed === 'data: [DONE]' || trimmed === '[DONE]') {
                    return true;
                }
                const payloadText = trimmed.startsWith('data:') ? trimmed.slice(5).trim() : trimmed;
                if (!payloadText || payloadText === '[DONE]') {
                    return false;
                }
                try {
                    const payloadJson = JSON.parse(payloadText);
                    const deltaText = extractStreamContent(payloadJson);
                    if (deltaText) {
                        result += deltaText;
                        progressRef.value = Math.min(progressRef.value + 2, 98);
                    }
                }
                catch (parseError) {
                    console.warn(':', payloadText.slice(0, 100), parseError);
                }
                return false;
            };
            while (!done) {
                const { value, done: readerDone } = await reader.read();
                if (value) {
                    buffer += decoder.decode(value, { stream: true });
                    const lines = buffer.split('\n');
                    buffer = lines.pop() ?? '';
                    for (const line of lines) {
                        if (processLine(line)) {
                            done = true;
                            break;
                        }
                    }
                }
                if (readerDone) {
                    done = true;
                }
            }
            if (buffer && !done) {
                processLine(buffer);
            }
            progressRef.value = 100;
            return result;
        };
