                                        const progress = Math.min(60, 30 + chunkCount / 10);
                                        progressDialogRef.value?.setProgress(progress);
                                        // 10chunk
                                        if (chunkCount % 10 === 0) {
                                            progressDialogRef.value?.setMessage(` AI ... (${resultText.length} )`);
                                        }
                                    }
                                }
                                catch (e) {
                                    // 
                                }
                            }
                        }
                    }
                }
                else {
                    // 
                    progressDialogRef.value?.setProgress(30);
                    progressDialogRef.value?.setMessage(' AI ...');
                    await new Promise(r => setTimeout(r, 100)); //  DOM 
                    const response = await fetch(normalizedEndpoint, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                            Authorization: `Bearer ${apiKey}`,
                        },
                        body: JSON.stringify({
                            model: model,
                            messages: [
                                { role: 'system', content: systemPrompt },
                                { role: 'user', content: userPrompt },
                            ],
                            temperature: temperature,
                            max_tokens: maxTokens,
                        }),
                    });
                    if (!response.ok) {
                        let errorMsg = `API : ${response.status} ${response.statusText}`;
                        try {
                            const errorData = await response.json();
                            console.error('API :', errorData);
                            errorMsg += `\n${JSON.stringify(errorData, null, 2)}`;
                        }
                        catch (e) {
                            console.error('');
                        }
                        throw new Error(errorMsg);
                    }
                    progressDialogRef.value?.setProgress(60);
                    progressDialogRef.value?.setMessage(' AI ...');
                    const data = await response.json();
                    resultText = data.choices?.[0]?.message?.content || '';
                }
                progressDialogRef.value?.setProgress(75);
                progressDialogRef.value?.setMessage(' AI ...');
                console.log('AI :', resultText);
                console.log('AI :', resultText.length);
                if (!resultText || resultText.trim().length === 0) {
                    throw new Error('AI  API ');
                }
                // 
                const changes = [];
                // FILE_START: filename\n...FILE_END  FILE_START:filename\n...FILE_END
                const fileRegex = /FILE_START:\s*(.+?)[\r\n]+([\s\S]*?)FILE_END/gi;
                let match;
                console.log(' AI ...');
                // 
                const needMoreInfoMatch = resultText.match(/FILE_START:\s*NEED_MORE_INFO\s*[\r\n]+([\s\S]*?)FILE_END/i);
                if (needMoreInfoMatch) {
                    const guideContent = needMoreInfoMatch[1].trim();
                    console.log(' AI ');
                    //  AI 
                    closeAIDialog();
                    // 
                    toastr.info('AI ', '', { timeOut: 3000 });
                    // alert
                    alert(guideContent);
                    // 
                    aiGenerating.value = false;
                    return;
                }
                while ((match = fileRegex.exec(resultText)) !== null) {
                    const path = match[1].trim();
                    const content = match[2].trim();
                    console.log(`: ${path}, : ${content.length}`);
                    const file = proj.files.find(f => f.path === path);
                    if (file) {
                        changes.push({
                            path: path,
                            oldContent: file.content,
                            newContent: content,
                        });
                    }
                    else {
                        console.warn(` ${path} `);
                    }
                }
                if (changes.length === 0) {
                    console.error(' AI ');
                    console.error('AI :', resultText.substring(0, 500));
                    // 
                    if (resultText.length < 500 || !resultText.includes('FILE_END')) {
                        throw new Error(`AI  ${resultText.length} "" max_tokens 4000 `);
                    }
                    throw new Error('AI  AI  FILE_START  FILE_END ');
                }
                console.log(':', changes.length, '');
                progressDialogRef.value?.setProgress(85);
                progressDialogRef.value?.setMessage('...');
                // 
                if (changes.length === 1 && changes[0].path === 'index.html') {
                    toastr.warning('AI  HTML CSS/JS');
                }
                //  FILE_END
                const lastFile = resultText.trim().split('FILE_START').pop() || '';
                if (!lastFile.includes('FILE_END')) {
                    toastr.error('AI ');
                }
                const result = { changes };
                if (!result.changes || !Array.isArray(result.changes)) {
                    throw new Error('AI ');
                }
                // 
                aiChanges.value = result.changes;
                progressDialogRef.value?.setProgress(95);
                progressDialogRef.value?.setMessage(`... (${changes.length} )`);
                closeAIDialog();
                showComparison.value = true;
                previewMode.value = 'after';
                // 
                progressDialogRef.value?.setProgress(100);
                progressDialogRef.value?.setMessage(' AI ');
                setTimeout(() => {
                    showProgress.value = false;
                    toastr.success(` AI  ${changes.length} `);
                }, 800);
            }
            catch (error) {
                console.error('AI :', error);
                showProgress.value = false;
                //  CORS 
                const errorMessage = error.message || String(error);
                if (errorMessage.includes('CORS') ||
                    errorMessage.includes('Failed to fetch') ||
                    errorMessage.includes('Access-Control-Allow-Origin')) {
                    toastr.error(` CORS  API\n\n` +
                        `API ${new URL(normalizedEndpoint).origin} CORS \n\n` +
                        `\n` +
                        `1.  API  CORS\n` +
                        `2.  CORS  API \n` +
                        `3.  API `);
                }
                else if (errorMessage.includes('307') || errorMessage.includes('Temporary Redirect')) {
                    toastr.error(` URL \n\n` +
                        `${normalizedEndpoint}\n\n` +
                        `URL \n` +
                        `https:///v1`);
                }
                else {
                    toastr.error(`AI : ${errorMessage}`);
                }
            }
            finally {
                aiGenerating.value = false;
            }
        }
        function acceptChanges() {
            const proj = currentProject.value;
            if (!proj)
                return;
            // 
            const historyRecord = {
                timestamp: new Date().toISOString(),
                prompt: aiPrompt.value,
                changes: aiChanges.value,
            };
            if (!aiHistory.value[currentId.value]) {
                aiHistory.value[currentId.value] = [];
            }
            aiHistory.value[currentId.value].unshift(historyRecord);
            console.log(' AI :', aiHistory.value[currentId.value].length);
            // 
            aiChanges.value.forEach(change => {
                const file = proj.files.find(f => f.path === change.path);
                if (file) {
                    file.content = change.newContent;
                }
            });
            // 
            saveToChatVar();
            updatePreview();
            // 
            if (currentFile.value) {
                const file = proj.files.find(f => f.path === currentFile.value);
                if (file) {
                    code.value = file.content;
                }
            }
            showComparison.value = false;
            aiChanges.value = [];
            toastr.success('');
        }
        function rejectChanges() {
            showComparison.value = false;
            aiChanges.value = [];
            toastr.info('');
        }
        function showHistoryDialog() {
            showHistory.value = true;
        }
        function closeHistoryDialog() {
            showHistory.value = false;
        }
        function restoreHistory(record) {
            if (!confirm(`\n\n"${record.prompt}"\n\n`)) {
                return;
            }
            const proj = currentProject.value;
            if (!proj)
                return;
            // 
            record.changes.forEach(change => {
                const file = proj.files.find(f => f.path === change.path);
                if (file) {
                    file.content = change.newContent;
                }
            });
            console.log(':', record.prompt);
            // 
            saveToChatVar();
            updatePreview();
            // 
            if (currentFile.value) {
                const file = proj.files.find(f => f.path === currentFile.value);
                if (file) {
                    code.value = file.content;
                }
            }
            closeHistoryDialog();
            toastr.success('');
        }
        function exportToRegex() {
            const proj = currentProject.value;
            if (!proj) {
                toastr.error('');
                return;
            }
            // 
            const fileObj = proj.files.find(f => f.name === currentFile.value);
            if (fileObj && code.value !== fileObj.content) {
                const shouldContinue = confirm('\n\n');
                if (!shouldContinue) {
                    return;
                }
            }
            // 
            const triggerWord = prompt(`${proj.name}\n\n\n  //g`, `${proj.name}`);
            if (!triggerWord) {
                toastr.info('');
                return;
            }
            try {
                //  HTML proj.files 
                const fullHtml = buildPreviewFromFiles(proj.files);
                //  UUID
                const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
                    const r = (Math.random() * 16) | 0;
                    const v = c === 'x' ? r : (r & 0x3) | 0x8;
                    return v.toString(16);
                });
                //  JSON
                // 1.  Windows  \r
                // 2.  <!DOCTYPE html>\n <html> 
                // 3. ```html\n\n<html>...</html>\n```
                let cleanHtml = fullHtml.replace(/\r/g, '');
                cleanHtml = cleanHtml.replace(/^<!DOCTYPE html>\n/, '');
                const regexJson = {
                    id: uuid,
                    scriptName: proj.name,
                    findRegex: triggerWord.startsWith('/') ? triggerWord : `/${triggerWord}/g`,
                    replaceString: '```html\n\n' + cleanHtml + '\n```',
                    trimStrings: [],
                    placement: [1, 2],
                    disabled: false,
                    markdownOnly: true,
                    promptOnly: false,
                    runOnEdit: true,
                    substituteRegex: 0,
                    minDepth: null,
                    maxDepth: null,
                };
                // 
                const dataStr = JSON.stringify(regexJson, null, 4);
                const dataBlob = new Blob([dataStr], { type: 'application/json' });
                const url = URL.createObjectURL(dataBlob);
                const link = document.createElement('a');
                link.href = url;
                link.download = `${proj.name}_regex.json`;
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
                URL.revokeObjectURL(url);
                console.log(' JSON:', regexJson);
                toastr.success(` "${proj.name}"  JSON`);
            }
            catch (error) {
                console.error(':', error);
                toastr.error(`: ${error.message}`);
            }
        }
        function openInNewWindow() {
            if (!previewHtml.value) {
                toastr.error('');
                return;
            }
            const proj = currentProject.value;
            const windowTitle = proj ? proj.name : '';
            // 
            const newWindow = window.open('', '_blank', 'width=1200,height=800,menubar=no,toolbar=no,location=no,status=no');
            if (newWindow) {
                newWindow.document.write(previewHtml.value);
                newWindow.document.close();
                newWindow.document.title = windowTitle;
                toastr.success('');
            }
            else {
                toastr.error('');
            }
        }
