\n            border-radius: 50%;\n            animation: spin 1s linear infinite;\n          \"\n        ></div>\n        <p style=\"color: #888; margin-top: 15px; font-size: 14px\">...</p>\n      </div>\n\n      <div style=\"display: flex; gap: 12px\">\n        <button\n          v-if=\"!isGenerating\"\n          @click=\"handleConfirm\"\n          :disabled=\"showInput && !localInput\"\n          style=\"\n            flex: 1;\n            background: #667eea;\n            color: white;\n            border: none;\n            padding: 12px 24px;\n            border-radius: 6px;\n            cursor: pointer;\n            font-size: 14px;\n            font-weight: 600;\n            transition: all 0.2s;\n          \"\n          :style=\"{ opacity: showInput && !localInput ? 0.5 : 1 }\"\n        >\n          <i class=\"fa-solid fa-check\"></i>\n          \n        </button>\n        <button\n          @click=\"handleClose\"\n          :disabled=\"isGenerating\"\n          style=\"\n            flex: 1;\n            background: #666;\n            color: white;\n            border: none;\n            padding: 12px 24px;\n            border-radius: 6px;\n            cursor: pointer;\n            font-size: 14px;\n            font-weight: 600;\n            transition: all 0.2s;\n          \"\n          :style=\"{ opacity: isGenerating ? 0.5 : 1 }\"\n        >\n          {{ isGenerating ? '...' : '' }}\n        </button>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, watch } from 'vue';\n\nconst props = defineProps<{\n  show: boolean;\n  title?: string;\n  description?: string;\n  placeholder?: string;\n  isGenerating?: boolean;\n  showInput?: boolean; // \n}>();\n\nconst emit = defineEmits<{\n  (e: 'close'): void;\n  (e: 'confirm', input?: string): void;\n}>();\n\nconst localInput = ref('');\n\n// \nwatch(\n  () => props.show,\n  newVal => {\n    if (newVal) {\n      localInput.value = '';\n    }\n  },\n);\n\nconst handleCo