 fa-circle-question\"></i> {{ showHelp ? '' : '' }}\n        </button>\n        <button\n          v-if=\"greetings.length > 0\"\n          class=\"mini-button\"\n          style=\"\n            padding: 6px 12px;\n            background: #dc2626;\n            border: none;\n            border-radius: 4px;\n            color: white;\n            cursor: pointer;\n            font-size: 12px;\n            font-weight: 600;\n            transition: all 0.2s;\n            display: flex;\n            align-items: center;\n            gap: 6px;\n          \"\n          @click=\"clearAllConfig\"\n          @mouseenter=\"($event.currentTarget as HTMLElement).style.transform = 'translateY(-2px)'\"\n          @mouseleave=\"($event.currentTarget as HTMLElement).style.transform = 'translateY(0)'\"\n        >\n          <i class=\"fa-solid fa-eraser\"></i> \n        </button>\n        <button\n          v-if=\"greetings.length > 0\"\n          class=\"mini-button\"\n          style=\"\n            padding: 6px 12px;\n            background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);\n            border: none;\n            border-radius: 4px;\n            color: white;\n            cursor: pointer;\n            font-size: 12px;\n            font-weight: 600;\n            transition: all 0.2s;\n            display: flex;\n            align-items: center;\n            gap: 6px;\n          \"\n          @click=\"showAiStyleDialog = true\"\n          @mouseenter=\"($event.currentTarget as HTMLElement).style.transform = 'translateY(-2px)'\"\n          @mouseleave=\"($event.currentTarget as HTMLElement).style.transform = 'translateY(0)'\"\n        >\n          <i class=\"fa-solid fa-wand-magic-sparkles\"></i> AI\n        </button>\n        <button\n          class=\"mini-button refresh-button\"\n          style=\"\n            padding: 6px 12px;\n            background: #2a2a2a;\n            border: 1px solid #3a3a3a;\n            border-radius: 4px;\n            color: #e0e0e0;\n            cursor: pointer;\n            font-size: 12px;\n            transition: all 0.2s;\n            display: flex;\n            align-items: center;\n            gap: 6px;\n          \"\n          @click=\"loadConfig\"\n        >\n          <i class=\"fa-solid fa-refresh\"></i> \n        </button>\n      </div>\n    </div>\n\n    <!--  -->\n    <div style=\"flex: 1; display: flex; gap: 20px; padding: 20px; overflow: hidden\">\n      <!--  -->\n      <div style=\"flex: 1; overflow-y: auto; padding-right: 10px\">\n        <!--  /  -->\n        <div\n          v-if=\"greetings.length === 0\"\n          style=\"\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n            justify-content: center;\n            padding: 60px 20px;\n            text-align: center;\n            color: #888;\n          \"\n        >\n          <i class=\"fa-solid fa-comment-dots\" style=\"font-size: 64px; margin-bottom: 20px; opacity: 0.3\"></i>\n          <div style=\"font-size: 18px; font-weight: 600; color: #e0e0e0; margin-bottom: 10px\"></div>\n          <div style=\"font-size: 14px; color: #888; max-width: 400px; line-height: 1.6\">\n            \n          </div>\n        </div>\n\n        <!--  -->\n        <div\n          v-else-if=\"showHelp\"\n          style=\"background: #2a2a2a; border: 1px solid #667eea; border-radius: 8px; padding: 20px; margin-bottom: 20px\"\n        >\n          <div style=\"display: flex; justify-content: space-between; align-items: center; margin-bottom: 15px\">\n            <h4 style=\"color: #667eea; margin: 0; display: flex; align-items: center; gap: 8px\">\n              <i class=\"fa-solid fa-circle-info\"></i>\n              \n            </h4>\n            <button\n              style=\"background: transparent; border: none; color: #888; cursor: pointer; font-size: 18px; padding: 4px\"\n              @click=\"showHelp = false\"\n            >\n              <i class=\"fa-solid fa-times\">