import { APISettings, ChatMessage } from '../types';

// API
async function fetchWithProxy(url: string, options: RequestInit = {}) {
  try {
    const origin = window.location.origin;
    console.log(' :', origin);

    const response = await fetch(`${origin}/api/proxy`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        ...(typeof SillyTavern !== 'undefined' && SillyTavern.getRequestHeaders ? SillyTavern.getRequestHeaders() : {}),
      },
      body: JSON.stringify({
        url: url,
        method: options.method || 'GET',
        headers: options.headers || {},
      }),
    });

    if (response.ok) {
      console.log(' ');
      return response;
    }
    console.log(' ');
  } catch (error) {
    console.log(' :', error);
  }

  return fetch(url, options);
}

// API
export function normalizeApiEndpoint(endpoint: string, path: string = ''): string {
  try {
    const url = new URL(endpoint);
    if (path && !url.pathname.endsWith('/')) {
      url.pathname += '/';
    }
    if (path) {
      url.pathname += path.startsWith('/') ? path.slice(1) : path;
    }
    return url.toString();
  } catch (error) {
    throw new Error(`API : ${endpoint}`);
  }
}

// 
export async function fetchAvailableModels(settings: APISettings): Promise<string[]> {
  if (!settings.api_endpoint || settings.api_endpoint.trim() === '') {
    throw new Error('API ');
  }

  const endpoint = settings.api_endpoint.trim();
  let modelEndpoint: string;

  console.log(' :', endpoint);

  try {
    modelEndpoint = normalizeApiEndpoint(endpoint, '/models');
    console.log('  models :', modelEndpoint);
  } catch (error) {
    throw new Error(`API : ${endpoint}`);
  }

  try {
    new URL(modelEndpoint);
  } catch (error) {
    throw new Error(`API : ${modelEndpoint}`);
  }

  // 
  const possibleEndpoints = [
    modelEndpoint,
    endpoint + (endpoint.endsWith('/') ? 'models' : '/models'),
    new URL(modelEndpoint).origin + '/v1/models',
    new URL(modelEndpoint).origin + '/models',
  ];

  console.log(' :', possibleEndpoints);

  const errors: string[] = [];
  let hasCorsError = false;

  for (const endpoint of possibleEndpoints) {
    try {
      console.log(` : ${endpoint}`);

      const response = await fetchWithProxy(endpoint, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${settings.api_key}`,
        },
      });

      console.log(` : ${response.status} ${response.statusText}`);

      if (!response.ok) {
        const errorText = await response.text();
        errors.push(`${endpoint}: ${response.status} - ${errorText.substring(0, 100)}`);
        console.log(` : ${response.status}`);
        console.log(`: ${errorText.substring(0, 200)}`);
        continue;
      }

      const contentType = response.headers.get('content-type');
      console.log(` : ${contentType}`);

      if (!contentType || !contentType.includes('application/json')) {
        const errorText = await response.text();
        errors.push(`${endpoint}:  JSON  - ${errorText.substring(0, 100)}`);
        console.log('  JSON :', errorText.substring(0, 200));
        continue;
      }

      const data = await response.json();
      console.log(' API :', JSON.stringify(data, null, 2));

      // 
      if (data.data && Array.isArray(data.data)) {
        const models = data.data.map((item: any) => item.id || item.name || item).filter(Boolean);
        if (models.length > 0) {
          console.log(`  ${models.length} :`, models);
          return models;
        }
      }

      if (Array.isArray(data)) {
        const models = data.map((item: any) => item.id || item.name || item).filter(Boolean);
        if (models.length > 0) {
          console.log(`  ${models.length} :`, models);
          return models;
        }
      }

      if (data.models && Array.isArray(data.models)) {
        const models = data.models.map((item: any) => item.id || item.name || item).filter(Boolean);
        if (models.length > 0) {
          console.log(`  ${models.length} :`, models);
          return models;
        }
      }

      errors.push(`${endpoint}:  - ${JSON.stringify(data).substring(0, 100)}`);
      console.log(' ');
    } catch (error) {
      const errorMessage = (error as Error).message;
      if (errorMessage.includes('Failed to fetch') || errorMessage.includes('CORS')) {
        hasCorsError = true;
      }
      errors.push(`${endpoint}: ${errorMessage}`);
      console.log(' :', error);
      continue;
    }
  }

  console.error(' :');
  errors.forEach((error, index) => console.error(`  ${index + 1}. ${error}`));

  let errorMessage = ` API  ${possibleEndpoints.length} \n\n\n${errors.join('\n\n')}\n\n\n1. API ${endpoint}\n2. API Key \n3.  API  /v1/models \n`;

  if (hasCorsError) {
    errorMessage += `\n  CORS \n API ${new URL(endpoint).origin} CORS \n\n  Zeabur  CORS\n  API  CORS \n  API\n\n`;
  }

  errorMessage += ' API ';

  throw new Error(errorMessage);
}

// 
export async function summarizeMessages(startId: number, endId: number, settings: APISettings): Promise<string> {
  if (!settings.api_endpoint || settings.api_endpoint.trim() === '') {
    throw new Error('API ');
  }

  const endpoint = settings.api_endpoint.trim();
  let apiUrl: string;

  try {
    apiUrl = normalizeApiEndpoint(endpoint);
    console.log(' :', endpoint);
    console.log('  API URL:', apiUrl);
  } catch (error) {
    throw new Error(`API : ${endpoint}`);
  }

  try {
    new URL(apiUrl);
  } catch (error) {
    throw new Error(`API : ${apiUrl}`);
  }

  // 
  const messages: ChatMessage[] = [];
  for (let messageId = startId; messageId <= endId; messageId++) {
    const messageList = getChatMessages(messageId);
    if (messageList.length > 0) {
      messages.push(...messageList);
    }
  }

  if (messages.length === 0) {
    throw new Error('');
  }

  const prompt = `****


- 
- 
- 
- 
- 
- XML
- 
-  ${settings.max_tokens / 4} 
- ****




: []
: []

 1: 
 2: 
 3: 
...


${messages.map(msg => `[${msg.role}]: ${msg.message}`).join('\n\n')}

`;

  let response: Response;

  console.log(' APIURL:', apiUrl);
  console.log(':', {
    model: settings.model,
    messages: [{ role: 'user', content: prompt }],
    max_tokens: settings.max_tokens,
    temperature: settings.temperature,
  });

  try {
    response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.api_key}`,
      },
      body: JSON.stringify({
        model: settings.model,
        messages: [{ role: 'user', content: prompt }],
        max_tokens: settings.max_tokens,
        temperature: settings.temperature,
        top_p: settings.top_p,
        presence_penalty: settings.presence_penalty,
        frequency_penalty: settings.frequency_penalty,
      }),
    });
  } catch (error) {
    console.error('fetch :', error);
    throw new Error(` API: ${(error as Error).message}`);
  }

  if (!response.ok) {
    let errorMessage = `API : ${response.status}`;
    let errorDetails = '';

    try {
      const errorData = await response.json();
      errorMessage = errorData.error?.message || errorData.message || errorMessage;
      errorDetails = JSON.stringify(errorData, null, 2);
    } catch (error) {
      try {
        errorDetails = await response.text();
      } catch {
        // 
      }
    }

    let userFriendlyMessage = errorMessage;

    if (response.status === 500) {
      userFriendlyMessage = `API  (500)${errorMessage}\n\n\n API \n \n `;
    } else if (response.status === 429) {
      userFriendlyMessage = 'API  (429)';
    } else if (response.status === 401) {
      userFriendlyMessage = 'API  (401) API ';
    } else if (response.status === 400) {
      userFriendlyMessage = `API  (400)${errorMessage}`;
    }

    console.error(' API :', {
      status: response.status,
      statusText: response.statusText,
      errorMessage,
      errorDetails: errorDetails.substring(0, 500),
    });

    throw new Error(userFriendlyMessage);
  }

  const data = await response.json();

  if (!data.choices || !data.choices[0] || !data.choices[0].message?.content) {
    throw new Error('API ');
  }

  return data.choices[0].message.content;
}
