import { useSettingsStore, normalizeApiEndpoint } from './settings';

/**
 *  CORS
 */
async function proxyFetch(url: string, options: RequestInit = {}): Promise<Response> {
  try {
    //  1: 
    const tavernOrigin = window.location.origin; //  http://localhost:8000

    console.log(' :', tavernOrigin);

    const proxyResponse = await fetch(`${tavernOrigin}/api/proxy`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        ...(typeof SillyTavern !== 'undefined' && SillyTavern.getRequestHeaders ? SillyTavern.getRequestHeaders() : {}),
      },
      body: JSON.stringify({
        url: url,
        method: options.method || 'GET',
        headers: options.headers || {},
      }),
    });

    if (proxyResponse.ok) {
      console.log(' ');
      return proxyResponse;
    }

    console.log(' ');
  } catch (proxyError) {
    console.log(' :', proxyError);
  }

  //  2:  CORS
  return fetch(url, options);
}

/**
 * 
 */
export async function fetchAvailableModels(): Promise<string[]> {
  const settings = useSettingsStore().settings;

  //  API endpoint
  if (!settings.api_endpoint || settings.api_endpoint.trim() === '') {
    throw new Error('API ');
  }

  //  normalizeApiEndpoint  models 
  const baseUrl = settings.api_endpoint.trim();
  console.log(' :', baseUrl);

  //  /models 
  let modelsUrl: string;
  try {
    modelsUrl = normalizeApiEndpoint(baseUrl, '/models');
    console.log('  models :', modelsUrl);
  } catch (e) {
    throw new Error(`API : ${baseUrl}`);
  }

  //  URL
  try {
    new URL(modelsUrl);
  } catch (e) {
    throw new Error(`API : ${modelsUrl}`);
  }

  //  models API 
  const possibleEndpoints = [
    modelsUrl, //  /models
    // 
    baseUrl + (baseUrl.endsWith('/') ? 'models' : '/models'),
    new URL(modelsUrl).origin + '/v1/models',
    new URL(modelsUrl).origin + '/models',
  ];

  console.log(' :', possibleEndpoints);

  const errors: string[] = [];
  let hasCorsError = false;

  for (const modelsUrl of possibleEndpoints) {
    try {
      console.log(` : ${modelsUrl}`);

      // 
      const response = await proxyFetch(modelsUrl, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${settings.api_key}`,
        },
      });

      console.log(` : ${response.status} ${response.statusText}`);

      if (!response.ok) {
        const errorText = await response.text();
        errors.push(`${modelsUrl}: ${response.status} - ${errorText.substring(0, 100)}`);
        console.log(` : ${response.status}`);
        console.log(`: ${errorText.substring(0, 200)}`);
        continue;
      }

      const contentType = response.headers.get('content-type');
      console.log(` : ${contentType}`);

      if (!contentType || !contentType.includes('application/json')) {
        const text = await response.text();
        errors.push(`${modelsUrl}:  JSON  - ${text.substring(0, 100)}`);
        console.log(`  JSON :`, text.substring(0, 200));
        continue;
      }

      const data = await response.json();
      console.log(' API :', JSON.stringify(data, null, 2));

      // OpenAI  API : { data: [...] }
      if (data.data && Array.isArray(data.data)) {
        const models = data.data.map((model: any) => model.id || model.name || model).filter(Boolean);
        console.log(`  ${models.length} :`, models);
        if (models.length > 0) {
          return models;
        }
      }

      // 
      if (Array.isArray(data)) {
        const models = data.map((model: any) => model.id || model.name || model).filter(Boolean);
        console.log(`  ${models.length} :`, models);
        if (models.length > 0) {
          return models;
        }
      }

      //  API  { models: [...] }
      if (data.models && Array.isArray(data.models)) {
        const models = data.models.map((model: any) => model.id || model.name || model).filter(Boolean);
        console.log(`  ${models.length} :`, models);
        if (models.length > 0) {
          return models;
        }
      }

      errors.push(`${modelsUrl}:  - ${JSON.stringify(data).substring(0, 100)}`);
      console.log(' ');
    } catch (e) {
      const errMsg = (e as Error).message;
      if (errMsg.includes('Failed to fetch') || errMsg.includes('CORS')) {
        hasCorsError = true;
      }
      errors.push(`${modelsUrl}: ${errMsg}`);
      console.log(` :`, e);
      continue;
    }
  }

  console.error(' :');
  errors.forEach((err, i) => console.error(`  ${i + 1}. ${err}`));

  let errorMessage =
    ` API  ${possibleEndpoints.length} \n\n` +
    `\n${errors.join('\n\n')}\n\n` +
    `\n` +
    `1. API ${baseUrl}\n` +
    `2. API Key \n` +
    `3.  API  /v1/models \n`;

  if (hasCorsError) {
    errorMessage +=
      `\n  CORS \n` +
      ` API ${new URL(baseUrl).origin} CORS \n` +
      `\n` +
      `  Zeabur  CORS\n` +
      `  API  CORS \n` +
      `  API\n\n`;
  }

  errorMessage += ` API `;

  throw new Error(errorMessage);
}

/**
 * 
 * @param start_id 
 * @param end_id 
 * @returns 
 */
export async function summarizeMessages(start_id: number, end_id: number): Promise<string> {
  const settings = useSettingsStore().settings;

  //  API endpoint
  if (!settings.api_endpoint || settings.api_endpoint.trim() === '') {
    throw new Error('API ');
  }

  //  API  /chat/completions
  const baseUrl = settings.api_endpoint.trim();
  let apiUrl: string;
  try {
    apiUrl = normalizeApiEndpoint(baseUrl);
    console.log(' :', baseUrl);
    console.log('  API URL:', apiUrl);
  } catch (e) {
    throw new Error(`API : ${baseUrl}`);
  }

  //  URL
  try {
    new URL(apiUrl);
  } catch (e) {
    throw new Error(`API : ${apiUrl}`);
  }

  // 
  // 
  const messages: Array<{ role: string; message: string }> = [];
  for (let i = start_id; i <= end_id; i++) {
    const msg = getChatMessages(i);
    if (msg.length > 0) {
      messages.push(...msg);
    }
  }

  if (messages.length === 0) {
    throw new Error('');
  }

  //  - 
  const summaryPrompt = `****


- 
- 
- 
- 
- 
- XML
- 
-  ${settings.max_tokens / 4} 
- ****




: []
: []

 1: 
 2: 
 3: 
...


${messages.map(msg => `[${msg.role}]: ${msg.message}`).join('\n\n')}

`;

  console.log(' APIURL:', apiUrl);
  console.log(':', {
    model: settings.model,
    messages: [{ role: 'user', content: summaryPrompt }],
    max_tokens: settings.max_tokens,
    temperature: settings.temperature,
  });

  //  OpenAI  API
  let response;
  try {
    response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.api_key}`,
      },
      body: JSON.stringify({
        model: settings.model,
        messages: [
          {
            role: 'user',
            content: summaryPrompt,
          },
        ],
        max_tokens: settings.max_tokens,
        temperature: settings.temperature,
        top_p: settings.top_p,
        presence_penalty: settings.presence_penalty,
        frequency_penalty: settings.frequency_penalty,
      }),
    });
  } catch (e) {
    console.error('fetch :', e);
    throw new Error(` API: ${(e as Error).message}`);
  }

  if (!response.ok) {
    let errorMessage = `API : ${response.status}`;
    let errorDetails = '';

    try {
      const error = await response.json();
      errorMessage = error.error?.message || error.message || errorMessage;
      errorDetails = JSON.stringify(error, null, 2);
    } catch (e) {
      //  JSON
      try {
        errorDetails = await response.text();
      } catch {}
    }

    // 
    let userFriendlyMessage = errorMessage;
    if (response.status === 500) {
      userFriendlyMessage = `API  (500)${errorMessage}\n\n\n API \n \n `;
    } else if (response.status === 429) {
      userFriendlyMessage = `API  (429)`;
    } else if (response.status === 401) {
      userFriendlyMessage = `API  (401) API `;
    } else if (response.status === 400) {
      userFriendlyMessage = `API  (400)${errorMessage}`;
    }

    console.error(' API :', {
      status: response.status,
      statusText: response.statusText,
      errorMessage,
      errorDetails: errorDetails.substring(0, 500),
    });

    throw new Error(userFriendlyMessage);
  }

  const data = await response.json();
  if (!data.choices || !data.choices[0] || !data.choices[0].message?.content) {
    throw new Error('API ');
  }
  return data.choices[0].message.content;
}
