nerateWithStreaming(requestPayload, characterProgressPercent);\n    } else {\n      // \n      const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n      const response = await fetch(apiUrl, {\n        method: 'POST',\n        headers: {\n          'Content-Type': 'application/json',\n          Authorization: `Bearer ${settings.value.api_key}`,\n        },\n        body: JSON.stringify(requestPayload),\n      });\n\n      if (!response.ok) {\n        throw new Error(`HTTP error! status: ${response.status}`);\n      }\n\n      const data = await response.json();\n      generatedText = data.choices[0].message.content.trim();\n      characterProgressPercent.value = 100;\n    }\n\n    characterCardOutput.value = generatedText;\n    saveToolsDataImmediate(); // \n\n    window.toastr.success('');\n  } catch (error) {\n    console.error(':', error);\n    window.toastr.error('' + (error as Error).message);\n  } finally {\n    isGeneratingCharacter.value = false;\n    characterProgressPercent.value = 0;\n  }\n};\n\nconst clearCharacterForm = () => {\n  characterDescription.value = '';\n  characterCardOutput.value = '';\n  modifyRequest.value = '';\n  saveToolsDataImmediate(); // \n  window.toastr.success('');\n};\n\nconst copyCharacterCard = () => {\n  if (!characterCardOutput.value) {\n    window.toastr.warning('');\n    return;\n  }\n\n  // \n  copyToClipboard(characterCardOutput.value, '');\n};\n\n// \nconst clearAntiClicheForm = () => {\n  antiClicheInput.value = '';\n  antiClicheOutput.value = '';\n  saveToolsDataImmediate(); // \n  window.toastr.success('');\n};\n\n// \nconst handleModifyCharacterCard = async () => {\n  if (!modifyRequest.value.trim()) {\n    window.toastr.warning('');\n    return;\n  }\n\n  if (!characterCardOutput.value) {\n    window.toastr.warning('');\n    return;\n  }\n\n  try {\n    isModifyingCharacter.value = true;\n    characterProgressPercent.value = 0;\n    window.toastr.info('AI...');\n\n    const requestPayload = {\n      model: settings.value.model || 'gpt-3.5-turbo',\n      max_tokens: settings.value.max_tokens || 16000,\n      temperature: 0.8,\n      stream: enableCharacterStreaming.value,\n      messages: [\n        {\n          role: 'system',\n          content: ` YAML \n\n****\n1.  YAML \n2. \n3. \n4. \n5. \n6. \n\n****\n- ////\n- \n- \n- AI\n\n YAML `,\n        },\n        {\n          role: 'user',\n          content: `\n\n${characterCardOutput.value}\n\n---\n\n\n${modifyRequest.value}`,\n        },\n      ],\n    };\n\n    let modifiedCard: string;\n\n    if (enableCharacterStreaming.value) {\n      // \n      modifiedCard = await generateWithStreaming(requestPayload, characterProgressPercent);\n    } else {\n      // \n      const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n      const response = await fetch(apiUrl, {\n        method: 'POST',\n        headers: {\n          'Content-Type': 'application/json',\n          Authorization: `Bearer ${settings.value.api_key}`,\n        },\n        body: JSON.stringify(requestPayload),\n      });\n\n      if (!response.ok) {\n        throw new Error(`HTTP error! status: ${response.status}`);\n      }\n\n      const data = await response.json();\n      modifiedCard = data.choices[0].message.content.trim();\n      characterProgressPercent.value = 100;\n    }\n\n    characterCardOutput.value = modifiedCard;\n    modifyRequest.value = ''; // \n    saveToolsDataImmediate(); // \n\n    window.toastr.success('');\n  } catch (error) {\n    console.error(':', error);\n    window.toastr.error('' + (error as Error).message);\n  } finally {\n    isModifyingCharacter.value = false;\n    characterProgressPercent.value = 0;\n  }\n};\n\n// \nconst clearModifyRequest = () => {\n  modifyRequest.value = '';\n  saveToolsDataImmediate(); // \n  window.toastr.success('');\n};\n\n// ====================  ====================\n\n// \nconst extractStreamContent = (chunk: any): string | null => {\n  // OpenAI \n  if (chunk.choices?.[0]?.delta?.content) {\n    return chunk.choices[0].delta.content;\n  }\n  // Claude \n  if (chunk.delta?.text) {\n    return chunk.delta.text;\n  }\n  // \n  if (typeof chunk.content === 'string') {\n    return chunk.content;\n  }\n  if (typeof chunk.text === 'string') {\n    return chunk.text;\n  }\n  if (typeof chunk.response === 'string') {\n    return chunk.response;\n  }\n  return null;\n};\n\n// \nconst generateWithStreaming = async (\n  requestPayload: Record<string, any>,\n  progressRef: Ref<number>,\n): Promise<string> => {\n  const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n  const response = await fetch(apiUrl, {\n    method: 'POST',\n    headers: {\n      'Content-Type': 'application/json',\n      Authorization: `Bearer ${settings.value.api_key}`,\n    },\n    body: JSON.stringify(requestPayload),\n  });\n\n  if (!response.ok) {\n    const errorText = await response.text();\n    throw new Error(`${response.status} ${response.statusText}\\n${errorText.slice(0, 400)}`);\n  }\n\n  if (!response.body) {\n    throw new Error(' API ');\n  }\n\n  const reader = response.body.getReader();\n  const decoder = new TextDecoder('utf-8');\n  let buffer = '';\n  let done = false;\n  let result = '';\n\n  progressRef.value = 0;\n\n  const processLine = (line: string): boolean => {\n    const trimmed = line.trim();\n    if (!trimmed) {\n      return false;\n    }\n\n    if (trimmed === 'data: [DONE]' || trimmed === '[DONE]') {\n      return true;\n    }\n\n    const payloadText = trimmed.startsWith('data:') ? trimmed.slice(5).trim() : trimmed;\n    if (!payloadText || payloadText === '[DONE]') {\n      return false;\n    }\n\n    try {\n      const payloadJson = JSON.parse(payloadText);\n      const deltaText = extractStreamContent(payloadJson);\n      if (deltaText) {\n        result += deltaText;\n        progressRef.value = Math.min(progressRef.value + 2, 98);\n      }\n    } catch (parseError) {\n      console.warn(':', payloadText.slice(0, 100), parseError);\n    }\n\n    return false;\n  };\n\n  while (!done) {\n    const { value, done: readerDone } = await reader.read();\n    if (value) {\n      buffer += decoder.decode(value, { stream: true });\n      const lines = buffer.split('\\n');\n      buffer = lines.pop() ?? '';\n      for (const line of lines) {\n        if (processLine(line)) {\n          done = true;\n          break;\n        }\n      }\n    }\n\n    if (readerDone) {\n      done = true;\n    }\n  }\n\n  if (buffer && !done) {\n    processLine(buffer);\n  }\n\n  progressRef.value = 100;\n  return result;\n};\n\n// ====================  ====================\n\n// \nconst handleGenerateWorldbookEntry = async () => {\n  if (!worldbookDescription.value.trim()) {\n    window.toastr.warning('');\n    return;\n  }\n\n  try {\n    isGeneratingWorldbook.value = true;\n    worldbookProgressPercent.value = 0;\n    window.toastr.info('AI...', '', { timeOut: 15000 });\n\n    const requestPayload = {\n      model: settings.value.model || 'gpt-3.5-turbo',\n      max_tokens: settings.value.max_tokens || 4000,\n      temperature: 0.7,\n      stream: enableWorldbookStreaming.value,\n      messages: [\n        {\n          role: 'system',\n          content: ` SillyTavern  JSON \n\n****\n\\`\\`\\`typescript\n{\n  \"name\": \"\",\n  \"enabled\": true,\n  \"strategy\": {\n    \"type\": \"selective\",  // \"constant\"(-)  \"selective\"(-)\n    \"keys\": [\"1\", \"2\"],  // \n    \"keys_secondary\": {\n      \"logic\": \"and_any\",  // \"and_any\" | \"and_all\" | \"not_all\" | \"not_any\"\n      \"keys\": []  // \n    },\n    \"scan_depth\": \"same_as_global\"  //  10\n  },\n  \"position\": {\n    \"type\": \"after_character_definition\",  // : \"after_character_definition\" | \"at_depth\"\n    \"role\": \"system\",  // \"system\" | \"assistant\" | \"user\"\n    \"depth\": 4,\n    \"order\": 100\n  },\n  \"content\": \"\",\n  \"probability\": 100,  //  100\n  \"recursion\": {\n    \"prevent_incoming\": false,\n    \"prevent_outgoing\": false,\n    \"delay_until\": null\n  },\n  \"effect\": {\n    \"sticky\": null,\n    \"cooldown\": null,\n    \"delay\": null\n  }\n}\n\\`\\`\\`\n\n****\n1. ** (name)**\n2. ** (strategy.type)**\n   -  \"selective\" ()\n   -  \"constant\" ()\n3. ** (strategy.keys)**\n   - \n   - \n   -  3-6 \n4. ** (content)**\n   - ****\n   - \n   - \n   - ** content **\n   - ****\n5. ** (position.type)**\n   -  \"after_character_definition\"\n   -  \"at_depth\" \n6. ****\n\n** 1**\n\"16\"\n\n\\`\\`\\`json\n{\n  \"name\": \"\",\n  \"enabled\": true,\n  \"strategy\": {\n    \"type\": \"selective\",\n    \"keys\": [\"\", \"\", \"\"],\n    \"keys_secondary\": { \"logic\": \"and_any\", \"keys\": [] },\n    \"scan_depth\": \"same_as_global\"\n  },\n  \"position\": {\n    \"type\": \"after_character_definition\",\n    \"role\": \"system\",\n    \"depth\": 4,\n    \"order\": 100\n  },\n  \"content\": \"16\",\n  \"probability\": 100,\n  \"recursion\": {\n    \"prevent_incoming\": false,\n    \"prevent_outgoing\": false,\n    \"delay_until\": null\n  },\n  \"effect\": {\n    \"sticky\": null,\n    \"cooldown\": null,\n    \"delay\": null\n  }\n}\n\\`\\`\\`\n\n** 2**\n\"\"\n\n\\`\\`\\`json\n{\n  \"name\": \"\",\n  \"enabled\": true,\n  \"strategy\": {\n    \"type\": \"selective\",\n    \"keys\": [\"\", \"\", \"\", \"\"],\n    \"keys_secondary\": { \"logic\": \"and_any\", \"keys\": [] },\n    \"scan_depth\": \"same_as_global\"\n  },\n  \"position\": {\n    \"type\": \"after_character_definition\",\n    \"role\": \"system\",\n    \"depth\": 4,\n    \"order\": 100\n  },\n  \"content\": \"\",\n  \"probability\": 100,\n  \"recursion\": {\n    \"prevent_incoming\": false,\n    \"prevent_outgoing\": false,\n    \"delay_until\": null\n  },\n  \"effect\": {\n    \"sticky\": null,\n    \"cooldown\": null,\n    \"delay\": null\n  }\n}\n\\`\\`\\`\n\n****\n-  JSON \n-  JSON \n- **content **\n-  content \n-  strategy.type`,\n        },\n        {\n          role: 'user',\n          content: wor