                    return;
                }
                const scriptVars = getVariables({ type: 'script', script_id: scriptId });
                const savedData = scriptVars.mvu_beta_config;
                if (savedData) {
                    if (savedData.varName)
                        varName.value = savedData.varName;
                    if (savedData.subFields)
                        subFields.value = savedData.subFields;
                    if (savedData.generatedStructure)
                        generatedStructure.value = savedData.generatedStructure;
                    if (savedData.variableCategories)
                        variableCategories.value = savedData.variableCategories;
                    if (savedData.generatedPrompt)
                        generatedPrompt.value = savedData.generatedPrompt;
                    console.log('  MVU Beta ');
                }
            }
            catch (error) {
                console.error(' MVU Beta :', error);
            }
        }
