 background: #444;\n            border: none;\n            border-radius: 8px;\n            color: #e0e0e0;\n            font-size: 13px;\n            font-weight: 500;\n            cursor: pointer;\n            transition: all 0.2s;\n          \"\n          @click=\"showHistoryDialog\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#555')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#444')\"\n        >\n          <i class=\"fa-solid fa-history\" style=\"margin-right: 6px\"></i>\n          \n        </button>\n        <button\n          v-if=\"currentId\"\n          style=\"\n            padding: 8px 16px;\n            background: #8b5cf6;\n            border: none;\n            border-radius: 8px;\n            color: white;\n            font-size: 13px;\n            font-weight: 500;\n            cursor: pointer;\n            transition: all 0.2s;\n          \"\n          @click=\"showAIDialog\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#7c3aed')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#8b5cf6')\"\n        >\n          <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 6px\"></i>\n          AI \n        </button>\n        <button\n          v-if=\"currentId\"\n          style=\"\n            padding: 8px 16px;\n            background: #ef4444;\n            border: none;\n            border-radius: 8px;\n            color: white;\n            font-size: 13px;\n            font-weight: 500;\n            cursor: pointer;\n            transition: all 0.2s;\n          \"\n          @click=\"showBugFixDialog\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#dc2626')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#ef4444')\"\n        >\n          <i class=\"fa-solid fa-bug\" style=\"margin-right: 6px\"></i>\n          Bug \n        </button>\n        <button\n          style=\"\n            padding: 8px 16px;\n            background: #444;\n            border: none;\n            border-radius: 8px;\n            color: #e0e0e0;\n            font-size: 13px;\n            font-weight: 500;\n            cursor: pointer;\n            transition: all 0.2s;\n          \"\n          @click=\"showProjectTemplateDialog\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#555')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#444')\"\n        >\n          <i class=\"fa-solid fa-file-lines\" style=\"margin-right: 6px\"></i>\n          \n        </button>\n        <button\n          style=\"\n            padding: 8px 16px;\n            background: #444;\n            border: none;\n            border-radius: 8px;\n            color: #e0e0e0;\n            font-size: 13px;\n            font-weight: 500;\n            cursor: pointer;\n            transition: all 0.2s;\n          \"\n          @click=\"triggerFolderImport\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#555')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#444')\"\n        >\n          <i class=\"fa-solid fa-folder-open\" style=\"margin-right: 6px\"></i>\n          \n        </button>\n        <button\n          style=\"\n            padding: 8px 16px;\n            background: #10b981;\n            border: none;\n            border-radius: 8px;\n            color: white;\n            font-size: 13px;\n            font-weight: 500;\n            cursor: pointer;\n            transition: all 0.2s;\n          \"\n          @click=\"createProject\"\n          @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#059669')\"\n          @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#10b981')\"\n        >\n          <i class=\"fa-solid fa-plus\" style=\"margin-right: 6px\"></i>\n          \n        </button>\n      </div>\n    </div>\n\n    <!--  -->\n    <input\n 