             <i class=\"fa-solid fa-folder-plus\" style=\"font-size: 10px\"></i>\n              </button>\n              <button\n                style=\"\n                  padding: 4px 8px;\n                  background: #10b981;\n                  border: none;\n                  border-radius: 5px;\n                  color: white;\n                  font-size: 11px;\n                  cursor: pointer;\n                  transition: all 0.2s;\n                  display: flex;\n                  align-items: center;\n                  gap: 4px;\n                \"\n                title=\"\"\n                @click=\"showNewFileDialog\"\n                @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#059669')\"\n                @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#10b981')\"\n              >\n                <i class=\"fa-solid fa-plus\" style=\"font-size: 10px\"></i>\n              </button>\n            </div>\n          </div>\n          <div\n            v-for=\"file in currentProject.files\"\n            :key=\"file.path\"\n            :style=\"{\n              padding: '8px 10px',\n              marginBottom: '5px',\n              background: currentFile === file.path ? '#10b981' : '#1e1e1e',\n              borderRadius: '6px',\n              cursor: 'pointer',\n              transition: 'all 0.2s ease',\n              color: currentFile === file.path ? '#fff' : '#e0e0e0',\n              fontSize: '12px',\n              display: 'flex',\n              justifyContent: 'space-between',\n              alignItems: 'center',\n            }\"\n            @click=\"selectFile(file.path)\"\n            @mouseenter=\"(e: any) => currentFile !== file.path && (e.currentTarget.style.background = '#3a3a3a')\"\n            @mouseleave=\"(e: any) => currentFile !== file.path && (e.currentTarget.style.background = '#1e1e1e')\"\n          >\n            <span>\n              <i :class=\"getFileIcon(file.path)\" style=\"margin-right: 6px\"></i>\n              {{ file.name }}\n            </span>\n            <button\n              style=\"\n                width: 18px;\n                height: 18px;\n                background: rgba(239, 68, 68, 0.2);\n                border: none;\n                border-radius: 3px;\n                color: #ff4444;\n                font-size: 12px;\n                line-height: 1;\n                cursor: pointer;\n                opacity: 0;\n                transition: all 0.2s ease;\n              \"\n              title=\"\"\n              @click.stop=\"deleteFile(file.path)\"\n              @mouseenter=\"\n                (e: any) => (\n                  (e.currentTarget.style.opacity = '1'),\n                  (e.currentTarget.style.background = '#ef4444'),\n                  (e.currentTarget.style.color = '#fff')\n                )\n              \"\n              @mouseleave=\"\n                (e: any) => (\n                  (e.currentTarget.style.opacity = '0.6'),\n                  (e.currentTarget.style.background = 'rgba(239, 68, 68, 0.2)'),\n                  (e.currentTarget.style.color = '#ff4444')\n                )\n              \"\n            >\n              \n            </button>\n          </div>\n        </div>\n      </div>\n\n      <!--  -->\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 15px;\n          border: 1px solid #3a3a3a;\n          display: flex;\n          flex-direction: column;\n        \"\n      >\n        <div\n          v-if=\"!currentId\"\n          style=\"flex: 1; display: flex; align-items: center; justify-content: center; color: #666; font-size: 14px\"\n        >\n           \n        </div>\n        <div\n          v-else-if=\"!currentFile\"\n          style=\"flex: 1; display: flex; align-items: center; justify-content: center; color: #666; font-size: 14px\"\n        >\n           \n        </div>\n        <div v-else style=\"display: flex; flex-direction: column; height: 100%\">\n          <div style=\"margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center\">\n            <div style=\"display: flex; align-items: center; gap: 8px\">\n              <i :class=\"getFileIcon(currentFile)\" style=\"color: #4a9eff\"></i>\n              <span style=\"color: #fff; font-size: 14px; font-weight: 500\">{{ currentFile }}</span>\n            </div>\n            <div style=\"display: flex; align-items: center; gap: 10px\">\n              <span style=\"color: #888; font-size: 11px\">\n                <i class=\"fa-solid fa-circle-check\" style=\"color: #10b981; margin-right: 4px\"></i>\n                \n              </span>\n              <button\n                style=\"\n                  padding: 6px 12px;\n                  background: linear-gradient(135deg, #10b981 0%, #059669 100%);\n                  border: none;\n                  border-radius: 6px;\n                  color: white;\n                  font-size: 12px;\n                  font-weight: 600;\n                  cursor: pointer;\n                  transition: all 0.2s ease;\n                \"\n                @click=\"saveCode\"\n                @mouseenter=\"(e: any) => (e.currentTarget.style.transform = 'translateY(-1px)')\"\n                @mouseleave=\"(e: any) => (e.currentTarget.style.transform = 'translateY(0)')\"\n              >\n                <i class=\"fa-solid fa-floppy-disk\" style=\"margin-right: 4px\"></i>\n                \n              </button>\n            </div>\n          </div>\n          <textarea\n            v-model=\"code\"\n            :placeholder=\"` ${currentFile.split('.').pop()?.toUpperCase()} ...`\"\n            style=\"\n              flex: 1;\n              background: #1e1e1e;\n              border: 1px solid #3a3a3a;\n              border-radius: 8px;\n              color: #e0e0e0;\n              font-family: 'Consolas', 'Monaco', monospace;\n              font-size: 13px;\n              line-height: 1.6;\n              padding: 15px;\n              resize: none;\n              outline: none;\n              min-height: 400px;\n            \"\n          ></textarea>\n        </div>\n      </div>\n\n      <!--  -->\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 15px;\n          border: 1px solid #3a3a3a;\n          display: flex;\n          flex-direction: column;\n        \"\n      >\n        <div style=\"display: flex; justify-content: space-between; align-items: center; margin-bottom: 15px\">\n          <h4 style=\"margin: 0; color: #fff; font-size: 14px; font-weight: 600\">\n            <i class=\"fa-solid fa-eye\" style=\"margin-right: 8px; color: #4a9eff\"></i>\n            \n          </h4>\n          <div style=\"display: flex; gap: 8px\">\n            <button\n              v-if=\"previewHtml\"\n              title=\"\"\n              style=\"\n                width: 28px;\n                height: 28px;\n                padding: 0;\n                background: #3a3a3a;\n                border: none;\n                border-radius: 6px;\n                color: #e0e0e0;\n                cursor: pointer;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                transition: all 0.2s ease;\n              \"\n              @click=\"openInNewWindow\"\n              @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#4a9eff')\"\n              @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#3a3a3a')\"\n            >\n              <i class=\"fa-solid fa-external-link-alt\" style=\"font-size: 12px\"></i>\n            </button>\n          </div>\n        </div>\n        <div\n          style=\"\n            flex: 1;\n            background: #1e1e1e;\n            border-radius: 8px;\n            overflow: auto;\n            position: relative;\n            min-height: 400px;\n          \"\n        >\n          <iframe\n            v-if=\"previewHtml\"\n            :srcdoc=\"previewHtml\"\n            sandbox=\"allow-scripts allow-same-origin allow-modals allow-forms\"\n            style=\"width: 100%; height: 100%; border: none; background: white\"\n          ></iframe>\n          <div\n            v-else\n            style=\"\n              position: absolute;\n              top: 50%;\n              left: 50%;\n              transform: translate(-50%, -50%);\n              color: #666;\n              font-size: 12px;\n              text-align: center;\n            \"\n          >\n            \n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!-- AI  -->\n    <div\n      v-if=\"showAI\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.8);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n      \"\n      @click.self=\"closeAIDialog\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 25px;\n          width: 90%;\n          max-width: 600px;\n          border: 1px solid #3a3a3a;\n          box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);\n        \"\n      >\n        <h3\n          style=\"\n            margin: 0 0 20px 0;\n            color: #fff;\n            font-size: 18px;\n            font-weight: 600;\n            display: flex;\n            align-items: center;\n            gap: 10px;\n          \"\n        >\n          <i class=\"fa-solid fa-wand-magic-sparkles\" style=\"color: #8b5cf6\"></i>\n          AI \n        </h3>\n\n        <div style=\"margin-bottom: 15px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #ccc; font-size: 13px\">\n            <i class=\"fa-solid fa-pencil\" style=\"color: #8b5cf6; margin-right: 6px\"></i>\n            \n          </label>\n          <div\n            style=\"\n              margin-bottom: 10px;\n              padding: 10px 12px;\n              background: rgba(139, 92, 246, 0.1);\n              border-left: 3px solid #8b5cf6;\n              border-radius: 4px;\n              font-size: 12px;\n              color: #ccc;\n              line-height: 1.6;\n            \"\n          >\n            <strong style=\"color: #8b5cf6\"> </strong>\n            \"\"\"\"\n          </div>\n          <textarea\n            v-model=\"aiPrompt\"\n            placeholder=\"...\"\n            style=\"\n              width: 100%;\n              height: 120px;\n              background: #1e1e1e;\n              border: 1px solid #3a3a3a;\n              border-radius: 8px;\n              color: #e0e0e0;\n              font-size: 13px;\n              line-height: 1.6;\n              padding: 12px;\n              resize: none;\n              outline: none;\n            \"\n          ></textarea>\n\n          <!--  -->\n          <div style=\"margin-top: 12px\">\n            <div style=\"color: #888; font-size: 12px; margin-bottom: 8px\">\n              <i class=\"fa-solid fa-lightbulb\" style=\"color: #fbbf24; margin-right: 4px\"></i>\n              \n            </div>\n            <div style=\"display: flex; flex-wrap: wrap; gap: 8px\">\n              <button\n                v-for=\"suggestion in quickSuggestions\"\n                :key=\"suggestion\"\n                style=\"\n                  padding: 6px 12px;\n                  background: #2a2a2a;\n                  border: 1px solid #3a3a3a;\n                  border-radius: 6px;\n                  color: #ccc;\n                  font-size: 12px;\n                  cursor: pointer;\n                  transition: all 0.2s;\n                \"\n                @click=\"aiPrompt = suggestion\"\n                @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#3a3a3a')\"\n                @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#2a2a2a')\"\n              >\n                {{ suggestion }}\n              </button>\n            </div>\n          </div>\n        </div>\n\n        <!--  -->\n        <div\n          style=\"\n            display: flex;\n            align-items: center;\n            gap: 10px;\n            margin-bottom: 15px;\n            padding: 10px;\n            background: #252525;\n            border-radius: 6px;\n          \"\n        >\n          <label\n            style=\"\n              display: flex;\n              align-items: center;\n              gap: 8px;\n              cursor: pointer;\n              color: #ccc;\n              font-size: 13px;\n              user-select: none;\n            \"\n          >\n            <input v-model=\"enableStream\" type=\"checkbox\" style=\"width: 16px; height: 16px; cursor: pointer\" />\n            <i class=\"fa-solid fa-water\" style=\"color: #4a9eff\"></i>\n            \n          </label>\n          <span style=\"color: #888; font-size: 12px\">  </span>\n        </div>\n\n        <!--  -->\n        <div\n          v-if=\"aiGenerating && enableStream && streamingText\"\n          style=\"\n            margin-bottom: 15px;\n            padding: 15px;\n            background: #1e1e1e;\n            border: 1px solid #3a3a3a;\n            border-radius: 6px;\n            max-height: 200px;\n            overflow-y: auto;\n            font-family: 'Consolas', monospace;\n            font-size: 12px;\n            color: #e0e0e0;\n            line-height: 1.5;\n            white-space: pre-wrap;\n          \"\n        >\n          {{ streamingText }}\n          <span style=\"animation: blink 1s infinite\"></span>\n        </div>\n\n        <div style=\"display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 8px 16px;\n              background: #3a3a3a;\n              border: none;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              cursor: pointer;\n              transition: all 0.2s ease;\n            \"\n            @click=\"closeAIDialog\"\n            @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#4a4a4a')\"\n            @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#3a3a3a')\"\n          >\n            \n          </button>\n          <button\n            :disabled=\"aiGenerating || !aiPrompt.trim()\"\n            style=\"\n              padding: 8px 16px;\n              background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n              transition: all 0.2s ease;\n            \"\n            :style=\"{\n              opacity: aiGenerating || !aiPrompt.trim() ? 0.5 : 1,\n              cursor: aiGenerating || !aiPrompt.trim() ? 'not-allowed' : 'pointer',\n            }\"\n            @click=\"generateWithAI\"\n          >\n            <i v-if=\"!aiGenerating\" class=\"fa-solid fa-wand-magic-sparkles\" style=\"margin-right: 6px\"></i>\n            <i v-else class=\"fa-solid fa-spinner fa-spin\" style=\"margin-right: 6px\"></i>\n            {{ aiGenerating ? '...' : '' }}\n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!-- Bug  -->\n    <div\n      v-if=\"showBugFix\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.8);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n      \"\n      @click.self=\"closeBugFixDialog\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          padding: 25px;\n          width: 90%;\n          max-width: 650px;\n          border: 1px solid #3a3a3a;\n          box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);\n        \"\n      >\n        <h3\n          style=\"\n            margin: 0 0 20px 0;\n            color: #fff;\n            font-size: 18px;\n            font-weight: 600;\n            display: flex;\n            align-items: center;\n            gap: 10px;\n          \"\n        >\n          <i class=\"fa-solid fa-bug\" style=\"color: #ef4444\"></i>\n          Bug \n        </h3>\n\n        <!--  -->\n        <div style=\"margin-bottom: 15px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #ccc; font-size: 13px; font-weight: 600\">\n            <i class=\"fa-solid fa-circle-exclamation\" style=\"color: #f59e0b; margin-right: 6px\"></i>\n            \n          </label>\n          <textarea\n            v-model=\"bugDescription\"\n            placeholder=\"&#10;&#10;\"\n            style=\"\n              width: 100%;\n              height: 90px;\n              background: #1e1e1e;\n              border: 1px solid #3a3a3a;\n              border-radius: 8px;\n              color: #e0e0e0;\n              font-size: 13px;\n              line-height: 1.6;\n              padding: 12px;\n              resize: none;\n              outline: none;\n            \"\n          ></textarea>\n        </div>\n\n        <!-- Console  -->\n        <div style=\"margin-bottom: 15px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #ccc; font-size: 13px; font-weight: 600\">\n            <i class=\"fa-solid fa-terminal\" style=\"color: #ef4444; margin-right: 6px\"></i>\n             Console  F12 \n          </label>\n          <textarea\n            v-model=\"consoleError\"\n            placeholder=\" Console &#10;Uncaught TypeError: xxx is not a function&#10;&#10;\"\n            style=\"\n              width: 100%;\n              height: 80px;\n              background: #1e1e1e;\n              border: 1px solid #3a3a3a;\n              border-radius: 8px;\n              color: #ff6b6b;\n              font-size: 12px;\n              font-family: 'Consolas', monospace;\n              line-height: 1.5;\n              padding: 12px;\n              resize: none;\n              outline: none;\n            \"\n          ></textarea>\n        </div>\n\n        <!--  -->\n        <div style=\"margin-bottom: 15px\">\n          <label style=\"display: block; margin-bottom: 8px; color: #ccc; font-size: 13px; font-weight: 600\">\n            <i class=\"fa-solid fa-file-code\" style=\"color: #4a9eff; margin-right: 6px\"></i>\n            \n          </label>\n          <select\n            v-model=\"bugFile\"\n            style=\"\n              width: 100%;\n              background: #1e1e1e;\n              border: 1px solid #3a3a3a;\n              border-radius: 8px;\n              color: #e0e0e0;\n              font-size: 13px;\n              padding: 10px 12px;\n              outline: none;\n              cursor: pointer;\n            \"\n          >\n            <option value=\"\"> AI </option>\n            <option value=\"index.html\">HTML index.html</option>\n            <option value=\"style.css\">CSS style.css</option>\n            <option value=\"script.js\">JavaScript script.js</option>\n          </select>\n        </div>\n\n        <!--  -->\n        <div\n          style=\"\n            padding: 12px;\n            background: rgba(74, 158, 255, 0.1);\n            border: 1px solid rgba(74, 158, 255, 0.3);\n            border-radius: 6px;\n            margin-bottom: 15px;\n          \"\n        >\n          <p style=\"margin: 0; color: #4a9eff; font-size: 12px; line-height: 1.6\">\n            <i class=\"fa-solid fa-lightbulb\" style=\"margin-right: 6px\"></i>\n            <strong></strong>AI  bug Console\n            \n          </p>\n        </div>\n\n        <div style=\"display: flex; gap: 10px; justify-content: flex-end\">\n          <button\n            style=\"\n              padding: 8px 16px;\n              background: #3a3a3a;\n              border: none;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              cursor: pointer;\n              transition: all 0.2s ease;\n            \"\n            @click=\"closeBugFixDialog\"\n            @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#4a4a4a')\"\n            @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#3a3a3a')\"\n          >\n            \n          </button>\n          <button\n            :disabled=\"aiGenerating || !bugDescription.trim()\"\n            style=\"\n              padding: 8px 16px;\n              background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);\n              border: none;\n              border-radius: 6px;\n              color: white;\n              font-size: 13px;\n              font-weight: 600;\n              cursor: pointer;\n              transition: all 0.2s ease;\n            \"\n            :style=\"{\n              opacity: aiGenerating || !bugDescription.trim() ? 0.5 : 1,\n              cursor: aiGenerating || !bugDescription.trim() ? 'not-allowed' : 'pointer',\n            }\"\n            @click=\"fixBugWithAI\"\n          >\n            <i v-if=\"!aiGenerating\" class=\"fa-solid fa-wrench\" style=\"margin-right: 6px\"></i>\n            <i v-else class=\"fa-solid fa-spinner fa-spin\" style=\"margin-right: 6px\"></i>\n            {{ aiGenerating ? '...' : '' }}\n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!-- AI  -->\n    <div\n      v-if=\"showComparison\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.9);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10001;\n        padding: 20px;\n      \"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          width: 95%;\n          height: 90%;\n          border: 1px solid #3a3a3a;\n          box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);\n          display: flex;\n          flex-direction: column;\n        \"\n      >\n        <!--  -->\n        <div\n          style=\"\n            padding: 20px 25px;\n            border-bottom: 1px solid #3a3a3a;\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n          \"\n        >\n          <h3\n            style=\"\n              margin: 0;\n              color: #fff;\n              font-size: 18px;\n              font-weight: 600;\n              display: flex;\n              align-items: center;\n              gap: 10px;\n            \"\n          >\n            <i class=\"fa-solid fa-code-compare\" style=\"color: #8b5cf6\"></i>\n            AI \n          </h3>\n          <div style=\"display: flex; gap: 10px\">\n            <button\n              style=\"\n                padding: 8px 16px;\n                background: #ef4444;\n                border: none;\n                border-radius: 6px;\n                color: white;\n                font-size: 13px;\n                cursor: pointer;\n                transition: all 0.2s ease;\n              \"\n              @click=\"rejectChanges\"\n              @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#dc2626')\"\n              @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#ef4444')\"\n            >\n              <i class=\"fa-solid fa-xmark\" style=\"margin-right: 6px\"></i>\n              \n            </button>\n            <button\n              style=\"\n                padding: 8px 16px;\n                background: #10b981;\n                border: none;\n                border-radius: 6px;\n                color: white;\n                font-size: 13px;\n                font-weight: 500;\n                cursor: pointer;\n                transition: all 0.2s;\n              \"\n              @click=\"acceptChanges\"\n              @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#059669')\"\n              @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#10b981')\"\n            >\n              <i class=\"fa-solid fa-check\" style=\"margin-right: 6px\"></i>\n              \n            </button>\n          </div>\n        </div>\n\n        <!--  -->\n        <div\n          style=\"\n            flex: 1;\n            display: grid;\n            grid-template-columns: 1fr 1fr 1.5fr;\n            gap: 15px;\n            padding: 20px;\n            overflow: hidden;\n          \"\n        >\n          <!--  -->\n          <div style=\"display: flex; flex-direction: column; background: #1e1e1e; border-radius: 8px; overflow: hidden\">\n            <div style=\"padding: 12px 15px; background: #2a2a2a; border-bottom: 1px solid #3a3a3a\">\n              <h4 style=\"margin: 0; color: #fff; font-size: 14px; font-weight: 600\">\n                <i class=\"fa-solid fa-file-code\" style=\"margin-right: 8px; color: #ef4444\"></i>\n                \n              </h4>\n            </div>\n            <div style=\"flex: 1; overflow-y: auto; padding: 15px\">\n              <div v-for=\"change in aiChanges\" :key=\"change.path\" style=\"margin-bottom: 20px\">\n                <div\n                  style=\"\n                    color: #4a9eff;\n                    font-size: 12px;\n                    font-weight: 600;\n                    margin-bottom: 8px;\n                    display: flex;\n                    align-items: center;\n                    gap: 6px;\n                  \"\n                >\n                  <i :class=\"getFileIcon(change.path)\"></i>\n                  {{ change.path }}\n                </div>\n                <pre\n                  style=\"\n                    margin: 0;\n                    padding: 12px;\n                    background: #1a1a1a;\n                    border-radius: 6px;\n                    color: #e0e0e0;\n                    font-family: 'Consolas', 'Monaco', monospace;\n                    font-size: 12px;\n                    line-height: 1.5;\n                    overflow-x: auto;\n                    border: 1px solid #3a3a3a;\n                  \"\n                  >{{ change.oldContent }}</pre\n                >\n              </div>\n            </div>\n          </div>\n\n          <!--  -->\n          <div style=\"display: flex; flex-direction: column; background: #1e1e1e; border-radius: 8px; overflow: hidden\">\n            <div style=\"padding: 12px 15px; background: #2a2a2a; border-bottom: 1px solid #3a3a3a\">\n              <h4 style=\"margin: 0; color: #fff; font-size: 14px; font-weight: 600\">\n                <i class=\"fa-solid fa-file-code\" style=\"margin-right: 8px; color: #10b981\"></i>\n                \n              </h4>\n            </div>\n            <div style=\"flex: 1; overflow-y: auto; padding: 15px\">\n              <div v-for=\"change in aiChanges\" :key=\"change.path\" style=\"margin-bottom: 20px\">\n                <div\n                  style=\"\n                    color: #4a9eff;\n                    font-size: 12px;\n                    font-weight: 600;\n                    margin-bottom: 8px;\n                    display: flex;\n                    align-items: center;\n                    gap: 6px;\n                  \"\n                >\n                  <i :class=\"getFileIcon(change.path)\"></i>\n                  {{ change.path }}\n                </div>\n                <pre\n                  style=\"\n                    margin: 0;\n                    padding: 12px;\n                    background: #1a1a1a;\n                    border-radius: 6px;\n                    color: #e0e0e0;\n                    font-family: 'Consolas', 'Monaco', monospace;\n                    font-size: 12px;\n                    line-height: 1.5;\n                    overflow-x: auto;\n                    border: 1px solid #3a3a3a;\n                  \"\n                  >{{ change.newContent }}</pre\n                >\n              </div>\n            </div>\n          </div>\n\n          <!--  -->\n          <div style=\"display: flex; flex-direction: column; background: #1e1e1e; border-radius: 8px; overflow: hidden\">\n            <div\n              style=\"\n                padding: 12px 15px;\n                background: #2a2a2a;\n                border-bottom: 1px solid #3a3a3a;\n                display: flex;\n                justify-content: space-between;\n                align-items: center;\n              \"\n            >\n              <h4 style=\"margin: 0; color: #fff; font-size: 14px; font-weight: 600\">\n                <i class=\"fa-solid fa-eye\" style=\"margin-right: 8px; color: #4a9eff\"></i>\n                \n              </h4>\n              <div style=\"display: flex; gap: 8px\">\n                <button\n                  :style=\"{\n                    padding: '4px 10px',\n                    background: previewMode === 'before' ? '#ef4444' : '#3a3a3a',\n                    border: 'none',\n                    borderRadius: '4px',\n                    color: 'white',\n                    fontSize: '11px',\n                    cursor: 'pointer',\n                    transition: 'all 0.2s ease',\n                  }\"\n                  @click=\"previewMode = 'before'\"\n                >\n                  \n                </button>\n                <button\n                  :style=\"{\n                    padding: '4px 10px',\n                    background: previewMode === 'after' ? '#10b981' : '#3a3a3a',\n                    border: 'none',\n                    borderRadius: '4px',\n                    color: 'white',\n                    fontSize: '11px',\n                    cursor: 'pointer',\n                    transition: 'all 0.2s ease',\n                  }\"\n                  @click=\"previewMode = 'after'\"\n                >\n                  \n                </button>\n              </div>\n            </div>\n            <div style=\"flex: 1; background: white; overflow: hidden\">\n              <iframe\n                :srcdoc=\"comparisonPreviewHtml\"\n                sandbox=\"allow-scripts allow-same-origin allow-modals allow-forms\"\n                style=\"width: 100%; height: 100%; border: none\"\n              ></iframe>\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <div\n      v-if=\"showHistory\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.8);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n        padding: 20px;\n      \"\n      @click.self=\"closeHistoryDialog\"\n    >\n      <div\n        style=\"\n          background: #2a2a2a;\n          border-radius: 12px;\n          width: 90%;\n          max-width: 900px;\n          max-height: 80%;\n          border: 1px solid #3a3a3a;\n          box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);\n          display: flex;\n          flex-direction: column;\n        \"\n      >\n        <div\n          style=\"\n            padding: 20px 25px;\n            border-bottom: 1px solid #3a3a3a;\n            display: flex;\n            justify-content: space-between;\n            align-items: center;\n          \"\n        >\n          <h3\n            style=\"\n              margin: 0;\n              color: #fff;\n              font-size: 18px;\n              font-weight: 600;\n              display: flex;\n              align-items: center;\n              gap: 10px;\n            \"\n          >\n            <i class=\"fa-solid fa-history\" style=\"color: #f59e0b\"></i>\n            AI \n          </h3>\n          <button\n            style=\"\n              width: 28px;\n              height: 28px;\n              background: #3a3a3a;\n              border: none;\n              border-radius: 6px;\n              color: #e0e0e0;\n              cursor: pointer;\n              transition: all 0.2s ease;\n            \"\n            @click=\"closeHistoryDialog\"\n            @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#4a4a4a')\"\n            @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#3a3a3a')\"\n          >\n            \n          </button>\n        </div>\n        <div style=\"flex: 1; overflow-y: auto; padding: 20px\">\n          <div v-if=\"currentProjectHistory.length === 0\" style=\"text-align: center; color: #666; padding: 40px 20px\">\n            <i class=\"fa-solid fa-inbox\" style=\"font-size: 48px; margin-bottom: 15px; opacity: 0.3\"></i>\n            <p></p>\n          </div>\n          <div\n            v-for=\"(record, index) in currentProjectHistory\"\n            :key=\"index\"\n            style=\"\n              background: #1e1e1e;\n              border-radius: 8px;\n              padding: 15px;\n              margin-bottom: 15px;\n              border: 1px solid #3a3a3a;\n            \"\n          >\n            <div style=\"display: flex; justify-content: space-between; align-items: start; margin-bottom: 10px\">\n              <div>\n                <div style=\"color: #fff; font-size: 14px; font-weight: 600; margin-bottom: 5px\">\n                  {{ record.prompt }}\n                </div>\n                <div style=\"color: #888; font-size: 12px\">\n                  {{ new Date(record.timestamp).toLocaleString('zh-CN') }}\n                </div>\n              </div>\n              <button\n                style=\"\n                  padding: 6px 12px;\n                  background: #4a9eff;\n                  border: none;\n                  border-radius: 6px;\n                  color: white;\n                  font-size: 12px;\n                  font-weight: 500;\n                  cursor: pointer;\n                  transition: all 0.2s;\n                \"\n                @click=\"restoreHistory(record)\"\n                @mouseenter=\"(e: any) => (e.currentTarget.style.background = '#3b82f6')\"\n                @mouseleave=\"(e: any) => (e.currentTarget.style.background = '#4a9eff')\"\n              >\n                <i class=\"fa-solid fa-undo\" style=\"margin-right: 4px\"></i>\n                \n              </button>\n            </div>\n            <div style=\"color: #ccc; font-size: 13px; margin-top: 10px\">\n              <strong></strong>\n              <div style=\"margin-top: 5px; display: flex; flex-wrap: wrap; gap: 8px\">\n                <span\n                  v-for=\"change in record.changes\"\n                  :key=\"change.path\"\n                  style=\"\n                    background: #2a2a2a;\n                    padding: 4px 10px;\n                    border-radius: 4px;\n                    font-size: 12px;\n                    display: inline-flex;\n                    align-items: center;\n                    gap: 6px;\n                  \"\n                >\n                  <i :class=\"getFileIcon(change.path)\"></i>\n                  {{ change.path }}\n                </span>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <div\n      v-if=\"showProjectTemplate\"\n      style=\"\n        position: fixed;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background: rgba(0, 0, 0, 0.8);\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 10000;\n        padding: 20px;\n      \"\n      @click.self=\"showProjectTemplate = false\"\n    >\n      <div\n        style=\"\n          background: #1e1e1e;\n          border-radius: 16px;\n          max-width: 850px;\n          width: 100%;\n          max-height: 90vh;\n          overflow-y: auto;\n          box-shadow: 0 20px 60px rgba(0, 0, 0,