___CSS_LOADER_EXPORT___.push([module.id, `.settings-tab{height:100%;overflow-y:auto;padding:25px !important;background:#1a1a1a !important;border:2px solid red !important}.config-section{padding:20px 25px !important;border-bottom:1px solid #3a3a3a;margin-bottom:5px}.config-section:last-child{border-bottom:none;margin-bottom:0}.config-section h3{margin:0 0 20px 0 !important;color:#fff;font-size:15px !important;font-weight:bold;display:flex;align-items:center;gap:8px}.form-group{margin-bottom:18px !important}.form-group label{display:block;margin-bottom:6px;color:#ccc;font-size:13px}.checkbox-label{display:flex;align-items:center;gap:8px}.flex-label{display:flex;justify-content:space-between;align-items:center}.form-group input[type='text'],.form-group input[type='password'],.form-group input[type='number'],.form-group select{width:100%;padding:10px 12px;background:#2a2a2a;border:1px solid #3a3a3a;border-radius:6px;color:#e0e0e0;font-size:13px;transition:border-color 0.2s}.form-group input:focus,.form-group select:focus{outline:none;border-color:#4a9eff}.model-controls{display:flex;flex-direction:column;gap:10px}.button-group{display:flex;gap:12px;flex-wrap:wrap;margin-top:5px}.action-button{flex:1;min-width:120px;padding:12px 16px;border:none;border-radius:6px;cursor:pointer;font-weight:500;font-size:13px;transition:all 0.2s;display:flex;align-items:center;justify-content:center;gap:6px;background:#3a3a3a;color:#e0e0e0;border:1px solid #4a4a4a}.action-button:hover{background:#4a4a4a;border-color:#5a5a5a}.test-button{background:#3a3a3a;color:#e0e0e0;border:1px solid #4a4a4a}.test-button:hover{background:#4a4a4a;border-color:#5a5a5a}.fetch-button{background:#4a9eff;color:white;border:1px solid #5aaeff}.fetch-button:hover{background:#5aaeff;border-color:#6abeff}.summarize-button{background:#4a9eff;color:white;border:1px solid #5aaeff}.summarize-button:hover{background:#5aaeff;border-color:#6abeff}.create-button{background:#51cf66;color:white;border:1px solid #40c057}.create-button:hover{background:#40c057;border-color:#37b24d}.table-button{background:#4a9eff;color:white;border:1px solid #5aaeff}.table-button:hover{background:#5aaeff;border-color:#6abeff}.hide-button{background:#ff6b6b;color:white;border:1px solid #ff5252}.hide-button:hover{background:#ff5252;border-color:#ff4444}.unhide-button{background:#51cf66;color:white;border:1px solid #40c057}.unhide-button:hover{background:#40c057;border-color:#37b24d}.refresh-button{background:#ffd43b;color:#333;border:1px solid #ffcc02}.refresh-button:hover{background:#ffcc02;border-color:#ffb700}.action-button:disabled{opacity:0.5;cursor:not-allowed}.model-select{margin-top:8px}.hidden-messages-section{margin-top:20px}.hidden-messages-list{max-height:300px;overflow-y:auto;border-radius:6px;border:1px solid #3a3a3a;background:#1a1a1a}.hidden-message-item{padding:10px 12px;display:flex;align-items:center;gap:12px;border-bottom:1px solid #2a2a2a;transition:background 0.2s}.hidden-message-item:last-child{border-bottom:none}.hidden-message-item:hover{background:#2a2a2a}.message-info{display:flex;align-items:center;gap:10px;flex:1}.message-id{font-weight:bold;color:#4a9eff;font-size:13px;min-width:50px}.message-role{font-size:12px;padding:2px 8px;border-radius:4px;white-space:nowrap}.message-role.role-user{background:#3a5a3a;color:#90ee90}.message-role.role-assistant{background:#3a3a5a;color:#90a0ee}.message-role.role-system{background:#5a5a3a;color:#eea090}.message-preview{flex:1;color:#888;font-size:12px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.show-button{background:#4a9eff !important;color:white !important;padding:6px 12px;border:1px solid #5aaeff !important;border-radius:4px;cursor:pointer;font-size:12px;transition:all 0.2s;min-width:60px;text-align:center}.show-button:hover{background:#5aaeff !important;border-color:#6abeff !important}.mini-button{background:#666;color:white;border:1px solid #777;padding:6px 12px;border-radius:4px;cursor:pointer;font-size:12px;transition:all 0.2s}.mini-button:hover{background:#777;border-color:#888}.empty-state{margin-top:20px;padding:20px;background:#2a2a2a;border-radius:6px;text-align:center;color:#888;border:1px solid #3a3a3a}
`, "",{"version":3,"sources":["webpack://./src/_with_worldbook/components/SettingsTab.vue"],"names":[],"mappings":"AA6kFA,cACE,WAAY,CACZ,eAAgB,CAChB,uBAAwB,CACxB,6BAA8B,CAC9B,+BACF,CAEA,gBACE,4BAA6B,CAC7B,+BAAgC,CAChC,iBACF,CAEA,2BACE,kBAAmB,CACnB,eACF,CAEA,mBACE,4BAA6B,CAC7B,UAAW,CACX,yBAA0B,CAC1B,gBAAiB,CACjB,YAAa,CACb,kBAAmB,CACnB,OACF,CAEA,YACE,6BACF,CAEA,kBACE,aAAc,CACd,iBAAkB,CAClB,UAAW,CACX,cACF,CAEA,gBACE,YAAa,CACb,kBAAmB,CACnB,OACF,CAEA,YACE,YAAa,CACb,6BAA8B,CAC9B,kBACF,CAEA,sHAIE,UAAW,CACX,iBAAkB,CAClB,kBAAmB,CACnB,wBAAyB,CACzB,iBAAkB,CAClB,aAAc,CACd,cAAe,CACf,4BACF,CAEA,iDAEE,YAAa,CACb,oBACF,CAEA,gBACE,YAAa,CACb,qBAAsB,CACtB,QACF,CAEA,cACE,YAAa,CACb,QAAS,CACT,cAAe,CACf,cACF,CAEA,eACE,MAAO,CACP,eAAgB,CAChB,iBAAkB,CAClB,WAAY,CACZ,iBAAkB,CAClB,cAAe,CACf,eAAgB,CAChB,cAAe,CACf,mBAAoB,CACpB,YAAa,CACb,kBAAmB,CACnB,sBAAuB,CACvB,OAAQ,CACR,kBAAmB,CACnB,aAAc,CACd,wBACF,CAEA,qBACE,kBAAmB,CACnB,oBACF,CAEA,aACE,kBAAmB,CACnB,aAAc,CACd,wBACF,CAEA,mBACE,kBAAmB,CACnB,oBACF,CAEA,cACE,kBAAmB,CACnB,WAAY,CACZ,wBACF,CAEA,oBACE,kBAAmB,CACnB,oBACF,CAEA,kBACE,kBAAmB,CACnB,WAAY,CACZ,wBACF,CAEA,wBACE,kBAAmB,CACnB,oBACF,CAEA,eACE,kBAAmB,CACnB,WAAY,CACZ,wBACF,CAEA,qBACE,kBAAmB,CACnB,oBACF,CAEA,cACE,kBAAmB,CACnB,WAAY,CACZ,wBACF,CAEA,oBACE,kBAAmB,CACnB,oBACF,CAEA,aACE,kBAAmB,CACnB,WAAY,CACZ,wBACF,CAEA,mBACE,kBAAmB,CACnB,oBACF,CAEA,eACE,kBAAmB,CACnB,WAAY,CACZ,wBACF,CAEA,qBACE,kBAAmB,CACnB,oBACF,CAEA,gBACE,kBAAmB,CACnB,UAAW,CACX,wBACF,CAEA,sBACE,kBAAmB,CACnB,oBACF,CAEA,wBACE,WAAY,CACZ,kBACF,CAEA,cACE,cACF,CAEA,yBACE,eACF,CAEA,sBACE,gBAAiB,CACjB,eAAgB,CAChB,iBAAkB,CAClB,wBAAyB,CACzB,kBACF,CAEA,qBACE,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,QAAS,CACT,+BAAgC,CAChC,0BACF,CAEA,gCACE,kBACF,CAEA,2BACE,kBACF,CAEA,cACE,YAAa,CACb,kBAAmB,CACnB,QAAS,CACT,MACF,CAEA,YACE,gBAAiB,CACjB,aAAc,CACd,cAAe,CACf,cACF,CAEA,cACE,cAAe,CACf,eAAgB,CAChB,iBAAkB,CAClB,kBACF,CAEA,wBACE,kBAAmB,CACnB,aACF,CAEA,6BACE,kBAAmB,CACnB,aACF,CAEA,0BACE,kBAAmB,CACnB,aACF,CAEA,iBACE,MAAO,CACP,UAAW,CACX,cAAe,CACf,eAAgB,CAChB,sBAAuB,CACvB,kBACF,CAEA,aACE,6BAA8B,CAC9B,sBAAuB,CACvB,gBAAiB,CACjB,mCAAoC,CACpC,iBAAkB,CAClB,cAAe,CACf,cAAe,CACf,mBAAoB,CACpB,cAAe,CACf,iBACF,CAEA,mBACE,6BAA8B,CAC9B,+BACF,CAEA,aACE,eAAgB,CAChB,WAAY,CACZ,qBAAsB,CACtB,gBAAiB,CACjB,iBAAkB,CAClB,cAAe,CACf,cAAe,CACf,mBACF,CAEA,mBACE,eAAgB,CAChB,iBACF,CAEA,aACE,eAAgB,CAChB,YAAa,CACb,kBAAmB,CACnB,iBAAkB,CAClB,iBAAkB,CAClB,UAAW,CACX,wBACF","sourcesContent":["<template>\n  <div class=\"settings-tab\" style=\"padding: 25px !important; background: #1a1a1a !important\">\n    <!-- API  -->\n    <div\n      class=\"config-section\"\n      style=\"padding: 20px 25px !important; border-bottom: 1px solid #3a3a3a; margin-bottom: 5px\"\n    >\n      <div\n        style=\"\n          cursor: pointer;\n          user-select: none;\n          display: flex;\n          align-items: center;\n          justify-content: space-between;\n          padding: 20px 28px;\n          background: linear-gradient(\n            135deg,\n            rgba(30, 30, 30, 0.95) 0%,\n            rgba(38, 38, 38, 0.9) 50%,\n            rgba(30, 30, 30, 0.95) 100%\n          );\n          backdrop-filter: blur(12px);\n          border-radius: 14px;\n          margin-bottom: 20px;\n          border: 1px solid rgba(255, 255, 255, 0.06);\n          box-shadow:\n            0 3px 12px rgba(0, 0, 0, 0.3),\n            inset 0 1px 0 rgba(255, 255, 255, 0.04),\n            inset 0 -1px 0 rgba(0, 0, 0, 0.2);\n          position: relative;\n          overflow: hidden;\n          transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);\n        \"\n        @click=\"toggleSection('api')\"\n        @mouseenter=\"\n          ($event.currentTarget as HTMLElement).style.background =\n            'linear-gradient(135deg, rgba(42, 42, 42, 0.98) 0%, rgba(50, 50, 50, 0.95) 50%, rgba(42, 42, 42, 0.98) 100%)';\n          ($event.currentTarget as HTMLElement).style.transform = 'translateX(4px) scale(1.005)';\n          ($event.currentTarget as HTMLElement).style.borderLeft = '3px solid rgba(255, 193, 7, 0.6)';\n        \"\n        @mouseleave=\"\n          ($event.currentTarget as HTMLElement).style.background =\n            'linear-gradient(135deg, rgba(30, 30, 30, 0.95) 0%, rgba(38, 38, 38, 0.9) 50%, rgba(30, 30, 30, 0.95) 100%)';\n          ($event.currentTarget as HTMLElement).style.transform = 'none';\n          ($event.currentTarget as HTMLElement).style.borderLeft = '1px solid rgba(255, 255, 255, 0.06)';\n        \"\n      >\n        <h3\n          style=\"\n            position: relative;\n            z-index: 1;\n            display: flex;\n            align-items: center;\n            gap: 12px;\n            margin: 0;\n            color: #fff;\n            font-size: 16px;\n            font-weight: 600;\n            letter-spacing: 0.5px;\n            text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);\n          \"\n        >\n          <i class=\"fa-solid fa-cog\" style=\"color: #17a2b8; font-size: 18px\"></i>\n          API \n        </h3>\n        <i\n          :class=\"expandedSections['api'] ? 'fa-chevron-up' : 'fa-chevron-down'\"\n          class=\"fa-solid\"\n          style=\"color: rgba(255, 255, 255, 0.6); font-size: 14px; transition: transform 0.3s\"\n        ></i>\n      </div>\n\n      <div v-show=\"expandedSections['api']\">\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">API </label>\n          <select\n            v-model=\"settings.api_provider\"\n            @change=\"handleProviderChange\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n              cursor: pointer;\n            \"\n          >\n            <option value=\"openai\">OpenAI</option>\n            <option value=\"gemini\">Gemini AI Studio</option>\n          </select>\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">\n            API \n            <span style=\"color: #888; font-size: 11px; margin-left: 8px\"> ( base URL ) </span>\n          </label>\n          <input\n            v-model=\"settings.api_endpoint\"\n            type=\"text\"\n            placeholder=\"https:///v1\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n          <div\n            style=\"\n              margin-top: 8px;\n              padding: 8px 12px;\n              background: rgba(74, 158, 255, 0.1);\n              border-radius: 4px;\n              border-left: 3px solid #4a9eff;\n            \"\n          >\n            <div style=\"color: #4a9eff; font-size: 12px; font-weight: bold; margin-bottom: 4px\">\n               \n            </div>\n            <div style=\"color: #999; font-size: 11px; line-height: 1.6\">\n               <strong>OpenAI :</strong> https://api.openai.com/v1<br />\n               <strong>Gemini AI Studio:</strong> https://generativelanguage.googleapis.com/v1beta/openai/<br />\n               <strong>NewAPI / One API:</strong> https:///v1<br />\n               <strong> (Ollama):</strong> http://localhost:11434/v1<br />\n               <strong></strong> /chat/completions\n            </div>\n          </div>\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">API Key</label>\n          <input\n            v-model=\"settings.api_key\"\n            type=\"password\"\n            placeholder=\"sk-...\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n        </div>\n\n        <div class=\"form-group\">\n          <div class=\"model-controls\" style=\"display: flex; flex-direction: column; gap: 10px\">\n            <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">\n              \n              <span v-if=\"available_models.length === 0\" style=\"color: #888; font-size: 11px; margin-left: 8px\">\n                ( gpt-4o-mini)\n              </span>\n            </label>\n            <div class=\"button-group\" style=\"display: flex; gap: 12px; flex-wrap: wrap; margin-top: 5px\">\n              <button\n                :disabled=\"loading_models\"\n                class=\"fetch-button\"\n                style=\"\n                  flex: 1;\n                  min-width: 120px;\n                  padding: 12px 16px;\n                  border: 1px solid #5aaeff;\n                  border-radius: 6px;\n                  cursor: pointer;\n                  font-weight: 500;\n                  font-size: 13px;\n                  transition: all 0.2s;\n                  display: flex;\n                  align-items: center;\n                  justify-content: center;\n                  gap: 6px;\n                  background: linear-gradient(135deg, #4a9eff 0%, #3a8edf 100%);\n                  box-shadow:\n                    0 2px 8px rgba(74, 158, 255, 0.25),\n                    inset 0 1px 0 rgba(255, 255, 255, 0.15);\n                  color: white;\n                \"\n                onmouseover=\"this.style.transform='translateY(-2px)'; this.style.boxShadow='0 4px 12px rgba(74, 158, 255, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.15)'\"\n                onmouseout=\"this.style.transform='translateY(0)'; this.style.boxShadow='0 2px 8px rgba(74, 158, 255, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.15)'\"\n                @click=\"handle_fetch_models\"\n              >\n                {{ loading_models ? '...' : ' ' }}\n              </button>\n              <button\n                class=\"save-button\"\n                style=\"\n                  flex: 1;\n                  min-width: 120px;\n                  padding: 12px 16px;\n                  border: 1px solid #28a745;\n                  border-radius: 6px;\n                  cursor: pointer;\n                  font-weight: 500;\n                  font-size: 13px;\n                  transition: all 0.2s;\n                  display: flex;\n                  align-items: center;\n                  justify-content: center;\n                  gap: 6px;\n                  background: linear-gradient(135deg, #51cf66 0%, #40c057 100%);\n                  box-shadow:\n                    0 2px 8px rgba(81, 207, 102, 0.25),\n                    inset 0 1px 0 rgba(255, 255, 255, 0.15);\n                  color: white;\n                \"\n                onmouseover=\"this.style.transform='translateY(-2px)'; this.style.boxShadow='0 4px 12px rgba(81, 207, 102, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.15)'\"\n                onmouseout=\"this.style.transform='translateY(0)'; this.style.boxShadow='0 2px 8px rgba(81, 207, 102, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.15)'\"\n                @click=\"handleSaveApiConfig\"\n              >\n                 \n              </button>\n            </div>\n          </div>\n          <input\n            v-if=\"available_models.length === 0\"\n            v-model=\"settings.model\"\n            type=\"text\"\n            placeholder=\"gpt-4o-mini  deepseek-chat \"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n          <select\n            v-else\n            v-model=\"settings.model\"\n            class=\"model-select\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          >\n            <option v-for=\"model in available_models\" :key=\"model\" :value=\"model\">{{ model }}</option>\n          </select>\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\"\n            > Token 4000</label\n          >\n          <input\n            v-model.number=\"settings.max_tokens\"\n            type=\"number\"\n            min=\"100\"\n            max=\"16000\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">\n            Temperature () <span style=\"color: #888; font-size: 11px\">(0-2 0.7)</span>\n          </label>\n          <input\n            v-model.number=\"settings.temperature\"\n            type=\"number\"\n            min=\"0\"\n            max=\"2\"\n            step=\"0.1\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n          <div style=\"margin-top: 4px; color: #888; font-size: 11px\">\n             0.8 0.2\n          </div>\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">\n            Top P () <span style=\"color: #888; font-size: 11px\">(0-1 1.0)</span>\n          </label>\n          <input\n            v-model.number=\"settings.top_p\"\n            type=\"number\"\n            min=\"0\"\n            max=\"1\"\n            step=\"0.01\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n          <div style=\"margin-top: 4px; color: #888; font-size: 11px\">\n              Temperature  Top P\n          </div>\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">\n            Presence Penalty () <span style=\"color: #888; font-size: 11px\">(-2.0 to 2.0 0)</span>\n          </label>\n          <input\n            v-model.number=\"settings.presence_penalty\"\n            type=\"number\"\n            min=\"-2\"\n            max=\"2\"\n            step=\"0.1\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n          <div style=\"margin-top: 4px; color: #888; font-size: 11px\">\n            \n          </div>\n        </div>\n\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\">\n            Frequency Penalty () <span style=\"color: #888; font-size: 11px\">(-2.0 to 2.0 0)</span>\n          </label>\n          <input\n            v-model.number=\"settings.frequency_penalty\"\n            type=\"number\"\n            min=\"-2\"\n            max=\"2\"\n            step=\"0.1\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n          <div style=\"margin-top: 4px; color: #888; font-size: 11px\">\n            \n          </div>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <div\n      class=\"config-section\"\n      style=\"padding: 20px 25px !important; border-bottom: 1px solid #3a3a3a; margin-bottom: 5px\"\n    >\n      <div\n        style=\"\n          cursor: pointer;\n          user-select: none;\n          display: flex;\n          align-items: center;\n          justify-content: space-between;\n          padding: 20px 28px;\n          background: linear-gradient(\n            135deg,\n            rgba(30, 30, 30, 0.95) 0%,\n            rgba(38, 38, 38, 0.9) 50%,\n            rgba(30, 30, 30, 0.95) 100%\n          );\n          backdrop-filter: blur(12px);\n          border-radius: 14px;\n          mar