    box-shadow:\n                0 2px 8px rgba(74, 158, 255, 0.25),\n                inset 0 1px 0 rgba(255, 255, 255, 0.15);\n              color: white;\n            \"\n            @click=\"handle_generate_table\"\n          >\n            <i v-if=\"!is_generating_table\" class=\"fa-solid fa-robot\"></i>\n            <i v-else class=\"fa-solid fa-spinner fa-spin\"></i>\n            {{ is_generating_table ? 'AI...' : 'AI' }}\n          </button>\n          <button\n            class=\"action-button create-button\"\n            style=\"\n              flex: 1;\n              min-width: 120px;\n              padding: 12px 16px;\n              border: 1px solid #40c057;\n              border-radius: 6px;\n              cursor: pointer;\n              font-weight: 500;\n              font-size: 13px;\n              transition: all 0.2s;\n              display: flex;\n              align-items: center;\n              justify-content: center;\n              gap: 6px;\n              background: linear-gradient(135deg, #51cf66 0%, #40c057 100%);\n              box-shadow:\n                0 2px 8px rgba(81, 207, 102, 0.25),\n                inset 0 1px 0 rgba(255, 255, 255, 0.15);\n              color: white;\n            \"\n            @click=\"handle_create_table\"\n          >\n            <i class=\"fa-solid fa-plus\"></i> \n          </button>\n        </div>\n      </div>\n    </div>\n\n    <!--  -->\n    <div\n      class=\"config-section\"\n      style=\"padding: 20px 25px !important; border-bottom: 1px solid #3a3a3a; margin-bottom: 5px\"\n    >\n      <div\n        style=\"\n          cursor: pointer;\n          user-select: none;\n          display: flex;\n          align-items: center;\n          justify-content: space-between;\n          padding: 20px 28px;\n          background: linear-gradient(\n            135deg,\n            rgba(30, 30, 30, 0.95) 0%,\n            rgba(38, 38, 38, 0.9) 50%,\n            rgba(30, 30, 30, 0.95) 100%\n          );\n          backdrop-filter: blur(12px);\n          border-radius: 14px;\n          margin-bottom: 20px;\n          border: 1px solid rgba(255, 255, 255, 0.06);\n          box-shadow:\n            0 3px 12px rgba(0, 0, 0, 0.3),\n            inset 0 1px 0 rgba(255, 255, 255, 0.04),\n            inset 0 -1px 0 rgba(0, 0, 0, 0.2);\n          position: relative;\n          overflow: hidden;\n          transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);\n        \"\n        @click=\"toggleSection('messageManagement')\"\n        @mouseenter=\"\n          ($event.currentTarget as HTMLElement).style.background =\n            'linear-gradient(135deg, rgba(42, 42, 42, 0.98) 0%, rgba(50, 50, 50, 0.95) 50%, rgba(42, 42, 42, 0.98) 100%)';\n          ($event.currentTarget as HTMLElement).style.transform = 'translateX(4px) scale(1.005)';\n          ($event.currentTarget as HTMLElement).style.borderLeft = '3px solid rgba(255, 193, 7, 0.6)';\n        \"\n        @mouseleave=\"\n          ($event.currentTarget as HTMLElement).style.background =\n            'linear-gradient(135deg, rgba(30, 30, 30, 0.95) 0%, rgba(38, 38, 38, 0.9) 50%, rgba(30, 30, 30, 0.95) 100%)';\n          ($event.currentTarget as HTMLElement).style.transform = 'none';\n          ($event.currentTarget as HTMLElement).style.borderLeft = '1px solid rgba(255, 255, 255, 0.06)';\n        \"\n      >\n        <h3\n          style=\"\n            position: relative;\n            z-index: 1;\n            display: flex;\n            align-items: center;\n            gap: 12px;\n            margin: 0;\n            color: #fff;\n            font-size: 16px;\n            font-weight: 600;\n            letter-spacing: 0.5px;\n            text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);\n          \"\n        >\n          <i class=\"fa-solid fa-layer-group\" style=\"color: #dc3545; font-size: 18px\"></i>\n          \n        </h3>\n        <i\n          :class=\"expandedSections['messageManagement'] ? 'fa-chevron-up' : 'fa-chevron-down'\"\n          class=\"fa-solid\"\n          style=\"color: rgba(255, 255, 255, 0.6); font-size: 14px; transition: transform 0.3s\"\n        ></i>\n      </div>\n\n      <div v-show=\"expandedSections['messageManagement']\">\n        <div class=\"form-group\" style=\"margin-bottom: 18px !important\">\n          <label style=\"display: block; margin-bottom: 6px; color: #ccc; font-size: 13px\"\n            >1-10 5</label\n          >\n          <input\n            v-model=\"hide_range\"\n            type=\"text\"\n            placeholder=\"1-10\"\n            style=\"\n              width: 100%;\n              padding: 10px 12px;\n              background: #2a2a2a;\n              border: 1px solid #3a3a3a;\n              border-radius: 6px;\n              color: #e0e0e0;\n              font-size: 13px;\n              transition: border-color 0.2s;\n            \"\n          />\n        </div>\n        <div class=\"button-group\" style=\"display: flex; gap: 12px; flex-wrap: wrap; margin-top: 5px\">\n          <button\n            class=\"action-button\"\n            style=\"\n              flex: 1;\n              min-width: 120px;\n              padding: 12px 16px;\n              border: 1px solid #ff5252;\n              border-radius: 6px;\n              cursor: pointer;\n              font-weight: 500;\n              font-size: 13px;\n              transition: all 0.2s;\n              display: flex;\n              align-items: center;\n              justify-content: center;\n              gap: 6px;\n              background: #ff6b6b;\n              color: white;\n            \"\n            @click=\"handle_hide_messages\"\n          >\n            <i class=\"fa-solid fa-eye-slash\"></i> \n          </button>\n          <button\n            class=\"action-button\"\n            style=\"\n              flex: 1;\n              min-width: 120px;\n              padding: 12px 16px;\n              border: 1px solid #40c057;\n              border-radius: 6px;\n              cursor: pointer;\n              font-weight: 500;\n              font-size: 13px;\n              transition: all 0.2s;\n              display: flex;\n              align-items: center;\n              justify-content: center;\n              gap: 6px;\n              background: linear-gradient(135deg, #51cf66 0%, #40c057 100%);\n              box-shadow:\n                0 2px 8px rgba(81, 207, 102, 0.25),\n                inset 0 1px 0 rgba(255, 255, 255, 0.15);\n              color: white;\n            \"\n            :disabled=\"!hide_range.trim()\"\n            @click=\"handle_show_messages\"\n          >\n            <i class=\"fa-solid fa-eye\"></i> \n          </button>\n          <button\n            class=\"action-button\"\n            style=\"\n              flex: 1;\n              min-width: 120px;\n              padding: 12px 16px;\n              border: 1px solid #ffcc02;\n              border-radius: 6px;\n              cursor: pointer;\n              font-weight: 500;\n              font-size: 13px;\n              transition: all 0.2s;\n              display: flex;\n              align-items: center;\n              justify-content: center;\n              gap: 6px;\n              background: #ffd43b;\n              color: #333;\n            \"\n            @click=\"() => handle_refresh_hidden(true)\"\n          >\n            <i class=\"fa-solid fa-refresh\"></i> \n          </button>\n        </div>\n\n        <!--  -->\n        <div v-if=\"hidden_messages.length > 0\" class=\"hidden-messages-section\">\n          <div class=\"form-group\">\n            <label class=\"flex-label\">\n              <span> ({{ hidden_messages.length }} )</span>\n              <button class=\"mini-button\" @click=\"hidden_display_expanded = !hidden_display_expanded\">\n                {{ hidden_display_expanded ? '' : '' }}\n              </button>\n            </label>\n          </div>\n          <div v-if=\"hidden_display_expanded\" class=\"hidden-messages-list\">\n            <div v-for=\"msg in hidden_messages\" :key=\"msg.message_id\" class=\"hidden-message-item\">\n              <div class=\"message-info\">\n                <span class=\"message-id\">#{{ msg.message_id }}</span>\n                <span class=\"message-role\" :class=\"'role-' + msg.role\">\n                  {{ msg.role === 'user' ? '' : msg.role === 'assistant' ? '' : '' }}\n                  {{ msg.name }}\n                </span>\n                <span class=\"message-preview\">\n                  {{ msg.message.substring(0, 50) }}{{ msg.message.length > 50 ? '...' : '' }}\n                </span>\n              </div>\n              <button class=\"show-button\" @click=\"() => handle_unhide_single(msg.message_id)\"></button>\n            </div>\n          </div>\n        </div>\n        <div v-else class=\"empty-state\"></div>\n      </div>\n    </div>\n\n    <!--  -->\n    <ProgressDialog ref=\"progressDialogRef\" :show=\"showProgress\" title=\"AI \" />\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { storeToRefs } from 'pinia';\nimport { onMounted, ref } from 'vue';\nimport { useSettingsStore, useSummaryHistoryStore } from '../settings';\nimport { getChatIdSafe, getScriptIdSafe } from '../utils';\nimport ProgressDialog from './ProgressDialog.vue';\n\nconst settingsStore = useSettingsStore();\nconst { settings } = storeToRefs(settingsStore);\nconst summaryHistoryStore = useSummaryHistoryStore();\n\n// \nconst expandedSections = ref<Record<string, boolean>>({\n  api: true,\n  autoSummary: true,\n  manualSummary: true,\n  tableGeneration: true,\n  messageManagement: true,\n});\n\n// /\nconst toggleSection = (section: string) => {\n  expandedSections.value[section] = !expandedSections.value[section];\n};\n\n// API\n// setChatMessages \n\n// \nconst parseMessageRange = (range: string): number[] => {\n  if (!range.trim()) return [];\n\n  const messageIds: number[] = [];\n  const parts = range.split(',');\n\n  for (const part of parts) {\n    const trimmed = part.trim();\n    if (!trimmed) continue;\n\n    if (trimmed.includes('-')) {\n      //  \"1-10\"\n      const [start, end] = trimmed.split('-').map(s => parseInt(s.trim()));\n      if (!isNaN(start) && !isNaN(end) && start >= 0 && end >= 0) {\n        for (let i = start; i <= end; i++) {\n          messageIds.push(i);\n        }\n      }\n    } else {\n      //  \"5\"  \"0\"\n      const id = parseInt(trimmed);\n      if (!isNaN(id) && id >= 0) {\n        messageIds.push(id);\n      }\n    }\n  }\n\n  return messageIds;\n};\n\n// \nconst available_models = ref<string[]>([]);\nconst loading_models = ref(false);\nconst hide_range = ref<string>('');\nconst hidden_messages = ref<Array<{ message_id: number; name: string; role: string; message: string }>>([]);\nconst hidden_display_expanded = ref(false);\n\n// \nconst is_summarizing = ref(false);\nconst is_generating_table = ref(false);\n\n// \nconst showProgress = ref(false);\nconst progressDialogRef = ref<InstanceType<typeof ProgressDialog> | null>(null);\n\n// \nconst headerTemplates = ref<Array<{ name: string; headers: string }>>([]);\nconst selectedTemplate = ref<string>('');\nconst currentTemplate = ref<{ name: string; headers: string }>({ name: '', headers: '' });\n\n// \nconst loadGenerationStatus = () => {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n    const scriptVars = getVariables({ type: 'script', script_id: scriptId });\n    is_summarizing.value = scriptVars.is_summarizing || false;\n    is_generating_table.value = scriptVars.is_generating_table || false;\n    console.log(':', { summarizing: is_summarizing.value, generating_table: is_generating_table.value });\n  } catch (error) {\n    console.error(':', error);\n    is_summarizing.value = false;\n    is_generating_table.value = false;\n  }\n};\n\nconst saveGenerationStatus = () => {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n    insertOrAssignVariables(\n      {\n        is_summarizing: is_summarizing.value,\n        is_generating_table: is_generating_table.value,\n      },\n      { type: 'script', script_id: scriptId },\n    );\n    console.log('');\n  } catch (error) {\n    console.error(':', error);\n  }\n};\n\nconst stopGeneration = () => {\n  if (is_summarizing.value || is_generating_table.value) {\n    is_summarizing.value = false;\n    is_generating_table.value = false;\n    saveGenerationStatus();\n    window.toastr.info('');\n  }\n};\n\n// \nconst loadHeaderTemplates = () => {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n    const scriptVars = getVariables({ type: 'script', script_id: scriptId });\n    //  header_templates  nullundefined\n    let templates = scriptVars.header_templates;\n    //  nullundefined  []\n    if (!Array.isArray(templates)) {\n      templates = [];\n    }\n    headerTemplates.value = templates;\n    console.log(':', headerTemplates.value.length, '');\n  } catch (error) {\n    console.error(':', error);\n    headerTemplates.value = [];\n  }\n};\n\nconst saveHeaderTemplates = () => {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n    insertOrAssignVariables(klona({ header_templates: headerTemplates.value }), {\n      type: 'script',\n      script_id: scriptId,\n    });\n    console.log('');\n  } catch (error) {\n    console.error(':', error);\n  }\n};\n\nconst loadTemplate = () => {\n  if (selectedTemplate.value !== '') {\n    const index = parseInt(selectedTemplate.value);\n    if (index >= 0 && index < headerTemplates.value.length) {\n      currentTemplate.value = { ...headerTemplates.value[index] };\n    }\n  }\n};\n\nconst showAddTemplateDialog = () => {\n  const name = window.prompt('');\n  if (name && name.trim()) {\n    const headers = window.prompt('');\n    if (headers && headers.trim()) {\n      headerTemplates.value.push({ name: name.trim(), headers: headers.trim() });\n      saveHeaderTemplates();\n      selectedTemplate.value = (headerTemplates.value.length - 1).toString();\n      loadTemplate();\n      window.toastr.success('');\n    }\n  }\n};\n\nconst saveTemplate = () => {\n  if (selectedTemplate.value !== '' && currentTemplate.value.name.trim() && currentTemplate.value.headers.trim()) {\n    const index = parseInt(selectedTemplate.value);\n    if (index >= 0 && index < headerTemplates.value.length) {\n      headerTemplates.value[index] = { ...currentTemplate.value };\n      saveHeaderTemplates();\n      window.toastr.success('');\n    }\n  } else {\n    window.toastr.warning('');\n  }\n};\n\nconst deleteTemplate = () => {\n  if (selectedTemplate.value !== '') {\n    if (confirm('')) {\n      const index = parseInt(selectedTemplate.value);\n      if (index >= 0 && index < headerTemplates.value.length) {\n        headerTemplates.value.splice(index, 1);\n        saveHeaderTemplates();\n        selectedTemplate.value = '';\n        currentTemplate.value = { name: '', headers: '' };\n        window.toastr.success('');\n      }\n    }\n  }\n};\n\n// \nconst loadHiddenMessages = () => {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n    console.log('ID:', scriptId);\n\n    const saved = getVariables({ type: 'script', script_id: scriptId });\n    console.log(':', saved);\n    console.log(' hidden_messages :', saved?.hidden_messages);\n\n    if (saved && saved.hidden_messages) {\n      hidden_messages.value = saved.hidden_messages;\n      console.log(':', hidden_messages.value.length, '');\n    } else {\n      console.log('...');\n      // \n      const possibleFields = ['hiddenMessages', 'hidden_floors', 'hiddenFloors', 'floor_data'];\n      for (const field of possibleFields) {\n        if (saved && saved[field]) {\n          console.log(` ${field}:`, saved[field]);\n          hidden_messages.value = saved[field];\n          break;\n        }\n      }\n    }\n  } catch (error) {\n    console.error(':', error);\n  }\n};\n\n// \nconst saveHiddenMessages = () => {\n  try {\n    const scriptId = getScriptIdSafe();\n    if (!scriptId) {\n      console.warn('script_id ');\n      return;\n    }\n    console.log('ID:', scriptId);\n    console.log(':', klona(hidden_messages.value));\n\n    const dataToSave = {\n      hidden_messages: hidden_messages.value,\n    };\n    console.log(':', klona(dataToSave));\n\n    insertOrAssignVariables(klona(dataToSave), { type: 'script', script_id: scriptId });\n    console.log(':', hidden_messages.value.length, '');\n\n    // \n    setTimeout(() => {\n      const saved = getVariables({ type: 'script', script_id: scriptId });\n      console.log(':', saved);\n      console.log(' hidden_messages :', saved?.hidden_messages);\n    }, 100);\n  } catch (error) {\n    console.error(':', error);\n  }\n};\n\n//  API \nconst initApiProvider = () => {\n  if (!settings.value.api_provider || settings.value.api_provider === '') {\n    const endpoint = settings.value.api_endpoint || '';\n    if (endpoint.includes('generativelanguage.googleapis.com')) {\n      settings.value.api_provider = 'gemini';\n    } else if (endpoint.includes('open.bigmodel.cn')) {\n      settings.value.api_provider = 'zhipu';\n    } else {\n      settings.value.api_provider = 'openai';\n    }\n  }\n};\n\n// \nonMounted(() => {\n  console.log('SettingsTab ');\n  initApiProvider(); //  API \n  loadHiddenMessages();\n  loadHeaderTemplates();\n  loadGenerationStatus();\n});\n\n// \nconst handleTestButton = () => {\n  console.log(' - Vue');\n  window.toastr.success('Vue');\n};\n\n// \nconst handleSaveSettings = () => {\n  console.log(' ...');\n  const success = settingsStore.saveSettings();\n  if (success) {\n    console.log(' ');\n  } else {\n    console.error(' ');\n  }\n};\n\nconst handleReloadSettings = () => {\n  console.log(' ...');\n  const success = settingsStore.reloadSettings();\n  if (success) {\n    console.log(' ');\n  } else {\n    console.error(' ');\n  }\n};\n\nconst handleResetAutoSummaryStart = () => {\n  console.log(' ...');\n  try {\n    // \n    if (typeof (window as any).resetAutoSummaryStart === 'function') {\n      (window as any).resetAutoSummaryStart();\n    } else {\n      window.toastr.error('');\n    }\n  } catch (error) {\n    console.error(' :', error);\n    window.toastr.error(': ' + (error as Error).message);\n  }\n};\n\n// API \nconst handleProviderChange = () => {\n  const provider = settings.value.api_provider;\n  const providerEndpoints: Record<string, string> = {\n    openai: 'https://api.openai.com/v1',\n    gemini: 'https://generativelanguage.googleapis.com/v1beta/openai',\n  };\n\n  if (providerEndpoints[provider]) {\n    settings.value.api_endpoint = providerEndpoints[provider];\n    window.toastr.success(` ${provider === 'openai' ? 'OpenAI' : 'Gemini AI Studio'}`);\n  }\n};\n\n// \n// API\nconst handleSaveApiConfig = () => {\n  try {\n    // \n    if (!settings.value.api_endpoint || !settings.value.api_key) {\n      window.toastr.warning(' API  API Key');\n      return;\n    }\n\n    // \n    const success = settingsStore.saveSettings();\n    if (success) {\n      console.log('API :', {\n        endpoint: settings.value.api_endpoint,\n        model: settings.value.model,\n        provider: settings.value.api_provider,\n        //  API Key\n        api_key: settings.value.api_key ? '***' + settings.value.api_key.slice(-4) : '',\n      });\n    }\n  } catch (error) {\n    console.error(' API :', error);\n    window.toastr.error('' + (error as Error).message);\n  }\n};\n\n// \nconst handleImportFromTavern = async () => {\n  try {\n    console.log(' ...');\n\n    // \n    if (typeof triggerSlash === 'undefined') {\n      window.toastr.error('');\n      return;\n    }\n\n    console.log(' ...');\n\n    //  1: \n    console.log('1  /profile ...');\n    const currentProfileResult = await triggerSlash('/profile', '');\n    console.log(':', currentProfileResult);\n\n    // \n    if (\n      !currentProfileResult ||\n      currentProfileResult === 'No connection profile selected.' ||\n      currentProfileResult.includes('')\n    ) {\n      console.log(' ...');\n\n      // \n      const parentWin = window.parent || window.top || window;\n      const powerUser = (parentWin as any).power_user;\n      const secretState = (parentWin as any).secret_state;\n\n      let endpoint = '';\n      let apiKey = '';\n      let model = '';\n\n      if (powerUser) {\n        endpoint = powerUser.custom_chat_url || powerUser.reverse_proxy || '';\n        model = powerUser.custom_model || powerUser.openai_model || '';\n      }\n\n      if (secretState) {\n        apiKey = secretState.api_key_custom || secretState.custom || secretState.openai || '';\n      }\n\n      if (!endpoint && !apiKey && !model) {\n        window.toastr.warning(\n          ' API \\n\\n' +\n            ' \\n' +\n            'API     ',\n        );\n        return;\n      }\n\n      // \n      if (endpoint) settings.value.api_endpoint = endpoint;\n      if (apiKey) settings.value.api_key = apiKey;\n      if (model) settings.value.model = model;\n\n      await settingsStore.saveSettings();\n\n      window.toastr.success(\n        ` \\n\\n` +\n          (endpoint ? ` : ${endpoint}\\n` : '') +\n          (apiKey ? ` Key: ***${apiKey.slice(-4)}\\n` : '') +\n          (model ? ` : ${model}` : ''),\n      );\n\n      return;\n    }\n\n    //  2: \n    const profileName = currentProfileResult.trim();\n    console.log(`2 : ${profileName}`);\n\n    const profileDetailsResult = await triggerSlash('/profile-get', profileName);\n    console.log(':', profileDetailsResult);\n\n    //  JSON\n    let profileData: any;\n    try {\n      profileData = JSON.parse(profileDetailsResult);\n      console.log(' :', profileData);\n    } catch (parseError) {\n      console.error('  JSON :', parseError);\n      window.toastr.error('');\n      return;\n    }\n\n    // \n    let endpoint = '';\n    let apiKey = '';\n    let model = '';\n\n    // API  api-url  server_url\n    if (profileData['api-url']) {\n      endpoint = profileData['api-url'];\n      console.log('  API  (api-url):', endpoint);\n    } else if (profileData.server_url) {\n      endpoint = profileData.server_url;\n      console.log('  API  (server_url):', endpoint);\n    }\n\n    // API Key - \n    if (profileData['secret-id']) {\n      const secretId = profileData['secret-id'];\n      console.log('  secret-id:', secretId);\n      console.log(' API Key ');\n    } else if (profileData.key) {\n      apiKey = profileData.key;\n      console.log('  API Key (key)');\n    }\n\n    // model\n    if (profileData.model) {\n      model = profileData.model;\n      console.log(' :', model);\n    }\n\n    // \n    if (!endpoint && !model) {\n      window.toastr.warning(\n        ' API \\n\\n' +\n          '\\n' +\n          ' \\n' +\n          ' \\n\\n' +\n          ' ',\n      );\n      console.warn(' :', profileData);\n      return;\n    }\n\n    //  API Key\n    if (!apiKey) {\n      console.log(' API Key ');\n    }\n\n    // \n    let importedCount = 0;\n    const importDetails: string[] = [];\n\n    if (endpoint) {\n      settings.value.api_endpoint = endpoint;\n      importedCount++;\n      importDetails.push(` API : ${endpoint}`);\n    }\n\n    if (apiKey) {\n      settings.value.api_key = apiKey;\n      importedCount++;\n      importDetails.push(` API Key: ***${apiKey.slice(-4)}`);\n    }\n\n    if (model) {\n      settings.value.model = model;\n      importedCount++;\n      importDetails.push(` : ${model}`);\n    }\n\n    // \n    await settingsStore.saveSettings();\n\n    // \n    let successMessage =\n      `  ${importedCount} \\n\\n` +\n      ` : ${profileName}\\n\\n` +\n      `${importDetails.join('\\n')}`;\n\n    //  API Key\n    if (!apiKey) {\n      successMessage += `\\n\\n API Key \\n API Key`;\n    }\n\n    window.toastr.success(successMessage);\n\n    console.log(' :', {\n      profileName,\n      endpoint,\n      apiKey: apiKey ? '***' + apiKey.slice(-4) : '',\n      model,\n    });\n  } catch (error) {\n    console.error(' :', error);\n    window.toastr.error('' + (error as Error).message);\n  }\n};\n\nconst handle_fetch_models = async () => {\n  if (loading_models.value) return;\n\n  try {\n    loading_models.value = true;\n    console.log(' ...');\n    console.log(' API :', settings.value.api_endpoint);\n    console.log(' API Key:', settings.value.api_key ? '***' + settings.value.api_key.slice(-4) : '');\n\n    //  API \n    if (!settings.value.api_endpoint || !settings.value.api_key) {\n      window.toastr.warning(' API  API Key');\n      return;\n    }\n\n    window.toastr.info('...');\n\n    // \n    const { fetchAvailableModels } = await import('../');\n    const models = await fetchAvailableModels();\n\n    console.log(' :', models);\n\n    if (models.length > 0) {\n      // \n      available_models.value = models;\n      window.toastr.success(\n        `  ${models.length} \\n` +\n          `: ${models.slice(0, 3).join(', ')}${models.length > 3 ? '...' : ''}`,\n      );\n\n      // \n      if (!settings.value.model && models[0]) {\n        settings.value.model = models[0];\n        console.log(' :', models[0]);\n      }\n    } else {\n      window.toastr.warning('');\n    }\n  } catch (error) {\n    console.error(' :', error);\n\n    // \n    const errorMessage = (error as Error).message;\n\n    // \n    if (errorMessage.length > 200) {\n      window.toastr.error(\n        ' \\n\\n' +\n          'F12\\n\\n' +\n          '\\n' +\n          ' API  /v1/models \\n' +\n          ' API Key \\n' +\n          '  CORS ',\n      );\n    } else {\n      window.toastr.error(` \\n\\n${errorMessage}`);\n    }\n\n    console.error(' :', errorMessage);\n  } finally {\n    loading_models.value = false;\n  }\n};\n\nconst handle_test_connection = async () => {\n  try {\n    console.log('...');\n\n    //  API \n    if (!settings.value.api_endpoint || !settings.value.api_key) {\n      window.toastr.warning(' API  API Key');\n      return;\n    }\n\n    window.toastr.info('...');\n\n    // \n    const { normalizeApiEndpoint } = await import('../settings');\n    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n    console.log(' :', apiUrl);\n\n    //  OpenAI \n    const response = await fetch(apiUrl, {\n      method: 'POST',\n      headers: {\n        'Content-Type': 'application/json',\n        Authorization: `Bearer ${settings.value.api_key}`,\n      },\n      body: JSON.stringify({\n        model: settings.value.model || 'gpt-3.5-turbo',\n        messages: [{ role: 'user', content: 'Hello' }],\n        max_tokens: 5,\n        temperature: settings.value.temperature,\n        top_p: settings.value.top_p,\n        presence_penalty: settings.value.presence_penalty,\n        frequency_penalty: settings.value.frequency_penalty,\n      }),\n    });\n\n    if (response.ok) {\n      const data = await response.json();\n      console.log('API :', data);\n\n      // \n      const reply = data.choices?.[0]?.message?.content || '()';\n      const modelUsed = data.model || settings.value.model;\n\n      window.toastr.success(` \\n` + ` : ${modelUsed}\\n` + ` : ${reply.substring(0, 50)}...`);\n    } else {\n      const errorText = await response.text();\n      console.error('API :', errorText);\n      window.toastr.error(`  (${response.status})\\n` + `: ${errorText.substring(0, 100)}`);\n    }\n  } catch (error) {\n    console.error(':', error);\n    window.toastr.error(': ' + (error as Error).message);\n  }\n};\n\nconst handle_summarize = async () => {\n  try {\n    if (is_summarizing.value) return;\n\n    console.log('...');\n    is_summarizing.value = true;\n    saveGenerationStatus();\n\n    //  API \n    if (!settings.value.api_endpoint || !settings.value.api_key) {\n      window.toastr.warning(' API  API Key');\n      return;\n    }\n\n    // \n    if (settings.value.start_message_id === undefined || settings.value.end_message_id === undefined) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    if (settings.value.start_message_id < 0 || settings.value.end_message_id < settings.value.start_message_id) {\n      window.toastr.warning('');\n      return;\n    }\n\n    console.log(`: ${settings.value.start_message_id} - ${settings.value.end_message_id}`);\n\n    // \n    showProgress.value = true;\n    progressDialogRef.value?.setProgress(10);\n    progressDialogRef.value?.setMessage('...');\n    progressDialogRef.value?.addDetail(\n      `: ${settings.value.start_message_id} - ${settings.value.end_message_id}`,\n    );\n\n    progressDialogRef.value?.setProgress(30);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(' 10-30 ');\n\n    // \n    const { summarizeMessages } = await import('../');\n    const summary = await summarizeMessages(settings.value.start_message_id, settings.value.end_message_id);\n\n    progressDialogRef.value?.setProgress(90);\n    progressDialogRef.value?.setMessage('...');\n\n    console.log(':', summary);\n\n    // \n    const scriptId = getScriptIdSafe();\n    if (scriptId) {\n      insertOrAssignVariables(klona({ last_summary: summary }), { type: 'script', script_id: scriptId });\n    }\n\n    // \n    summaryHistoryStore.addSummary(\n      settings.value.start_message_id ?? 0,\n      settings.value.end_message_id ?? settings.value.start_message_id ?? 0,\n      summary,\n    );\n\n    // \n    window.dispatchEvent(new CustomEvent('summary-history-updated'));\n\n    progressDialogRef.value?.setProgress(100);\n    progressDialogRef.value?.setMessage(' ');\n    progressDialogRef.value?.addDetail('');\n\n    setTimeout(() => {\n      showProgress.value = false;\n      window.toastr.success('');\n    }, 800);\n  } catch (error) {\n    console.error(':', error);\n    showProgress.value = false;\n    window.toastr.error(': ' + (error as Error).message);\n  } finally {\n    is_summarizing.value = false;\n    saveGenerationStatus();\n  }\n};\n\nconst handle_generate_table = async () => {\n  try {\n    if (is_generating_table.value) return;\n\n    console.log('...');\n    is_generating_table.value = true;\n    saveGenerationStatus();\n\n    //  API \n    if (!settings.value.api_endpoint || !settings.value.api_key) {\n      window.toastr.warning(' API  API Key');\n      return;\n    }\n\n    // \n    showProgress.value = true;\n    progressDialogRef.value?.setProgress(5);\n    progressDialogRef.value?.setMessage('...');\n\n    // \n    if (!settings.value.table_start_message_id || !settings.value.table_end_message_id) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    let headersInput: string | null = null;\n\n    if (currentTemplate.value.headers.trim()) {\n      // \n      const useTemplate = confirm(\n        `\"${currentTemplate.value.name}\"\\n${currentTemplate.value.headers}`,\n      );\n      if (useTemplate) {\n        headersInput = currentTemplate.value.headers;\n      }\n    }\n\n    if (!headersInput) {\n      // \n      headersInput = window.prompt(',,,');\n    }\n\n    if (!headersInput || !headersInput.trim()) {\n      window.toastr.warning('');\n      return;\n    }\n\n    const headers: string[] = headersInput\n      .split(',')\n      .map((h: string) => h.trim())\n      .filter((h: string) => h);\n\n    if (headers.length === 0) {\n      window.toastr.warning('');\n      return;\n    }\n\n    progressDialogRef.value?.setProgress(15);\n    progressDialogRef.value?.setMessage('...');\n    progressDialogRef.value?.addDetail(\n      `: ${settings.value.table_start_message_id} - ${settings.value.table_end_message_id}`,\n    );\n\n    // \n    let chatMessages;\n    try {\n      const messageRange = `${settings.value.table_start_message_id}-${settings.value.table_end_message_id}`;\n      console.log(':', messageRange);\n      chatMessages = getChatMessages(messageRange, { hide_state: 'all' });\n      console.log(':', chatMessages.length);\n\n      progressDialogRef.value?.addDetail(` ${chatMessages.length} `);\n    } catch (error) {\n      console.error(':', error);\n      showProgress.value = false;\n      window.toastr.error(': ' + (error as Error).message);\n      return;\n    }\n\n    if (!chatMessages || chatMessages.length === 0) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // AI\n    const messagesText = chatMessages\n      .map(msg => {\n        const role = msg.role === 'user' ? '' : 'AI';\n        return `${role}: ${msg.message}`;\n      })\n      .join('\\n\\n');\n\n    const aiPrompt = `${headers.join(', ')}\n\n\n${messagesText}\n\n****\n1. \n2. ${headers.join(', ')}\n3. \n4. \"\"\"-\"\n5. JSONmarkdown\n6. dataheaders\n\n**JSON**\n{\n  \"data\": [\n    [\"1\", \"2\", \"3\"],\n    [\"4\", \"5\", \"6\"]\n  ]\n}\n\ndata${headers.length}${headers.join(', ')}`;\n\n    console.log('AI...');\n\n    progressDialogRef.value?.setProgress(30);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(`: ${headers.join(', ')}`);\n\n    // \n    const { normalizeApiEndpoint } = await import('../settings');\n    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);\n\n    progressDialogRef.value?.setProgress(40);\n    progressDialogRef.value?.setMessage(' AI ...');\n    progressDialogRef.value?.addDetail(' 10-30 ');\n\n    // AI\n    const response = await fetch(apiUrl, {\n      method: 'POST',\n      headers: {\n        'Content-Type': 'application/json',\n        Authorization: `Bearer ${settings.value.api_key}`,\n      },\n      body: JSON.stringify({\n        model: settings.value.model,\n        messages: [\n          {\n            role: 'user',\n            content: aiPrompt,\n          },\n        ],\n        max_tokens: settings.value.max_tokens,\n        temperature: settings.value.temperature,\n        top_p: settings.value.top_p,\n        presence_penalty: settings.value.presence_penalty,\n        frequency_penalty: settings.value.frequency_penalty,\n      }),\n    });\n\n    if (!response.ok) {\n      throw new Error(`API: ${response.status} ${response.statusText}`);\n    }\n\n    progressDialogRef.value?.setProgress(70);\n    progressDialogRef.value?.setMessage(' AI ...');\n\n    const result = await response.json();\n    console.log('AI:', result);\n\n    if (!result.choices || !result.choices[0] || !result.choices[0].message) {\n      throw new Error('AI');\n    }\n\n    const aiResponse = result.choices[0].message.content;\n    console.log('AI:', aiResponse);\n\n    progressDialogRef.value?.setProgress(85);\n    progressDialogRef.value?.setMessage('...');\n\n    // AIJSON\n    let aiTableData;\n    try {\n      // JSONmarkdown\n      let jsonText = aiResponse.trim();\n\n      // markdown\n      jsonText = jsonText\n        .replace(/^```json\\s*/i, '')\n        .replace(/^```\\s*/, '')\n        .replace(/```\\s*$/, '');\n\n      // JSON\n      const jsonMatch = jsonText.match(/\\{[\\s\\S]*\\}/);\n      if (jsonMatch) {\n        aiTableData = JSON.parse(jsonMatch[0]);\n      } else {\n        throw new Error('JSON');\n      }\n    } catch (parseError) {\n      console.error('AI:', parseError);\n      console.log('AI:', aiResponse);\n      window.toastr.error('AI');\n      return;\n    }\n\n    // \n    if (!aiTableData.data || !Array.isArray(aiTableData.data)) {\n      window.toastr.error('AIdata');\n      return;\n    }\n\n    // \n    for (let i = 0; i < aiTableData.data.length; i++) {\n      if (!Array.isArray(aiTableData.data[i])) {\n        window.toastr.error(`${i + 1}`);\n        return;\n      }\n      if (aiTableData.data[i].length !== headers.length) {\n        window.toastr.error(`${i + 1}${headers.length}${aiTableData.data[i].length}`);\n        console.log(`:`, headers);\n        console.log(`:`, aiTableData.data[i]);\n        return;\n      }\n    }\n\n    // headers\n    const tableData = {\n      headers: headers,\n      data: aiTableData.data,\n    };\n\n    // \n    const chat_id = getChatIdSafe();\n    if (chat_id) {\n      const chat_vars = getVariables({ type: 'chat' });\n      const table_history = chat_vars.table_history || [];\n\n      table_history.push({\n        start_id: settings.value.table_start_message_id,\n        end_id: settings.value.table_end_message_id,\n        headers: tableData.headers,\n        data: tableData.data,\n      });\n\n      insertOrAssignVariables(klona({ table_history }), { type: 'chat' });\n\n      progressDialogRef.value?.setProgress(100);\n      progressDialogRef.value?.setMessage(' ');\n      progressDialogRef.value?.addDetail(` ${tableData.data.length} `);\n\n      setTimeout(() => {\n        showProgress.value = false;\n        window.toastr.success(`${tableData.data.length}`);\n      }, 800);\n\n      console.log(':', table_history);\n    } else {\n      showProgress.value = false;\n      window.toastr.warning('ID');\n    }\n  } catch (error) {\n    console.error(':', error);\n    showProgress.value = false;\n    window.toastr.error(': ' + (error as Error).message);\n  } finally {\n    is_generating_table.value = false;\n    saveGenerationStatus();\n  }\n};\n\nconst handle_create_table = () => {\n  try {\n    // \n    if (!settings.value.table_start_message_id || !settings.value.table_end_message_id) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    const headersInput: string | null = window.prompt(',,,');\n    if (!headersInput || !headersInput.trim()) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    const headers: string[] = headersInput\n      .split(',')\n      .map((h: string) => h.trim())\n      .filter((h: string) => h);\n\n    if (headers.length === 0) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    const emptyTableData = {\n      start_id: settings.value.table_start_message_id,\n      end_id: settings.value.table_end_message_id,\n      headers: headers,\n      data: [], // \n    };\n\n    // \n    const chat_id = getChatIdSafe();\n    if (chat_id) {\n      const chat_vars = getVariables({ type: 'chat' });\n      const table_history = chat_vars.table_history || [];\n\n      table_history.push(emptyTableData);\n      insertOrAssignVariables(klona({ table_history }), { type: 'chat' });\n\n      window.toastr.success(`${headers.join(', ')}`);\n      console.log(':', emptyTableData);\n    } else {\n      window.toastr.warning('ID');\n    }\n  } catch (error) {\n    console.error(':', error);\n    window.toastr.error(': ' + (error as Error).message);\n  }\n};\n\nconst handle_hide_messages = async () => {\n  try {\n    console.log('...');\n\n    if (!hide_range.value.trim()) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    const ranges = hide_range.value.split(',').map(range => range.trim());\n    const messageIds: number[] = [];\n\n    for (const range of ranges) {\n      if (range.includes('-')) {\n        // 1-10\n        const [start, end] = range.split('-').map(Number);\n        if (start && end && start <= end) {\n          for (let i = start; i <= end; i++) {\n            messageIds.push(i);\n          }\n        }\n      } else {\n        // 5\n        const id = Number(range);\n        if (id) {\n          messageIds.push(id);\n        }\n      }\n    }\n\n    if (messageIds.length === 0) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    let chatMessages;\n    try {\n      const lastMessageId = getLastMessageId();\n      console.log('ID:', lastMessageId);\n      chatMessages = getChatMessages(`0-${lastMessageId}`, { hide_state: 'all' });\n      console.log(':', chatMessages.length);\n    } catch (error) {\n      console.error(':', error);\n      window.toastr.error(': ' + (error as Error).message);\n      return;\n    }\n\n    if (!chatMessages || chatMessages.length === 0) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    let hiddenCount = 0;\n    const messagesToHide = [];\n\n    for (const messageId of messageIds) {\n      // \n      const alreadyHidden = hidden_messages.value.some(msg => msg.message_id === messageId);\n      if (alreadyHidden) {\n        console.log(` ${messageId} `);\n        continue;\n      }\n\n      const message = chatMessages.find(msg => msg.message_id === messageId);\n      if (message) {\n        // \n        messagesToHide.push({\n          message_id: messageId,\n          is_hidden: true,\n        });\n\n        // \n        hidden_messages.value.push({\n          message_id: messageId,\n          name: message.name || 'Unknown',\n          role: message.role || 'user',\n          message: message.message || '',\n        });\n        hiddenCount++;\n      }\n    }\n\n    // API\n    if (messagesToHide.length > 0) {\n      try {\n        console.log(' slash :', messageIds);\n\n        //  slash \n        for (const messageId of messageIds) {\n          try {\n            await triggerSlash(`/hide ${messageId}`);\n            console.log(` ${messageId}`);\n          } catch (error) {\n            console.error(` ${messageId} :`, error);\n          }\n        }\n\n        window.toastr.success('');\n      } catch (error) {\n        console.error('API:', error);\n        window.toastr.error('API: ' + (error as Error).message);\n      }\n    }\n\n    if (hiddenCount > 0) {\n      // \n      saveHiddenMessages();\n      window.toastr.success(` ${hiddenCount} `);\n      hide_range.value = ''; // \n    } else {\n      window.toastr.warning('');\n    }\n  } catch (error) {\n    console.error(':', error);\n    window.toastr.error(': ' + (error as Error).message);\n  }\n};\n\nconst handle_show_messages = async () => {\n  try {\n    console.log('...');\n\n    if (!hide_range.value.trim()) {\n      window.toastr.warning('');\n      return;\n    }\n\n    // \n    const message